<template>
  <div class="flex-width-1 flex">
    <div class="flex-width-1 flex" v-show="error || !access">
      <PageStatus
        @gofirstpage="goToFirstPage"
        @buymef="BuyMef"
        :error="error"
        :access="access"
      />
    </div>
    <div v-show="!error && access" class="full-wh">
      <div class="page">
        <div
          class="before-flt"
          v-if="filterActive"
          @click="tab.fltToBookIds = []"
        >
                 -  
        </div>
        <!-- <iframe src="file:///d://sample.html"></iframe> -->
        <Loader v-show="!ready" class="loader" />
        <div class="page_container" v-if="access">
          <canvas></canvas>

          <!-- width="1330" height="1883"-->
          <div id="markers_container">
            <div style="position:relative" v-if="ready">
              <template v-for="(section, index) in sections" v-key="index">
                <div
                  v-for="rect in section.PageRects"
                  :key="rect.id"
                  :secShasId-data="rect.shasSecId"
                  :style="getPosition(rect, section)"
                  class="shasSecMark"
                  @mouseenter="markRect(section)"
                  @click="setSelectedShasSec(sections[index])"
                  @mouseleave="markRectLeave(section)"
                >
                  <div
                    v-if="!clickDirty"
                    v-tooltip="'  '"
                    style="width:100%;height:100%"
                  ></div>
                </div>
              </template>
            </div>
          </div>
        </div>
        <div v-else>
          <span v-if="config.offline">
               /.
            <h3>
              <p style="color:brown" v-if="isDemo">
                 !      .   
                     
                <br />   
                <a class="lnk-first-page" @click="BuyMef"> </a>
                <br />    
                <a class="lnk-first-page" @click="goToFirstPage"> </a>
              </p>
            </h3>
          </span>
          <span v-else>      </span>
        </div>
        <!--<img  src='http://212.199.162.79/ot/sc/sshas/imgfromopk.php?book=173936&page=8' />-->
      </div>
    </div>
  </div>
</template>

<script>
import Pica from "pica";
import getcode from "@/utils/enc";
import SectionService from "@/services/sectionService.js";
import { mapState, mapActions, mapGetters } from "vuex";
import TabState from "@/mixing/importTabState";
import PageStatus from "./PageStatus";
import MainPageAddr from "@/mixing/mainPageAddr";
import getCanvasFromPpm from "@/utils/ppmcanvas";
import axios from "@/services/_axiosInstance";
import pictureService from "@/services/pictureService";
import config from "@/config/config";
import pako from "pako";
import { ShasBooks } from "@/services/shasService";
// import readFile from "@/utils/readfile";
// let tiff = require("tiff.js");
import djvu from "@/utils/djvu";
let mainCanvas = null;
function getPicture() {
  let picture;
  return picture;
}
function cloneCanvas(oldCanvas) {
  //create a new canvas
  var newCanvas = document.createElement("canvas");
  var context = newCanvas.getContext("2d");

  //set dimensions
  newCanvas.width = oldCanvas.width;
  newCanvas.height = oldCanvas.height;

  //apply the old canvas to the new one
  context.drawImage(oldCanvas, 0, 0);

  //return the new canvas
  return newCanvas;
}
export default {
  mixins: [TabState, MainPageAddr],
  components: { PageStatus },
  data() {
    return {
      clickDirty: false,
      // picture: null, in created - we dont need it reactive
      canvasHeight: 0,
      canvasWidth: 0,
      selected_rects: [],
      drawRect: [],
      sections: [],
      ready: false,
      first: true,
      access: true,
      error: false,
      pica: null,
      config,
    };
  },

  computed: {
    ...mapState("tabs", [
      "refreshMarkers",
      "currentTabId",
      "pageHeight",
      "pageWidth",
      "refreshPage",
      "reloadAll",
    ]),
    isDemo() {
      // console.log(window._demo);
      return window._demo;
    },
    filterActive() {
      return this.tab.fltToBookIds.length > 0;
    },
    currentSelectedRect() {
      let section = this.sections.filter(
        ({ id }) => id == this.tab.selectedSection
      );
      // console.log(section);
      if (section.length > 0) {
        return section[0].PageRects;
      } else return null;
    },
    page() {
      return this.tab.page;
    },
    zoom() {
      return this.tab.zoom;
    },
    bookid() {
      return this.tab.book;
    },
    currentPageChanged() {
      // this.$el.querySelector("canvas");
      // mainCanvas = null;
      if (this.$parent.$el != undefined) {
        this.$parent.$el.scrollTop = 0;
      }

      return this.tab.book + this.tab.page;
    },
  },

  created() {
    mainCanvas = null;
    this.picture = null;
    this.pica = Pica({ features: ["js", "wasm", "ww", "cib"] });
    this.loadPicture()
      .then(() => {
        this.loadSection();
      })
      .catch(() => {});
  },

  mounted() {
    this.$nextTick(() => {
      this.resetCanvas();
      mainCanvas = null;
      this.setDims({
        width: this.$el.offsetWidth - 10,
        height: this.$el.offsetHeight - 10,
      });
    });
  },

  watch: {
    zoom: function() {
      this.drawImage();
    },
    refreshMarkers: function() {
      this.resetCanvas();
      mainCanvas = null;
      if (this.$el.offsetHeight != 0) {
        //reset the canvas

        this.setDims({
          width: this.$el.offsetWidth - 10,
          height: this.$el.offsetHeight - 10,
        });
        //mainCanvas = null;
      }
    },
    reloadAll: function() {
      mainCanvas = null;
      this.ready = false;
      this.selected_rects = [];
      this.loadPicture()
        .then(() => this.loadSection())
        .catch(() => {});
    },
    refreshPage: function() {
      mainCanvas = null;

      this.drawImage();
      if (this.first) return;
      if (this.ready) this.ready = false;
    },

    currentPageChanged: function() {
      mainCanvas = null;
      this.ready = false;
      this.selected_rects = [];
      this.loadPicture()
        .then(() => this.loadSection())
        .catch(() => {});
    },
  },

  methods: {
    ...mapActions("tabs", ["setDims"]),
    // addMoreShasSec(section) {
    //   this.selected_rects.push(section.pageRects);
    // },
    goToFirstPage() {
      this.$router.push({
        name: "n-main-page",
        params: {
          bookid: this.$route.params.bookid.toString(),
          page: ShasBooks[this.$route.params.bookid].firstPage.toString(),
          tab: this.currentTabId.toString(),
        },
      });
    },
    BuyMef() {
      this.$router.push({
        name: "buymef",
      });
      // window.location.href =
      // "http://tablet.otzar.org/ot/mefstore/storedev.php?sn=111&curr=2&landing=http://127.0.0.1:3010/api/general/success_payment";
    },
    resetCanvas() {
      let canvas = this.$el.querySelector("canvas");

      canvas.className = "page-canvas-reset";
      canvas.width = 0;
      canvas.height = 0;
      canvas.className = "page-canvas-full";
    },
    resetMainCanvas() {
      mainCanvas = null;
      // console.log(mainCanvas);
    },
    setSelectedShasSec(section) {
      // console.log(section.type);

      this.clickDirty = true;
      this.selected_rects = section.pageRects;
      if (section.type == "mh" || section.type == "em") {
        // console.log(section);
        let Data = section;
        let _id = Math.floor(Math.random() * 1000000 + 1);
        let popup = {
          ...Data,
          ready: false,
          _id,
        };

        this.$store.state.tabs.popups.push(popup);
        return;
      }
      this.tab.selectedSection = section.id;

      this.$router.push(this.getTheMainPageAddr);
    },
    checkRectIsInTheMiddle(rect, lst) {
      let notTop = lst.some((e) => e.y < rect.y);
      let notBottom = lst.some((e) => e.y > rect.y);
      return notTop && notBottom;
    },
    getPosition(rect, section) {
      let canvas = this.$el.querySelector("canvas");
      let container = this.$el.querySelector("#markers_container");
      if (
        (section.type == "mh" || section.type == "em") &&
        section.MareMakom == null
      )
        return "";
      let pageWidth = canvas.width;
      let pageHeight = canvas.height;
      container.setAttribute(
        "style",
        `height:${pageHeight}px;width:${pageWidth}px;left:${canvas.offsetLeft}px;top:${canvas.offsetTop}px;position: absolute;`
      );

      let left = pageWidth * rect.x;
      let top = pageHeight * rect.y;
      let height = pageHeight * rect.h;
      //if (this.checkRectIsInTheMiddle(rect, section.PageRects)) height += 12; //add some space between
      let width = pageWidth * rect.w;
      let ret =
        "left:" +
        left +
        "px;top:" +
        top +
        "px;width:" +
        width +
        "px;height:" +
        height +
        "px;position:absolute;background-coloir:Red";
      return ret;
    },
    markRect(section) {
      this.drawRect = this.sections.filter(
        (e) => e.id === section.id
      )[0].PageRects;
      // console.log(this.drawRect);

      this.drawImage();
    },
    markRectLeave(rect) {
      this.drawRect = [];

      this.drawImage();
    },
    async loadSection() {
      let vm = this;
      mainCanvas = null;
      try {
        this.drawRect = [];
        let dataRequest = await SectionService.getAllSectionOnThePage(
          this.bookid,
          this.page
        );
        let data = dataRequest.data;
        // console.log(data);
        data = pako.inflate(data, { to: "string" });
        data = JSON.parse(data);

        data = data.slice(0);
        window.sc = data;
        //remove white space
        // data.forEach(sec => {
        //   let pr = sec.PageRects;
        //   for (let i = 1; i < pr.length; i++) {
        //     if (pr[i - 1].page !== pr[i].page) continue;
        //     var prevLineBottom = pr[i - 1].y + pr[i - 1].h;
        //     var thisLineBottom = pr[i].y + pr[i].h;
        //     pr[i].y = prevLineBottom;
        //     pr[i].h = thisLineBottom - prevLineBottom;
        //   }
        // });

        // console.log(data.filter(i => i.id == 57415));
        this.$store
          .dispatch("tab_" + this.currentTabId + "/setSections", data)
          .then(() => {
            this.sections = data; //data.data.content for mongodb
            this.drawImage();
          }); //data.data.content for mongodb
      } catch (e) {
        // console.log(e);
        // console.dir(e.response.status);
        vm.ready = true;
        //check if its limit access error
        try {
          if (e.response.status == 403 || e.response.status == 423)
            vm.access = false;
        } catch {}
        vm.error = true;
        vm.first = false;
      }
    },
    loadPicture() {
      this.error = false;
      if (this.picture != null) this.picture.done = false;
      return new Promise(async (resolve, reject) => {
        this.picture = document.createElement("img");
        let vm = this;
        this.picture.crossorigin = "anonymous";
        this.picture.onload = function() {
          vm.access = true;
          vm.picture.done = true;
          resolve();
        };
        this.picture.onerror = function(err) {
          vm.ready = true;
          // vm.access = false;
          vm.error = true;
          vm.first = false;

          reject();
        };
        if (config.offline) {
          vm.access = true;

          let pic = await pictureService.getImageData(
            this.tab.book,
            this.tab.page
          );

          let imagedata = null;
          // console.log(pic);
          if (pic.headers["content-type"] == "image/djvu") {
            let djvuPage = new djvu.Document(pic.data);
            imagedata = await djvuPage.pages[0].createPngObjectUrl();
          } else {
            imagedata = pic.data;
          }

          let _canvas = new OffscreenCanvas(imagedata.width, imagedata.height);
          let ctx = _canvas.getContext("2d");
          ctx.putImageData(imagedata, 0, 0);
          // var image = new tiff({ buffer: pic.data });
          // let canvas = image.toCanvas(); //getCanvasFromPpm(pic.data);
          this.picture = _canvas;
          this.picture.done = true;
          resolve();
          // window.registerProtocolHandler(
          //   "web+burger",
          //   "https://burgers.example.com/?burger=%s",
          //   "Burger handler"
          // );
          // let x = readFile("file:///d://lorem.txt");
          // let bin = await axios.get(`file:///${pic.data}`, {
          //   responseType: "arraybuffer"
          // });
          // console.log(window.filedata);
          // console.log(pic.data);
          // let ret = this.$root.server.emit("getbookpage", {
          //   book: this.tab.book,
          //   page: this.tab.page
          // });
          // ret.on("takethebook", data => {
          //   let image = new tiff({ buffer: data });

          //   let canvas = image.toCanvas(); //getCanvasFromPpm(pic.data);

          //   this.picture = canvas;
          //   // console.dir(canvas);
          //   resolve();
          // });
        } else
          this.picture.src = pictureService.getImageUrl(
            this.tab.book,
            this.tab.page
          );
      });
    },

    async drawImage() {
      if (this.picture.done != true) return;
      let rects = this.drawRect;
      //debugger;
      let canvas = this.$el.querySelector("canvas");

      let ctx = canvas.getContext("2d");

      //reset canvas
      this.resetCanvas();
      // debugger;
      let img = this.picture;

      let pageHeight = this.pageHeight;
      pageHeight += (this.tab.zoom * pageHeight) / 100;

      let pageWidth = this.pageWidth;
      pageWidth += (this.tab.zoom * pageWidth) / 100;

      let aspect_height =
        (pageWidth * this.picture.height) / this.picture.width;

      let aspect_width =
        (pageHeight * this.picture.width) / this.picture.height;
      //console.log(this.tab.page);
      if (this.tab.fitToWidth) {
        canvas.width = pageWidth - 50;
        canvas.height = aspect_height;

        if (mainCanvas == null) {
          let pic = await this.pica.resize(img, canvas);
          mainCanvas = cloneCanvas(pic);
        } else {
          ctx.drawImage(mainCanvas, 0, 0);
        }
      } else {
        this.canvasWidth = aspect_width;

        canvas.width = aspect_width;
        canvas.height = this.pageHeight;

        if (mainCanvas == null) {
          let pic = await this.pica.resize(img, canvas, {
            unsharpAmount: 100,
            unsharpRadius: 0.1,
            unsharpThreshold: 1,
          });
          mainCanvas = cloneCanvas(pic);
        } else {
          ctx.drawImage(mainCanvas, 0, 0);
        }
      }
      canvas.className = "";
      // canvas.style.width =
      //   (canvas.width / window.devicePixelRatio).toString() + "px";
      // canvas.style.height =
      //   (canvas.height / window.devicePixelRatio).toString() + "px";
      // let selected_rects = this.selected_rects;

      if (this.currentSelectedRect) {
        this.currentSelectedRect.forEach((element) => {
          ctx.save();
          ctx.fillStyle = "#98774F";
          ctx.globalCompositeOperation = "color";

          //ctx.globalCompositeOperation = "lighter";
          if (this.tab.fitToWidth) {
            ctx.fillRect(
              element.x * canvas.width,
              element.y * aspect_height,
              element.w * canvas.width,
              element.h * aspect_height
            );
          } else {
            ctx.fillRect(
              element.x * aspect_width,
              element.y * this.pageHeight,
              element.w * aspect_width,
              element.h * this.pageHeight
            );
          }
          ctx.fillStyle = "rgba(195, 153, 79, 0.3)";
          ctx.globalCompositeOperation = "multiply";
          if (this.tab.fitToWidth) {
            ctx.fillRect(
              element.x * canvas.width,
              element.y * aspect_height,
              element.w * canvas.width,
              element.h * aspect_height
            );
          } else {
            ctx.fillRect(
              element.x * aspect_width,
              element.y * this.pageHeight,
              element.w * aspect_width,
              element.h * this.pageHeight
            );
          }
        });
      }

      rects.forEach((element) => {
        // if (element.id == this.currentSelectedRect[0].id) return;

        ctx.fillStyle = "#dedede";

        if (element.type == "mh" || element.type == "em") {
          // if (element.MareMakom == null) return;
          ctx.fillStyle = "yellow";
        }
        ctx.save();
        ctx.globalCompositeOperation = "multiply";
        //ctx.globalCompositeOperation = "lighter";
        if (this.tab.fitToWidth) {
          ctx.fillRect(
            element.x * canvas.width,
            element.y * aspect_height,
            element.w * canvas.width,
            element.h * aspect_height
          );
        } else {
          ctx.fillRect(
            element.x * aspect_width,
            element.y * this.pageHeight,
            element.w * aspect_width,
            element.h * this.pageHeight
          );
        }
      });

      this.ready = true;
      this.first = false;
      // debugger;
    },
  },
};
</script>

<style scoped>
.page-canvas-reset {
  width: 1px;
  height: 1px;
}
.page-canvas-full {
  width: 100%;
  height: 100%;
}
/* .page_container canvas{
    box-sizing: border-box;
    width:100%;
    position: relative;
} */
.page_container {
  height: 100%;

  text-align: center;
}
.page {
  margin: 25px;

  position: relative;
  flex-grow: 1;
}

.shasSecMark {
  cursor: pointer;
}
#markers_container {
  position: relative;
}
.loader {
  position: absolute;
  left: calc(50% - 100px);
  background: transparent;
}
.before-flt {
  background: red;
  color: white;
  position: fixed;
  cursor: pointer;
  z-index: 1;
}
.lnk-first-page:hover {
  text-decoration: underline;
  cursor: pointer;
}
.lnk-first-page {
  color: blue;
}
</style>
