
import { ShasBooks,pagesDic } from "@/services/shasService";
export const namespaced = true;

export const state = {
    ShasBooks
};


export const actions = {
    setShasBook({ rootGetters }, data) {   
        rootGetters["tabs/getCurrentTab"].book=data 
    },
    setShasBookPage({rootGetters }, data) {   
        rootGetters["tabs/getCurrentTab"].page=data
    }
    
};

export const getters = {
    getShasBooks(state) {
        return state.ShasBooks
    },
    getBookPages(state,getters,rootState,rootGetters) {
       
        if(typeof(rootGetters["tabs/getCurrentTab"])==='undefined') return null;
        let pages=[];
        let book=state.ShasBooks[rootGetters["tabs/getCurrentTab"].book];
        for(let i=book.firstPage;i<=book.count + book.firstPage;i+=2){
            pages.push({
                title:pagesDic(book.firstPage,i),
                number:i
            })
        }
        return pages;
    },
    getSelectedBookNumber(state,getters,rootState,rootGetters){
        if(typeof(rootGetters["tabs/getCurrentTab"])==='undefined') return null;
        return rootGetters["tabs/getCurrentTab"].book;
    },
    getSelectedBookName(state,getters,rootState,rootGetters){
        if(typeof(rootGetters["tabs/getCurrentTab"])==='undefined') return null;
        return state.ShasBooks[rootGetters["tabs/getCurrentTab"].book].bookName;
    },
    getSelectedPageNumber(state,getters,rootState,rootGetters){
        if(typeof(rootGetters["tabs/getCurrentTab"])==='undefined') return null;
        return rootGetters["tabs/getCurrentTab"].page;
    },
    getSelectedPage(state,getters,rootState,rootGetters){
        if(typeof(rootGetters["tabs/getCurrentTab"])==='undefined') return null;
        let book=state.ShasBooks[rootGetters["tabs/getCurrentTab"].book];
        let page=rootGetters["tabs/getCurrentTab"].page;
        if (page % 2 != 0) page-=1
        return pagesDic(book.firstPage,page);
    },
    getSelectedSubPage(state,getters,rootState,rootGetters){
        if(typeof(rootGetters["tabs/getCurrentTab"])==='undefined') return null;
        let page=rootGetters["tabs/getCurrentTab"].page;
        if (page % 2 == 0) return '"'
        return '"';
    }
};

