<template>
  <div class="switcher" @click="toggleViewMode">
    <span v-if="!viewBookMode"> </span>
    <span v-else> </span>
  </div>
</template>

<script>
import { mapState } from "vuex";
import store from "@/store/store";
export default {
  props: ["id"],
  data() {
    return {};
  },
  computed: {
    //...mapState(`section-popup-` + this.id,["viewBookMode"]),
    viewBookMode() {
      return store.state[`section-popup-` + this.id].viewBookMode;
    }
  },
  methods: {
    toggleViewMode() {
      store.state[`section-popup-` + this.id].viewBookMode = !store.state[
        `section-popup-` + this.id
      ].viewBookMode;
    }
  }
};
</script>

<style>
.switcher {
  -webkit-box-flex: 0;
  -ms-flex-positive: 0;
  flex-grow: 0;
  -ms-flex-preferred-size: 120px;
  /* flex-basis: 120px; */
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  cursor: pointer;
  border-color: #98774f;
  background-color: white;
  color: #98774f;
  border-radius: 50px;
  padding: 0px 12px;
  height: 32px;
  border-width: 2px;
  border-style: solid;
  font-size: 14px;
  line-height: 32px;
}
.switcher:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
</style>
