<template>
  <a :href="link" target="_blank" style="text-align: left;width:fit-content"> </a>
</template>

<script>
export default {
  props: ["bookl", "bookr", "bookname", "pagel", "pager"],
  computed: {
    link() {
      return `http://ozrmef.otzar.org/smef/intfc.PHP?=${this.bookname}&bookl=${this.bookl}&bookr=${this.bookr}&bLpage=${this.pagel}&= &bRpage=${this.pager}`;
    },
  },
};
</script>

