function checkNullMarks(rect) {
  return rect.MefoToMarks.some(e => {
    return e.Marks == null || e.Marks == undefined;
  });
}
/**
 * check Null MefoToMark In None Shiur
 * @param {Object} rect
 * @returns {Boolean}
 */
function checkNullMefoToMark(rect) {
  if (
    rect.MefoToMarks == undefined ||
    rect.MefoToMarks.length <= 0 //&& type != "s"
  ) {
    return true;
  }
  return false;
}

function checkShufflingMarks(rect) {
  let item = rect.MefoToMarks[0].Marks.book;
  return rect.MefoToMarks.some(e => {
    return e.Marks.book != item;
  });
}

function checkShiurNotExist(rect) {
  if (rect.type == "s" && rect.SectionToShiur == null) return true;
  return false;
}

export default {
  getError(rect) {
    let ret = checkNullMefoToMark(rect);
    if (ret) return "     ";
    ret = checkNullMarks(rect);
    if (ret) return "   ";
    ret = checkShufflingMarks(rect);
    if (ret) {
      if (rect.book != rect.MefoToMarks[0].Marks.book)
        return "    ";
      else return "    ";
    }
    ret = checkShiurNotExist(rect);
    if (ret) return "    ";

    return false;
  }
};
