import Vue from "vue";
import Router from "vue-router";
import mainPageRoutes from "./mainPage/mainPageRoutes";
import selectPageRoutes from "./selectPage/selectPageRoutes";
import MefUserRoutes from "./meforeshRepo/meforshim-user-libs";

// import Loading from "@/views/LoadProgram.vue";
import mobileRouters from "./mobileRouters/mobileRouter";
Vue.use(Router);
// console.log(mobileRouters);
const router = new Router({
  // scrollBehavior(to, from, savedPosition) {
  //   console.log(savedPosition);
  //   return { x: 0, y: 0 };
  // },
  routes: [
    ...mobileRouters,
    ...mainPageRoutes,
    ...selectPageRoutes,
    ...MefUserRoutes,
    ,
    {
      path: "/about",
      name: "about",

      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: "About" */ "@/views/About.vue"),
    },
    {
      path: "/lic-creator/23324423/w2/lc",
      name: "lic457873452342",

      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(
          /* webpackChunkName: "Login" */ "@/components/Lic/LicCreator.vue"
        ),
    },
    {
      path: "/login",
      name: "login",

      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: "Login" */ "@/views/Login.vue"),
    },
    {
      path: "/load",
      name: "load",
      component: () =>
        import(/* webpackChunkName: "LoadProgram" */ "@/views/LoadProgram.vue"),
    },
    {
      path: "/error/:errorNum",
      name: "error",
      component: () => import("@/views/ErrorPage.vue"),
      props: (route) => ({
        errorNum: parseInt(route.params.errorNum),
        debugData: route.params.debugData || null,
      }),
      beforeEnter: (to, from, next) => {
        if (from.name == null) {
          next("/");
        } else next();
      },
    },
    {
      path: "/buy-mef",
      name: "buymef",
      component: () =>
        import(/* webpackChunkName: "LoadProgram" */ "@/views/BuyMef.vue"),
    },
    {
      path: "/success-payment",
      name: "success-payment",
      component: () =>
        import(
          /* webpackChunkName: "LoadProgram" */ "@/views/SuccessPayment.vue"
        ),
    },
  ],
});

// Creates a `nextMiddleware()` function which not only
// runs the default `next()` callback but also triggers
// the subsequent Middleware function.
function nextFactory(context, middleware, index) {
  const subsequentMiddleware = middleware[index];
  // If no subsequent Middleware exists,
  // the default `next()` callback is returned.
  if (!subsequentMiddleware) return context.next;

  return (...parameters) => {
    // Run the default Vue Router `next()` callback first.
    context.next(...parameters);
    // Then run the subsequent Middleware with a new
    // `nextMiddleware()` callback.
    const nextMiddleware = nextFactory(context, middleware, index + 1);
    subsequentMiddleware({ ...context, next: nextMiddleware });
  };
}

router.beforeEach((to, from, next) => {
  if (to.meta.middleware) {
    const middleware = Array.isArray(to.meta.middleware)
      ? to.meta.middleware
      : [to.meta.middleware];

    const context = {
      from,
      next,
      router,
      to,
    };
    const nextMiddleware = nextFactory(context, middleware, 1);

    return middleware[0]({ ...context, next: nextMiddleware });
  }

  return next();
});

export default router;
