      </span>
    </div>
    <div v-tooltip="'   '" style="height:31px" v-if="$store.state.workEdition">
      <span
        class="mef-font clickable"
        style="font-size:60px;line-height:31px;height:31px;width:24px;display: inline-block;cursor:pointer"
        @click="tab.showerror = !tab.showerror"
        :class="{ disable: tab.showerror }"
      >
        <HoverGray h="25" w="25" t="3" l="-1">R</HoverGray>
      </span>

      <!-- <component
        :is="showOnlyError"
        v-if="$store.state.workEdition"
        @click="tab.showerror = !tab.showerror"
        class="icon"
        :class="{ disable: !tab.showerror }"
      ></component>-->
    </div>
    <div v-tooltip="' '" style="height:31px">
      <span
        class="mef-font clickable"
        style="font-size:60px;line-height:31px;height:31px;width:24px;display: inline-block;"
        @click="toggleFilters()"
      >
        <HoverGray h="25" w="25" t="3" l="-1">k</HoverGray>
      </span>

      <!-- <component
        :is="FilterIcon"
        @click="toggleFilters()"
        @mouseenter="filterHoverEvent()"
        @mouseleave="filterHoverEvent(false)"
        class="icon"
      ></component>-->
    </div>
    <div v-tooltip="'    '" style="height:31px">
      <span
