        @click="toggleViewMode()"
      >
        <HoverGray h="25" w="25" t="3" l="-2">
          <template v-if="tab.viewFullMode">m</template>
          <template v-else>n</template>
        </HoverGray>
      </span>
      <!--
         @mouseenter="filterHoverEvent()"
        @mouseleave="filterHoverEvent(false)"
      <component :is="ExpandSectionsView" @click="toggleViewMode()" class="icon"></component>-->
