      <span>B</span>
    </div>-->
  </div>
</template>

<script>
import store from "@/store/store";
import TabState from "@/mixing/importTabState";

export default {
  name: "TitleOptions",
  mixins: [TabState],
  computed: {
    toggleMode() {
      return this.tab.viewFullMode;
    }
  },

  data() {
    return {
      sortBy: " "
    };
  },
  computed: {
    // SectionViewMode() {
    //   if (this.tab.bookViewerMode == 1) return BookOpenPageVariant;
    //   else return Checkbook;
    // },
    // ExpandSectionsView() {
    //   if (this.tab.viewFullMode) return Minus;
    //   else return Plus;
    // },
    // scrollViewMode() {
    //   return ScriptTextOutline;
    // },
    // showOnlyError() {
    //   return BeakerRemoveOutline;
    // },
    // bookList() {
    //   return FormatListBulletedSquare;
    // }
    lblSortBy() {
      if (this.tab.alphaSort) return " -";
      return "  ";
    }
  },

  methods: {
    setSortBy(e) {
      this.tab.alphaSort = e.val;
      //console.log(this.tab);
    },
    // filterHoverEvent(modeIn = true) {
    //   if (modeIn) this.FilterIcon = FilterIcon;
    //   else this.FilterIcon = FilterOutline;
    // },
    // SectionViewHoverEvent(modeIn = true) {
    //   if (modeIn) this.SectionViewMode = BookOpenPageVariant;
    //   else this.SectionViewMode = Checkbook;
    // },
    toggleFilters() {
      this.tab.showFilters = !this.tab.showFilters;
    },
    toggleViewMode() {
      let v = true;
      if (this.tab.viewFullMode) v = false;
      for (let i = 0; i < this.tab.cards.length; i++) {
        this.tab.cards[i] = v;
      }

      this.tab.viewFullMode = !this.tab.viewFullMode;
    }
  }
};
</script>

<style>
.clickable {
  cursor: pointer;
}
.filter-show-btn-container {
  width: 25px;
  height: 25px;
  cursor: pointer;
  vertical-align: -webkit-baseline-middle;
  display: inline-block;
  fill: white;
  stroke: #000;
}
.filter-show-btn-container:hover {
  fill: black;
  stroke: #000;
}
.filter-on-btn {
  vertical-align: -webkit-baseline-middle;
  color: red;
  height: 15px;
}
.filter-minimize-btn-container {
  width: 20px;
  cursor: pointer;
  display: inline-block;
  margin-right: 5px;
  stroke: gray;
}
.filter-minimize-btn-container:hover {
  stroke: black;
}
.FilterTitleOption {
  display: flex;
  color: #626262;
}

.material-design-icon.icon {
  width: 20px;
  height: 20px;
  cursor: pointer;
  margin-left: 3px;
}

.material-design-icon.icon > .material-design-icon__svg {
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.disable {
  color: red;
}
.toggle-table {
  font-size: 30px;
  line-height: 31px;
  height: 31px;
  /* width: 24px; */
  display: inline-block;
  cursor: pointer;
  height: 31px;
}
</style>
