<template>
  <form @submit.prevent="handleSubmitClick" class="create-lib">
    <p class="text-create">  </p>
    <div>
      <input
        dir="rtl"
        type="text"
        v-model="label"
        class="input-box"
        placeholder="   "
      />
      <input class="submit-btn" type="submit" value="" />
    </div>

    <div class="exit-to-list" @click.stop="goToLibslist()">
      <img src="@/./assets/icons/exit-x-icon.png" class="exit-icon" alt />
      <router-view></router-view>
    </div>
  </form>
</template>

<script>
import { mapMutations, mapState, mapActions } from "vuex";

export default {
  data() {
    return {
      label: ""
    };
  },
  methods: {
    handleSubmitClick() {
      this.createLib();
      this.goToLibslist();
    },
    goToLibslist() {
      this.$router.push({ name: "mef-user-libs" });
    },
    createLib() {
      this.$store.dispatch("meforshimUserLibs/createLib", this.label);
    }
  },
  computed: {
    ...mapState("meforshimUserLibs", ["meforshUserList"])
  }
};
</script>

<style scoped>
.exit-to-list {
  position: fixed;
  left: 36.7%;
  top: 21px;
}
.submit-btn {
  text-shadow: none;
  text-decoration: none;
  outline: none;
  width: 76px;
  height: 24px;
  font-size: 14px;
  border: 1px solid #c6c6c6;
  background-color: white;
  border-radius: 14px;

  position: fixed;
  left: 37.2%;
  top: 60px;
}
.input-box {
  position: fixed;
  left: 43%;
  top: 60px;

  width: 248px;
  height: 24.5px;
  font-size: 12px;
  outline: none;
  text-align: right;

  margin: 0px;
  padding: 0px;
  border-style: solid #f5f5f5;
  border-width: 0px 0px 1px 0px;
}
.text-create {
  font-size: 14px;
  text-align: center;
  font-weight: bold;
  color: #dedede;
}
.create-lib {
