<template>
  <div class="pages-container" tabindex="2" v-focus v-show="ready" :style="{ height: height }">
    <Page v-for="tabid in tabs" :key="tabid" :tabid="tabid" v-show="tabid == currentTabId"></Page>
    <!-- <PageTools /> -->
  </div>
</template>

<script>
import RouterManager from "@/mixing/routerMixing";
import { mapState } from "vuex";
import store from "@/store/store";
import Page from "./Page";

import PageTools from "./PageTools/PageToolsContainer";

const getOffsetTop = element => {
  let offsetTop = 0;
  while (element) {
    offsetTop += element.offsetTop;
    element = element.offsetParent;
  }
  return offsetTop;
};

export default {
  data() {
    return {
      ready: false,
      height: "700px"
    };
  },

  components: {
    Page,
    PageTools
  },
  mounted() {
    this.ready = true;
    let tmp = getOffsetTop(this.$el);
    //this.height =
    //   globalThis.outerHeight - (this.$el.parentElement.offsetTop + 36 + 100);
    //calc(100vh - " + tmp + "px)
    this.height = "calc(100vh - 72px)";
    // console.log(this.height);
  },

  computed: {
    ...mapState("tabs", ["tabs", "currentTabId"]),
    keyPress() {
      return this.$store.state.keyPress;
    }
  },
  watch: {
    keyPress: function(val) {
      if (val == "MAIN-LEFT") {
        this.$el.focus();
      }
    }
  },
  beforeRouteUpdate(to, from, next) {
    Page.methods.resetMainCanvas();
    next();
  },
  mixins: [RouterManager]
};
</script>

<style scoped>
.pages-container {
  display: flex;
  height: 100%;
  overflow-y: scroll;
}
</style>
