<!-- this component hold all the SectionContainers components
 each SectionContainer Per Tab-->
<template>
  <div>
    <div class="sections">
      <SectionsContaier
        v-for="tab in tabs"
        :tabid="tab"
        :key="tab"
        v-show="tab == currentTabId"
      />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import store from "@/store/store";
import SectionsContaier from "@/components/MainPage/sections/SectionsContainer.vue";
export default {
  name: "SectionsSideBarContainer",
  components: {
    SectionsContaier
  },
  computed: {
    ...mapState("tabs", ["tabs", "currentTabId"])
  },
  beforeRouteEnter(to, from, next) {
    //store.dispatch('tabs/increment')
    // called before the route that renders this component is confirmed.
    // does NOT have access to `this` component instance,
    // because it has not been created yet when this guard is called!

    next(vm => {
      store.dispatch(
        "tab_" + vm.currentTabId + "/setSelectedSection",
        to.params.sec
      );
      if (to.params.chapter != undefined) {
        store.dispatch(
          "tab_" + vm.currentTabId + "/setSelectedChapter",
          to.params.chapter
        );
      } else {
        store.dispatch("tab_" + vm.currentTabId + "/setSelectedChapter", null);
      }
    });
  },
  beforeRouteUpdate(to, from, next) {
    store.dispatch(
      "tab_" + this.currentTabId + "/setSelectedSection",
      to.params.sec
    );
    if (to.params.chapter != undefined) {
      store.dispatch(
        "tab_" + this.currentTabId + "/setSelectedChapter",
        to.params.chapter
      );
    } else {
      store.dispatch("tab_" + this.currentTabId + "/setSelectedChapter", null);
    }
    next();
  }
};
</script>

