<!-- this component views the sections by 3 options. goes in ShowUserLib component -->
<template>
  <div>
    <!-- the block view options -->
    <div class="list-option-block">
      <p class="text-options">: </p>
      <ul class="options-sections-list-icons">
        <img
          src="@/./assets/icons/list-lines.png"
          alt
          class="select-btn1"
          @click="changeMode(1)"
          v-show="viewMode!=1"
        />
        <img
          src="@/./assets/icons/list-lines-checked.png"
          alt
          class="select-btn1"
          v-show="viewMode===1"
        />
        <img
          src="@/./assets/icons/list-small-box.png"
          alt
          class="select-btn2"
          @click="changeMode(2)"
          v-show="viewMode!=2"
        />
        <img
          src="@/./assets/icons/list-small-box-checked.png"
          alt
          class="select-btn2"
          v-show="viewMode===2"
        />
        <img
          src="@/./assets/icons/list-larg-box.png"
          alt
          class="select-btn3"
          @click="changeMode(3)"
          v-show="viewMode!=3"
        />
        <img
          src="@/./assets/icons/list-large-box-checked.png"
          alt
          class="select-btn3"
          v-show="viewMode===3"
        />
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import store from "@/store/store";

export default {
  computed: {
    ...mapState("sectionsFolder", ["viewMode"])
  },
  methods: {
    changeMode(state) {
      store.state.sectionsFolder.viewMode = state;
    }
  }
};
</script>

<style  scoped>
.list-option-block {
  width: 324px;
  height: 49px;
  position: absolute;
  left: 100px;
  top: 50px;
}
.text-options {
  color: #b7b7b7;
  font-size: 12px;
  position: relative;
  left: 243px;
  top: 0px;
}
.options-sections-list-icons {
  list-style-type: none;
  height: 28px;
  width: 124px;
  overflow: hidden;
  position: relative;
  left: 200px;
  bottom: 13px;
  direction: rtl;
}

.select-btn1 {
  width: 28px;
  height: 28px;
  position: relative;
  left: 40px;
}
.select-btn2 {
  width: 28px;
  height: 28px;
  position: relative;
  left: 20px;
}
.select-btn3 {
  width: 28px;
  height: 28px;
  position: relative;
  left: 0px;
}
</style>