<!-- this component will pin to top lib or un pin by user that inside of ContextMenuLib component-->
<template>
  <div>
    <!-- shows just if pinTotop is false and will change to true if user click on it -->
    <div
      v-on="$listeners"
      class="nopin"
      @click.stop="updatePinToTop()"
      v-show="lib.pinToTop===false"
      @change="updatePinToTop()"
    >pin to top</div>
    <!-- shows just if pinTotop is true and will change to false if user click on it -->
    <div
      v-on="$listeners"
      @click.stop="updatePinToTop()"
      v-show="lib.pinToTop===true"
      @change="updatePinToTop()"
    >unpin from top</div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from "vuex";
import store from "@/store/store";
export default {
  computed: {
    ...mapState("sectionsfolderuser", ["meforshUserList"])
  },
  props: {
    //getting the lib object from ContextMenuLib
    lib: Object,
    display: Boolean
  },
  methods: {
    //updates pin when user click either
    updatePinToTop() {
      // let folderId = this.lib.folderId;
      // let pinToTop = this.lib.pinToTop;
      let payload = {
        folderId: this.lib.folderId,
        pinToTop: this.lib.pinToTop
        /* more parameters */
      };
      this.$store.dispatch("meforshimUserLibs/pinToTopLib", payload);
    }
  }
};
</script>

