<template>
  <div class="tab-item" :class="{active: isTabActive,tab_item_ltr: $parent.is_ltr}">
    <div class="tab-content" @click.prevent="setInternalActive()">
      <slot></slot>
    </div>
    <svg xmlns="http://www.w3.org/2000/svg" class="tab-svg" viewBox="0 0 114.72 37.7">
      <g id="Layer_2" data-name="Layer 2">
        <g id="Layer_1-2" data-name="Layer 1">
          <path
            id="Path_22"
            data-name="Path 22"
            d="M.46,36.5,12.14,9.05C13.8,3.5,16.39.5,20.17.5H94.54c3.79,0,6.38,3,8,8.55L114.26,36.5"
            style="stroke-linejoin:bevel"
          />
        </g>
      </g>
    </svg>
  </div>
</template>

<script >
import { mapActions } from "vuex";
export default {
  name: "TabItem",
  props: {
    active: {
      default: false
    },
    tab: {
      required: true
    }
  },
  computed: {
    isTabActive() {
      return this.active == 1;
    }
  },
  methods: {
    ...mapActions("mainTabs", ["setActive"]),
    setInternalActive() {
      this.setActive(this.tab);
    }
  }
};
</script>

<style scoped>
.tab-item:hover {
  /* text-shadow: 4px 4px 9px rgba(0, 0, 0, 0.6); */
}
.tab-item {
  cursor: pointer;
  width: 114px;

  color: white;

  display: inline-block;
  position: relative;
  text-align: center;
  height: 100%;
  line-height: 36px;
  margin-left: -15px;
  overflow: hidden;
  z-index: 0;
}
.tab-item_ltr {
  margin-right: -35px;
}
.tab-item:last-child {
  margin-left: 0px;
  margin-right: 0px;
}
.tab-item-ltr:first-child {
  margin-left: 0px;
  margin-right: 0px;
}
.tab-content {
  z-index: 1;
  margin: 0 auto;
  position: relative;
  vertical-align: middle;
  width: 80%;
  height: 100%;
  line-height: 36px;
}
.tab-svg {
  display: inline-block;
  position: absolute;
  width: 100%;
  left: 0;
  top: 0px;
}
.active {
  fill: #f5f5f5;
  stroke: #f5f5f5;
  z-index: 2;
  color: black;
}
@media (max-width: 48em) {
  .tab-item {
    display: none;
  }
}
</style>