import Vue from "vue";
import Vuex from "vuex";
import * as tabs from "@/store/modules/tabs.js";
import * as page from "@/store/modules/page.js";
import * as Shas from "@/store/modules/page-picker.js";
import Prints from "@/store/modules/prints.js";
// import * as mainTabs from "@/store/modules/page-picker.js";
import * as mainTabs from "@/store/modules/mainTabs.js";
import * as SelectPage from "@/store/modules/select-page.js";
import { saveStatePlugin } from "./plugins/saveState";
import * as sectionsFolder from "@/store/modules/sectionsFolder.js";
import * as meforshimUserLibs from "@/store/modules/meforshimUserLibs.js";
import * as books from "@/store/modules/books.js";
import * as user from "@/store/modules/user.js";
import * as minibar from "@/store/modules/minibar.js";
import CardModule from "@/store/modules/section-card.js";

Vue.use(Vuex);

let pinnedTabs = require("./plugins/loadSavedTabs");

let store = new Vuex.Store({
  modules: {
    tabs,
    minibar,
    Prints,
    books,
    page,
    Shas,
    sectionsFolder,
    meforshimUserLibs,
    SelectPage,
    CardModule,
    mainTabs,
    user,
  },
  plugins: [saveStatePlugin],
  state: {
    workEdition: false,
    mainResizeOccured: 0,
    welcomeScreen: true,
    showBook: null,
    simpleMode: false,
    keyPress: "NONE",
  },
  mutations: {
    SET_WORK_EDITION(state, active = true) {
      state.workEdition = active;
    },
  },
  actions: {
    setWorkEditon({ commit }, active = true) {
      commit("SET_WORK_EDITION", active);
    },
  },
  getters: {
    isItWorkEdition(state) {
      return state.workEdition;
    },
    getCurrentTab(state, getters) {
      return getters["tabs/getCurrentTab"];
    },
  },
});
pinnedTabs = pinnedTabs.default;
// console.log(pinnedTabs);

pinnedTabs.forEach((item) => {
  let name = item.state().tabId;
  store.registerModule("tab_" + name, item);
  store.dispatch("tabs/addTabId", name);
});

// console.log(store);
export default store;
