let token = localStorage.getItem("token");
import io from "socket.io-client";

let server = null;
let time_check = null;
let my_token = null;
if (token) {
  let axios = require("../services/_axiosInstance").default;
  require("../services/_axiosInstance").setJwt(token);
  axios
    .get("/api/user/verify")
    .then((err, data) => {
      loginCheck(token);
    })
    .catch((e) => {
      localStorage.removeItem("token");

      // localStorage.removeItem("name");
      require("../services/_axiosInstance").clearJwt();
    });
  //axios.setJwt(token);
} else {
  //localStorage.removeItem("name");
}

export function loginCheck(token) {
  server = io.connect(`${MAIN_SERVER}:${MAIN_PORT}/`, { secure: true });
  server.on("connect", (data) => {
    //first let get token
    server.emit("get_token", token);

    server.on("set_token", (data) => {
      console.log("token is " + data);
      my_token = data;
    });
    server.on("login_status", (data) => {
      if (data == "bad") {
        localStorage.removeItem("token");
        require("../services/_axiosInstance").clearJwt();
        clearInterval(time_check);
        time_check = null;
        alert("       .   ");
        location.reload();
      }
    });
    if (time_check) clearInterval(time_check);
    time_check = null;
    //set token checker

    time_check = setInterval(() => {
      server.emit("check_login", { id: my_token, user: token });
    }, 3010);
  });
}
