<template>
  <div class="flex flex-full flex-center flex-dir-col">
    <div v-if="justError" class="flex flex-full flex-center flex-dir-col">
      <p class="title"> </p>
      <p class="title content-title">     </p>
    </div>

    <div
      v-if="offline && !justError"
      class="flex flex-full flex-center flex-dir-col"
    >
      <p class="title title-main">
         !      .
      </p>
      <p class="title content-title">
                
      </p>
      <p class="title content">
               
      </p>
      <div class="flex">
        <div style="margin-left:10px">
          <BaseButton @click="gofirstpage">  </BaseButton>
        </div>
        <div>
          <BaseButton @click="$emit('buymef')">  </BaseButton>
        </div>
      </div>
    </div>
    <div v-else-if="!justError" class="flex flex-full flex-center flex-dir-col">
      <p class="title title-main">
        <span>       </span>
        <!-- <span> !   </span> -->
      </p>
      <p class="title content-title">
                   
      </p>
      <p class="title content" v-if="user.name == null">
                (   
         )
      </p>
      <p class="title content" v-else>
              
      </p>
      <div class="flex">
        <div style="margin-left:10px">
          <BaseButton @click="gofirstpage">  </BaseButton>
        </div>
        <div>
          <BaseButton @click="gotoCreateUser" v-if="user.name == null"
            > </BaseButton
          >
          <BaseButton @click="gotoStore" v-else> </BaseButton>
        </div>
      </div>

      <p class="title" style="margin-top:32px">
        <span>   ?</span>
      </p>
      <p class="title content-title">
               
      </p>

      <BaseButton @click="downloadApp">  </BaseButton>
    </div>
  </div>
</template>

<script>
import config from "@/config/config";
import { mapState } from "vuex";

export default {
  props: ["error", "access"],
  computed: {
    ...mapState({ user: (state) => state.user }),
    offline() {
      return config.offline;
    },
    justError() {
      return this.error && this.access ? true : false;
    },
  },
  methods: {
    gofirstpage() {
      this.$emit("gofirstpage");
    },
    gotoCreateUser() {
      window.open("https://www.otzar.org/wotzar/register.aspx?src=mef");
    },
    gotoStore() {
      window.open("https://www.otzar.org/wotzar/pcionlinestore.aspx");
    },
    downloadApp() {
      window.open("https://mefo.otzar.org/setup.exe");
    },
  },
};
</script>

<style lang="scss" scoped>
.title {
  font-family: "heebo";
  letter-spacing: 0px;
  font-size: 15px;
  font-weight: bold;
  color: #b48c4f;
  margin-bottom: 15px;
}
.title-main {
  margin-bottom: 23px;
}
.content-title {
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  color: #6d5940;
}
.content {
  height: 15px;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  margin-top: -8px;
  color: #6d5940;
}
</style>
