<template>
  <div
    class="card_mini_btn card_button"
    @click="toggleViewMode(id)"
    v-tooltip="'   '"
  >
    <font-awesome-icon icon="minus" v-if="fullViewMode" />
    <font-awesome-icon icon="plus" v-else />
  </div>
</template>

<script>
import store from "@/store/store";
import { mapActions } from "vuex";
import Vue from "vue";
export default {
  name: "MinimizeButton",
  props: ["id"],
  inject: ["inx"],
  computed: {
    fullViewMode() {
      let full = this.$parent.$parent.$parent.full;
      return full;
    }
  },
  data() {
    return {};
  },
  methods: {
    toggleViewMode() {
      store.getters.getCurrentTab.eventFullModeId += 1;
      store.getters.getCurrentTab.eventFullMode = this.inx;
      // store.getters.getCurrentTab.eventFullMode = null;
    }
  }
};
</script>

<style>
.card_mini_btn {
  width: 16px;
  height: 16px;
  display: flex;
}
.col-icon {
  flex: 1;
}
</style>
