import ShowUserLib from "@/components/meforshimLib/ShowUserLib.vue";
import CreateNewLib from "@/components/meforshimLib/CreateNewLib.vue";
import List from "@/views/List.vue";
import tabMiddleware from "@/middleware/tab-mw.js";

export default [
  {
    path: "/mef-user-libs",
    name: "mef-user-libs",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: List,
    meta: { middleware: [tabMiddleware], tab: 1 },
    children: [
      {
        path: "/mef-user-libs/create-new-mefrsh-user-lib",
        name: "create-new-mefrsh-user-lib",
        component: CreateNewLib
      }
    ]
  },
  {
    path: "/mef-user-libs/view-meforsh-user-lib/:folderId",
    name: "view-meforsh-user-lib",
    component: ShowUserLib,
    meta: { middleware: [tabMiddleware], tab: 1 }
  }
];
