<!--  -->
<template>
  <div class="folder-view">
    <div class="lib-head-block">
      <div class="folder-label">{{lib.label}}</div>
      <div v-show="viewMode===2">{{viewMode}}</div>
      <ViewMode />
      <router-link :to="{ name: 'mef-user-libs' }">
        <img src="@/./assets/icons/go-back.png" class="go-back-to-libs" alt />
      </router-link>
    </div>
    <img
      v-if="countSelectedSections===lib.secs.length"
      @click="removeAllSelectedSecs()"
      src="@/./assets/icons/selected-section-v.png"
      alt
      class="selected-section-v"
    />
    <img
      v-if="countSelectedSections!=0&&countSelectedSections!=lib.secs.length"
      @click="removeAllSelectedSecs()"
      src="@/./assets/icons/minus-checked-boxes-sections.png"
      alt
      class="minus-checked-boxes-sections"
    />

    <div v-if="countSelectedSections===0" @click="SelecteAllSecs()" class="selecte-box"></div>
    <delete-selected-sections
      v-if="displayDelete"
      @click.stop="displayDelete=false"
      :secs="lib.secs"
      :folderId="lib.folderId"
    />

    <img
      v-if="countSelectedSections!=0"
      @click.stop="openDeleteSection"
      class="delete-sections-icon"
      src="@/./assets/icons/trash-delete.png"
      alt
    />
    <div class="sections-block">
      <div>
        <section-user-card
          v-for="sec of lib.secs"
          :viewMode="viewMode"
          :lib="lib"
          :sec="sec"
          :key="lib.folderId+sec.section.secId"
        />
      </div>
    </div>
  </div>
</template>
<script >
import { mapGetters, mapState } from "vuex";
import ViewMode from "./sectionsUserLib/viewMode/ViewMode.vue";
import SectionUserCard from "./sectionsUserLib/SectionUserCard.vue";
import DeleteSelectedSections from "./sectionsUserLib/DeleteSelectedSections.vue";
import store from "@/store/store";
export default {
  data() {
    return {
      displayDelete: false
    };
  },
  components: {
    ViewMode,
    "section-user-card": SectionUserCard,
    "delete-selected-sections": DeleteSelectedSections
  },
  computed: {
    ...mapGetters("meforshimUserLibs", ["getLib"]),
    ...mapState("sectionsFolder", ["viewMode"]),
    ...mapState("sectionsFolder", ["countSelectedSections"]),
    lib() {
      return this.getLib(this.$route.params.folderId);
    }
  },
  methods: {
    openDeleteSection() {
      this.displayDelete = true;
    },
    SelecteAllSecs() {
      store.state.sectionsFolder.countSelectedSections = this.lib.secs.length;
      for (var i = 0; i < this.lib.secs.length; i++) {
        if (!this.lib.secs[i].section.selected) {
          let payload = {
            folderId: this.lib.folderId,
            secId: this.lib.secs[i].section.secId
          };
          this.$store.dispatch(
            "meforshimUserLibs/changeSelectedInSection",
            payload
          );
        }
      }
    },
    removeAllSelectedSecs() {
      store.state.sectionsFolder.countSelectedSections = 0;
      for (var i = 0; i < this.lib.secs.length; i++) {
        this.$store.dispatch(
          "meforshimUserLibs/setAllSelectedInLib",
          this.lib.folderId
        );
      }
    }
  }
};
</script>

<style scoped>
.selecte-box {
  background-color: white;
  border: 1px solid #cdcdcd;
  position: fixed;
  right: 8%;
  top: 200px;
  width: 14px;
  height: 14px;
}
.selected-section-v {
  position: fixed;
  right: 8%;
  top: 200px;
  width: 14px;
  height: 14.3px;
}
.minus-checked-boxes-sections {
  position: fixed;
  right: 8%;
  top: 200px;
  width: 14px;
  height: 14.3px;
}
.delete-sections-icon {
  position: fixed;
  right: 6%;
  top: 200px;
  width: 14px;
  height: 14.3px;
}
.sections-block {
  position: fixed;
  left: 10%;
  top: 180px;
  width: 80%;
  height: 490px;
  border: 1px solid rgb(167, 183, 185);
  overflow-y: scroll;
  overflow-x: hidden;
}
.go-back-to-libs {
  position: absolute;
  right: 60px;
  top: 55px;
  width: 16px;
  height: 10px;
  font-size: 20px;
}
.lib-head-block {
  width: 100%;
  height: 64px;
  color: #00a6a6;
  text-align: center;
  border: 1px solid rgb(167, 183, 185);
  margin: 1px;
  padding-top: 51px;

  position: absolute;
  top: 36px;
}
.folder-label {
  position: absolute;
  right: 84px;
  top: 45px;

  width: fit-content;
  height: 21px;
  color: #00a6a6;
  font-size: 20px;
}
.folder-container {
  font-size: 25px;
}
</style>