<template>
  <div class="switcher" @click="newTab">
    <span>  </span>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  props: ["popup"],
  methods: {
    ...mapActions("tabs", ["addTab"]),
    async newTab() {
      // console.log(this.popup);
      let page = Number(this.popup.MareMakom.pageNum);
      // page += 1;

      let tab = await this.addTab({
        book: this.popup.MareMakom.book,
        page
      });

      this.$router.push({
        name: "n-main-page",
        params: { bookid: this.popup.MareMakom.book, page, tab: tab }
      });
    }
  }
};
</script>

<style scoped>
.switcher {
  -webkit-box-flex: 0;
  -ms-flex-positive: 0;
  flex-grow: 0;
  -ms-flex-preferred-size: 120px;
  /* flex-basis: 120px; */
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  cursor: pointer;
  border-color: #98774f;
  background-color: white;
  color: #98774f;
  border-radius: 50px;
  padding: 0px 12px;
  height: 32px;
  border-width: 2px;
  border-style: solid;
  font-size: 14px;
  line-height: 32px;
}
.switcher:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
</style>
