<!-- this component contains:
 1 a list of libs that every lib is the component user lib card
 2 the options to sort the libs list from the component OrderLibsBy
 3 create a new lib that directed to the router create and goes to the component createNewLib -->
<template>
  <div>
    <div class="head_text"> </div>
    <!-- the sort component of the libs list-->
    <order-libs-by />
    <!-- the block for all libs and the create lib card -->
    <div class="folders_list_block">
      <!-- runs on all user's libs and shows a lib from the component userLibCard -->
      <user-lib-card v-for="lib of libs" :key="lib.folderId" :lib="lib" />
      <!-- the create card -->
      <div class="create-new-lib" @click.stop="goToCreateLib()">
        <!-- the plus icon -->
        <img class="create-new-lib-icon" src="@/./assets/icons/create-new-lib.png" alt />
        <router-view />
      </div>
    </div>
  </div>
</template>

<script >
//import components
import { mapState, mapGetters } from "vuex";
import UserLibCard from "./UserLibCard.vue";
import OrderLibsBy from "./OrderLibsBy.vue";
export default {
  methods: {
    //when user click on create card the router will change and show the CreateNewLib component
    goToCreateLib() {
      this.$router.push({ name: "create-new-mefrsh-user-lib" });
    }
  },
  components: {
    "user-lib-card": UserLibCard,
    "order-libs-by": OrderLibsBy
  },
  props: {
    lib: Object
  },
  computed: {
    //mapGetters to get the getLibs from getters
    ...mapGetters("meforshimUserLibs", ["getLibs"]),
    //the list of libs from the getter
    libs() {
      return this.getLibs;
    },

    isLibOpen() {
      return this.$route.name === "lib";
    }
  }
};
</script>
<!-- styling the meforshim libs page-->
<style scoped>
.head_text {
  position: fixed;
  left: 74%;
  top: 75px;
  width: 140px;
  height: 21px;
  color: #00a6a6;
  font-size: 20px;
  font-weight: 500;
}

.folders_list_block {
  width: 69%;
  height: 500px;

  border: 1px solid #dedede;
  position: fixed;
  left: 14%;
  top: 130px;
  overflow-y: scroll;
  overflow-x: hidden;
  float: right;
}
.create-new-lib-icon {
  width: 24.3px;
  height: 24.3px;
  padding-top: 51px;
}
.create-new-lib {
  background-color: #f5f5f5;
  float: right;
  width: 124px;
  height: 122px;
  text-align: center;
  border: 1px solid #dedede;
  margin: 10px;
  border-radius: 50%;

  cursor: pointer;
}
</style>
