<template>
  <span class="tab_btn" @click="setTab">
    <span class="tab_text">{{ getTabTitle }}</span>
    <span
      class="close mef-font mef-font-default"
      @click.stop="closeTab()"
      v-if="$store.state.tabs.tabs.length > 1"
    >
      <!-- <font-awesome-icon icon="times" /> -->
      q
    </span>
  </span>
</template>

<script>
import { mapActions } from "vuex";
import store from "@/store/store";
import TabState from "@/mixing/importTabState";
import TabGetters from "@/mixing/importTabGetters";
import Page from "../page/Page";
import { mapState } from "vuex";
import { close } from "fs";
export default {
  mixins: [TabState, TabGetters],
  methods: {
    ...mapActions("tabs", ["delTab", "setSelectedTab"]),
    setTab() {
      let selectedSection = null;
      let name = "n-main-page";
      if (this.tab.selectedSection !== null) {
        selectedSection = { sec: this.tab.selectedSection };
        name = "home-page-plus-section";
      }

      this.setSelectedTab(this.tabid);
      // Page.methods.resetMainCanvas();
      this.$router.push({
        ...name,
        params: {
          bookid: this.tab.book,
          page: this.tab.page,
          tab: this.tabid,
          ...selectedSection
        }
      }); // -> /user/123
    },
    closeTab() {
      let inx = this.$store.state.tabs.tabs.indexOf(this.tab.tabId);
      if (inx == 0) inx += 1;
      else inx -= 1;
      let tab = this.$store.state["tab_" + this.$store.state.tabs.tabs[inx]];
      // console.log(inx);
      // console.log(tab);

      let name = "n-main-page";
      let selectedSection = null;
      if (tab.selectedSection !== null) {
        selectedSection = { sec: tab.selectedSection };
        name = "home-page-plus-section";
      }
      let newTab = 0;
      if (this.tab.tabId == this.$store.state.tabs.currentTabId) {
        this.setSelectedTab(tab.tabId);
        this.$router.push({
          ...name,
          params: {
            bookid: tab.book,
            page: tab.page,
            tab: tab.tabId,
            ...selectedSection
          }
        });
        // newTab = tab.tabId;
      }

      this.$store.state.tabs.refreshPage = !this.$store.state.tabs.refreshPage;

      this.delTab({ tabId: this.tab.tabId, newTab });
      // console.log(this.$store.state.tabs);

      // this.delTab(this.tab.tabId);
    }
  }
};
</script>

<style scoped>
.tab_btn {
  position: relative;
  background-color: #ffffff;
  border-color: #dedede;
  border-width: 1px;
  border-style: solid;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-width: 96px;
  box-sizing: border-box;
  line-height: 29px;
  border-top-width: 0px;
  font-size: 14px;
  font-family: "Heebo";
  cursor: pointer;
  justify-content: right;
  padding: 0 10px;
}
.icon {
  margin: 4px;
}

.close {
  position: absolute;
  left: 0px;
  top: -5px;
  vertical-align: text-top;
  color: #b2b2b2;
  font-weight: 400;
}
.close:hover {
  color: black;
}
.selected {
  font-weight: bold;
  border-bottom-width: 0px;
}
.selected:hover {
  background-color: #ffffff;
}
.unselected:hover {
  background-color: #f5f5f5;
}
.selected:after {
  border-bottom: 2px solid white;
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  bottom: -2px;
  margin: 0 auto;
}
</style>
