<template>
  <div class="card_button" style="margin:0px;height:31px">
    <span
      class="mef-font link-icon"
      @click="copyLink"
      v-tooltip="'  '"
    >
      <HoverGray :color="false" h="25" w="25" t="2" l="4">C</HoverGray>
    </span>
  </div>
</template>

<script>
import copyTextToClipboard from "@/utils/copyText";
export default {
  props: ["meforesh"],
  data() {
    return {
      url: false,
    };
  },
  methods: {
    copyLink() {
      let data = this.$route.params;
      let book = data.bookid;
      let page = data.page;
      let section = data.sec;

      let rect = this.meforesh.id;
      let lnk = `https://mefo.otzar.org/#/t/658103.743778234/b/${book}/p/${page}/s/${section}/c/${rect}`;
      // let lnk = `http://work.mef.otzar.org/#/t/658103.743778234/b/${book}/p/${page}/s/${section}/c/${rect}`;

      copyTextToClipboard(lnk);
      this.$notify({
        group: "msgs",
        title: "Important message",
        text: "  ",
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.link-icon {
  font-size: 50px;
  line-height: 31px;
  width: 26px;
  height: 31px;
  display: inline-block;
}
.active {
  color: orange;
}
</style>
