import store from "@/store/store";
export default {
  store,
  install(Vue, options) {
    Vue.prototype.$inViewPort = elementInViewport;
    Vue.prototype.$someInViewPort = somePartOfelementInViewport;
    Vue.prototype.$topInViewPort = checkTopInVp;
  }
};

