<template>
  <Modal @close="tab.showAllBooksPopup = false">
    <template v-slot:header>
      <h1 style="text-align: center;"> </h1>
      <hr style="margin-bottom:10px;" />
      <div class="flex" style="justify-content: space-between;">
        <div>
          <label> :</label>
          <input autofocus type="text" v-model="fltTo" />
        </div>
        <div>
          <button style="margin-left:5px"> </button>
          <button @click="showSelected = !showSelected" style="width:120px">
            <span v-if="!showSelected">  </span>
            <span v-else> </span>
          </button>
        </div>
      </div>
      <hr style="margin-bottom:10px;margin-top:10px;" />
    </template>

    <template v-slot:body>
      <div class="lst-books">
        <div v-for="item in getDistinctBooksOnPage" :key="item">
          <BookItem :book="bookName(item)" :bookid="item" :tabid="tabid" />
          <!-- <span>{{ item }}</span> -->
        </div>
      </div>
    </template>
  </Modal>
</template>

<script>
import tabState from "@/mixing/importTabState";
import tabGetters from "@/mixing/importTabGetters";
import BookItem from "./BookItem.vue";
import secutil from "@/utils/section-util";
import { mapGetters } from "vuex";
export default {
  mixins: [tabState, tabGetters],
  components: { BookItem },
  props: ["tabid"],
  created() {},
  methods: {
    getSubBookId(item) {
      let ret = null;
      item.MefoToMarks.forEach(i => {
        //SHOLUD FIX IT AND USE SEC-UTIL
        //console.log(i.Marks.book + "|" + item.book);
        if (i.Marks != null && i.Marks.book != item.book) {
          debugger;
          ret = "9" + item.book + i.Marks.book;
          return;
        }
      });
      return ret;
    },
    bookNotFiltered(id) {
      if (this.tab.fltToBookIds.some(item => item == id)) return false;
      return true;
    }
  },
  data() {
    return {
      fltTo: "",
      showSelected: false
    };
  },
  computed: {
    ...mapGetters("books", ["getBookById"]),
    bookName() {
      return id => {
        let name = "";
        let book = this.getBookById(id);
        if (book != undefined) name = book.name;
        return name;
      };
    },
    getDistinctBooksOnPage() {
      let lst = this.getAllBooks;

      // debugger;
      let books = new Set();
      lst.forEach(element => {
        element.RectMefo.forEach(e => {
          if (this.showSelected) {
            if (this.bookNotFiltered(id)) return;
            books.add(id);
            return;
          }
          let id = secutil.getBookId(e);
          // let id = e.book;
          // let sub = this.getSubBookId(e);
          // if (sub != null && sub != id) {
          //   id = sub;
          // }
          // console.log(e.book);
          let book = this.getBookById(id);
          // console.log(book);

          if (
            book == undefined ||
            (!book.name.includes(this.fltTo) &&
              !book.authors[0].name.includes(this.fltTo))
          )
            return;
          books.add(id);
        });
      });

      return books;
    }
  }
};
</script>

<style lang="scss" scoped>
.lst-books {
  height: 380px;
  width: 500px;
  overflow-y: scroll;
}
</style>
