import Home from "@/views/Home.vue";
import Page from "@/components/MainPage/page/PageContainer.vue";
import SectionsSideBarContainer from "@/components/MainPage/sectionsSidebar/Container.vue";
import BooksMiddleware from "@/middleware/books-mw.js";
import tabMiddleware from "@/middleware/tab-mw.js";
import sectionMiddleware from "@/middleware/section-mw.js";
import workersMiddleware from "@/middleware/workers-mw.js";

export default [
  {
    path: "/",
    name: "home",
    redirect: { name: "selectPage" }
  },
  {
    path: "/b/:bookid/p/:page",
    name: "main-page",
    beforeEnter: (to, from, next) => {
      // ...
      let id = Math.round(Math.random() * 100000);
      next({
        name: "n-main-page",
        params: { bookid: to.params.bookid, page: to.params.page, tab: id }
      });
      // console.log("dfg");
    }
  },
  {
    path: "/t/:tab/b/:bookid",
    name: "n-main-book",
    props: { default: true, mefSideBar: true },
    component: Home,
    // meta: { middleware: tabMiddleware },
    children: [
      {
        path: "p/:page",
        name: "n-main-page",
        components: {
          default: Page,
          mefSideBar: SectionsSideBarContainer
        },
        meta: {
          middleware: [BooksMiddleware, workersMiddleware, tabMiddleware],
          tab: 0
        }
      },
      {
        path: "p/:page/s/:sec", //s/:secId
        name: "home-page-plus-section",
        components: {
          default: Page,
          mefSideBar: SectionsSideBarContainer
        },
        meta: {
          middleware: [
            BooksMiddleware,
            tabMiddleware,
            sectionMiddleware,
            workersMiddleware
          ],
          tab: 0
        }
      },
      {
        path: "p/:page/s/:sec/c/:chapter", //s/:secId
        name: "home-page-plus-section-plus-chapter",
        components: {
          default: Page,
          mefSideBar: SectionsSideBarContainer
        },
        meta: {
          middleware: [
            BooksMiddleware,
            tabMiddleware,
            sectionMiddleware,
            workersMiddleware
          ],
          tab: 0
        }
      }
    ]
  }
];
