<template>
  <div class="home_container">
    <ModalClean @close="showPop = false" v-if="showPop">
      <template v-slot:body>
        <img
          @click="showPop = false"
          :src="nismatSrc"
          style="height:66vh;display:block;cursor:pointer;-webkit-box-shadow: 10px 10px 76px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 10px 10px 76px 0px rgba(0,0,0,0.75);
box-shadow: 10px 10px 76px 0px rgba(0,0,0,0.75);"
        />
      </template>
    </ModalClean>
    <div v-if="!$isMobile">
      <TabPopupsContainer />
      <Split
        :gutterSize="Number(12)"
        style="height:calc(100vh - 42px);overflow-y:hidden"
        @onDragEnd="refreshMarkers"
        @onDrag="onDrag"
      >
        <!-- :size="$isMobile ? 100 : 69" -->
        <SplitArea
          :size="$isMobile ? 100 : 70"
          class="main_pages_splitter_container"
          :direction="$isMobile ? 'vertical' : 'horizontal'"
        >
          <div class="page-side-container">
            <div class="topbarSelectorsContainer">
              <div class="topbarTabsContainer">
                <Tabs v-show="!$isMobile" />
              </div>
              <div class="topbarPageContainer">
                <!-- <pagePicker></pagePicker> -->
              </div>
            </div>
            <div id="tab_content" class="tab-content">
              <keep-alive>
                <router-view />
              </keep-alive>
            </div>
          </div>
        </SplitArea>
        <!-- :size="$isMobile ? 100 : 31" -->
        <SplitArea
          v-focus
          tabindex="1"
          :size="$isMobile ? 100 : 30"
          :minSize="380"
          id="splitterMiddle"
          style="overflow-y:auto;direction:ltr"
          ref="splitter"
        >
          <div id="sections_content" class="sections_content home_item">
            <keep-alive>
              <router-view name="mefSideBar" />
            </keep-alive>
          </div>
        </SplitArea>
      </Split>
    </div>
    <!-- mobile page -->
    <div v-else>
      <div class="main_pages home_item">
        <h1>  </h1>
        <tabs v-show="!$isMobile" />
        <div id="tab_content">
          <!--<page/>-->
        </div>
      </div>
      <div id="sections_content" class="sections_content home_item">
        <!--<SectionsSideBarContainer />-->
      </div>
    </div>
  </div>
</template>

<script>
import Tabs from "@/components/MainPage/pageTabs/Tabs.vue";
import pagePicker from "@/components/MainPage/pagePicker/PagePickerContainer.vue";
import TabPopupsContainer from "@/components/MainPage/tabSectionsPopups/PopupsContainer.vue";
import { mapActions } from "vuex";
import store from "@/store/store";
import { ShasBooks } from "@/services/shasService";
export default {
  components: {
    Tabs,
    pagePicker,
    TabPopupsContainer
  },
  computed: {
    nismatSrc() {
      return "/images/" + this.pop;
    },
    keyPress() {
      return this.$store.state.keyPress;
    }
  },
  data() {
    return {
      showPop: false,
      pop: ""
    };
  },
  methods: {
    ...mapActions("tabs", ["refreshMarkers"]),
    onDrag(size) {
      //console.log('on Drag', size[1] * (window.screen.width/100) - 10) // callback new size
    }
  },
  beforeRouteUpdate(to, from, next) {
    let divs = this.$el.getElementsByClassName("split-horizontal");
    let div = null;
    for (let i = 0; i < divs.length; i++) {
      let item = divs.item(i);
      if (item.id == "splitterMiddle") div = item;
    }

    if (div != null) div.scrollTo(0, 0);

    if (
      from.params.bookid != undefined &&
      from.params.bookid != to.params.bookid
    ) {
      if (ShasBooks[to.params.bookid].showPop != undefined) {
        this.showPop = true;
        this.pop = ShasBooks[to.params.bookid].showPop;
      }
    }

    next();
  },
  async beforeRouteEnter(to, from, next) {
    // console.log(ShasBooks[to.params.bookid]);
    let ispop = false;
    let pop = "";
    if (
      to.params.bookid != undefined &&
      ShasBooks[to.params.bookid] != undefined &&
      ShasBooks[to.params.bookid].showPop != undefined
    ) {
      ispop = true;
      pop = ShasBooks[to.params.bookid].showPop;
    }
    //first time we load the page
    //create new tab with the needed info
    if (store.state.tabs.tabs.length > 0) {
      next(vm => {
        vm.showPop = pop;
        vm.pop = pop;
      });
      return;
    }
    // console.log(store.state.tabs.t);
    let _tab = {
      book: to.params.bookid,
      page: to.params.page
    };
    let tabid = null;
    if (typeof to.params.sec !== "undefined") {
      // _tab.selected_shas_sec=to.params.sec
    }
    if (store.state.tabs.currentTabId == null)
      tabid = await store.dispatch("tabs/addTab", _tab);

    next(vm => {
      vm.showPop = pop;
      vm.pop = pop;
    });
  },
  watch: {
    keyPress: function(val) {
      if (val == "MAIN-RIGHT") {
        this.$refs.splitter.$el.focus();
      }
    }
  },
  created() {
    this.$store.state.welcomeScreen = false;
    // console.log(this.$store);
  }
};
</script>

