<template>
  <div class="tabs" v-show="!$store.state.simpleMode">
    <TabBtn
      v-for="tabid in tabs"
      :key="tabid"
      :tabid="tabid"
      :class="[tabid == currentTabId ? 'selected' : 'unselected']"
    />
    <span class="add_btn" @click="newTab">
      <!-- <font-awesome-icon icon="plus-square" /> -->
      +
    </span>
  </div>
</template>

<script>
import TabBtn from "./TabBtn";
import { mapActions, mapState } from "vuex";
import store from "@/store/store";

export default {
  components: {
    TabBtn
  },
  computed: {
    ...mapState("tabs", ["tabs", "currentTabId"])
  },
  methods: {
    ...mapActions("tabs", ["addTab"]),
    async newTab() {
      let page = Number(this.$route.params.page);
      // page += 1;

      let tab = await this.addTab({
        book: this.$route.params.bookid,
        page
      });

      this.$router.push({
        name: "n-main-page",
        params: { bookid: this.$route.params.bookid, page, tab: tab }
      });
      this.$store.state.tabs.reloadAll = !this.$store.state.tabs.reloadAll;
    }
  },
  created() {}
};
</script>

<style scoped>
.tabs {
  display: block;
  direction: rtl;
  height: 29px;
}
.add_btn {
  cursor: pointer;
  color: #626262;
  height: 25px;
  width: 16px;
  font-weight: 500;
  font-size: 17px;
  margin-right: 9px;
}
.add_btn:hover {
  color: black;
}
</style>
