const Prints = {
  state() {
    return {
      printId: 0,
      all: false,
      sections: [],
      mode: "s"
    };
  },
  mutations: {
    ADD_TO_PRINT(state, sections) {
      state.sections = sections;
      state.printId += 1;
    },
    PUSH_TO_PRINT(state, sections) {
      state.sections.push(sections);
      //state.printId += 1;
    },
    DEL_PRINT(state, section) {
      state.sections = state.sections.filter(i => i.id != section.id);
      //state.printId += 1;
    }
  },
  actions: {
    addToPrint({ commit }, ...sections) {
      commit("ADD_TO_PRINT", sections);
    },
    pushToPrint({ commit }, sections) {
      commit("PUSH_TO_PRINT", sections);
    },
    delPrint({ commit }, section) {
      commit("DEL_PRINT", section);
    }
  },
  getters: {},
  namespaced: true
};

export default Prints;
