<template>
  <div @mouseenter="hover = true" @mouseleave="hover = false" style="height:100%;display: flex;">
    <div
      class="card"
      :class="[
        full ? 'card-big-pb' : 'card-small-pb',
        hover ? 'hover-bg' : '',
        hoverAndRow ? 'borders' : ''
      ]"
      v-waypoint="{
        active: true,
        callback: onWaypoint
      }"
    >
      <div v-if="this.meforesh.err && isItWorkEdition" class="card-error error-content">
        <p style="text-align:center">  </p>
        <p style="text-align:center">{{ meforesh.error_msg }}</p>
        <p style="text-align:center">  {{ meforesh.id }}</p>
        <p style="text-align:center">  {{ meforesh.book }}</p>
        <p v-if="tryGetPage == 0" style="text-align:center">    </p>
        <GoToEditor
          v-if="
            isItWorkEdition &&
              meforesh.MefoToMarks != null &&
              meforesh.book != undefined
          "
          :bookl="getCurrentTab.book"
          :bookr="meforesh.book"
          :pagel="getCurrentTab.page"
          :pager="tryGetPage"
          :bookname="getBookName"
        />
      </div>
      <!-- v-if="!this.meforesh.err" -->
      <!-- <transition name="fadeheight"> -->
      <div
        v-if="showme"
        style="margin-right: 11px;margin-left: 11px;height:100%;display: flex;
    flex-direction: column;"
      >
        <CardHeader :meforesh="meforesh" :data="$refs.contentdata" :inx="inx" :hover="hover" />
        <div v-if="showBook" style="background:white">
          <BookContent
            v-show="full"
            :hideheader="true"
            :book="meforesh.book"
            :page="meforesh.MefoToMarks[0].Marks.pageNum"
            :sections="meforesh"
          />
        </div>
        <div v-else style="flex-grow: 1;
    display: flex;">
          <div
            ref="contentdata"
            class="section-card"
            :style="{
              'max-height':
                isItWorkEdition || noLimit ? 999999999 : maxHeight + 'px'
            }"
            :class="{
              'section-card-no-scroll':
                tooLarge && (!isItWorkEdition || !noLimit)
            }"
            v-if="meforesh.type != 's'"
            @click="addHeight"
            v-show="full"
            @dblclick="openBookViewer"
          >
            <SectionType v-if="isItWorkEdition" :stype="meforesh.type" />
            <SectionContent
              @donedraw="checkHeight"
              :refresh="refresh"
              :inx="inx"
              :meforesh="meforesh"
            ></SectionContent>
            <ExpandIcon v-if="tooLarge && !isItWorkEdition" @click.stop="addHeight(false)" />
            <!--false-->
          </div>

          <div
            class="section-card"
            v-show="full"
            v-else
            @click="showfullshiur = !showfullshiur"
            @dblclick="openBookViewer"
          >
            <SectionType v-if="isItWorkEdition" :stype="meforesh.type" />
            <SectionContent :meforesh="meforesh" :inx="inx" />
            <hr v-if="showfullshiur" />
            <BookContent
              @click.stop
              v-if="showfullshiur"
              v-show="full"
              :hideheader="true"
              :book="meforesh.book"
              :page="meforesh.MefoToMarks[0].Marks.pageNum"
              :sections="meforesh"
            />
          </div>
        </div>

        <GoToEditor
          v-if="
            isItWorkEdition &&
              meforesh.MefoToMarks != null &&
              meforesh.MefoToMarks[0].Marks != undefined
          "
          :bookl="getCurrentTab.book"
          :bookr="meforesh.book"
          :pagel="getCurrentTab.page"
          :pager="meforesh.MefoToMarks[0].Marks.pageNum"
          :bookname="getBookName"
        />
      </div>
      <!-- </transition> -->
    </div>
  </div>
</template>

<script>
import ExpandIcon from "./ExpandIcon.vue";
import { mapGetters, mapState, mapActions } from "vuex";
import CardModule from "@/store/modules/section-card";
import store from "@/store/store";
import CardHeader from "./CardHeader/CardHeader";
import GoToEditor from "./WokersSectionOption/GoToWorkerEditor";
import SectionType from "./SectionType";
import ShasService from "@/services/shasService";
// import BookContent from "../section_card/BookViewerSection/Container";
import BookContent from "@/components/MainPage/SectionBookViewer/Viewer.vue";

export default {
  props: ["meforesh", "tabId", "inx"],
  // provide: function() {
  //   return {
  //     inx: this.inx
  //   };
  // },
  components: { BookContent, ExpandIcon, CardHeader, GoToEditor, SectionType },
  methods: {
    checkHeight(disableCheck = false) {
      let cnvs = this.$el.querySelectorAll("canvas");
      let height = 0;
      if (cnvs) {
        for (let cnv of cnvs) {
          height += cnv.clientHeight;
          // console.log(cnv.clientHeight);
        }
      }
      // if (disableCheck) return;
      // console.log(height);
      if (height > this.maxHeight) {
        this.tooLarge = true;
      } else this.tooLarge = false;
      // console.log(this.$el.clientHeight);
    },
    addHeight(disableCheck = false) {
      if (!this.tooLarge) {
        // let tmp = this;
        this.maxHeight = 282;
        // setTimeout(function() {
        //   tmp.maxHeight = 282;
        // }, 500);
        this.checkHeight(disableCheck);
        return;
      }
      this.maxHeight += 1282;
      if (this.maxHeight > this.$el.height) {
      }
      this.checkHeight();
    },
    onWaypoint({ going, direction }) {
      if (going === this.$waypointMap.GOING_IN) {
        this.showme = true;
      }
    },
    //...mapActions(`card-${this.tabId}-${this.meforesh.secId}`,["setViewMode"]),
    open_editor() {},
    openBookViewer() {
      let Data = this.meforesh;
      let _id = Math.floor(Math.random() * 1000000 + 1);
      let popup = {
        ...Data,
        ready: false,
        _id
      };

      store.state.tabs.popups.push(popup);
    },
    toggleScrollbar() {
      this.hideScrollbar = !this.hideScrollbar;
    }
  },
  computed: {
    ...mapGetters("tabs", ["getCurrentTab", "getFullModeOfMeforesh"]),
    ...mapGetters(["isItWorkEdition"]),
    //...mapGetters(`card-${this.tabId}-${this.meforesh.secId}`,["getCurrentViewMode"]),
    hoverAndRow() {
      return this.hover;
    },

    tryGetPage() {
      let page = 0;
      if (this.meforesh.MefoToMarks && this.meforesh.MefoToMarks.length > 0) {
        this.meforesh.MefoToMarks.forEach(element => {
          if (element.Marks != null) {
            page = element.Marks.pageNum;
            return;
          }
        });
      }
      return page;
    },
    getBookName() {
      let book = ShasService.getBookName(this.getCurrentTab.book);
      return ShasService.getBookName(this.getCurrentTab.book);
    },
    GlobalViewMode() {
      return this.getCurrentTab.viewFullMode;
    },
    ViewModeEventId() {
      return this.getCurrentTab.eventFullModeId;
    },
    ViewModeEvent() {
      return this.getCurrentTab.eventFullMode;
    },
    refreshSection() {
      return this.$store.state.tabs.refreshMarkers;
    }
  },
  watch: {
    GlobalViewMode: function(val) {
      this.full = val;
    },
    ViewModeEventId: function(val) {
      if (this.ViewModeEvent == this.inx) this.full = !this.full;
    },
    refreshSection: function() {
      if (this.$inViewPort(this.$el)) {
        // this.showBook = !this.showBook;
        // this.showBook = !this.showBook;
        this.refresh += 1;
      }
    }
  },
  data() {
    return {
      picture: [],
      section: null,
      showme: false,
      showfullshiur: false,
      full: true,
      hover: false,
      noLimit: false,
      hideScrollbar: true,
      showBook: false,
      refresh: 0,
      tooLarge: false,
      maxHeight: 282
    };
  },
  created() {
    this.full = this.getCurrentTab.viewFullMode;
  },
  beforeDestroy() {}
};
</script>

<style lang="scss" scoped>
.hover-bg {
  background-color: #eeeeee;
}
.section-card {
  width: 100%;
  border-color: #d2d2d2;
  border-width: 2px;
  background-color: #ffffff;
  border-style: solid;
  margin-top: 3px;
  max-width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  position: relative;
  // height: 347px;
}
.section-card-no-scroll {
  // max-height: 282px; //347px
  transition: max-height 0.5s;
}
.section-card-no-scroll:after {
  content: " ";
  height: 63px;
  bottom: 0px;
  left: 0;
  width: 100%;
  /* background-color: white; */
  position: absolute;
  /* opacity: 0.9; */
  /* fill: #58b5b5; */
  background: transparent linear-gradient(180deg, #ffffff00 0%, #ffffff 100%) 0%
    0% no-repeat padding-box;
}
.card-big-pb {
  padding-bottom: 12px;
}
.card-small-pb {
  padding-bottom: 2.5px;
}
.card {
  direction: rtl;

  padding-top: 2.5px;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 30px;
  cursor: pointer;
  border-width: 1px;
  border-color: transparent;
  border-bottom-color: #dedede;
  border-style: solid;
  border-top-width: 0px;
}
.card-error {
  color: red;
  text-align: center;
}
.error-content {
  font-weight: 600;
  color: red;
}
.fadeheight-enter-active,
.fadeheight-leave-active {
  transition: opacity 0.3s;
  // max-height: 230px;
}
.fadeheight-enter, .fadeheight-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  // max-height: 0px;
}
.borders {
  border-width: 1px;
  border-color: #dedede;
  border-style: solid;
  border-top-width: 0px;
  box-sizing: content-box;
}
</style>
