<template>
  <!-- user lib card that inside list of libs in the component MeforshimLibList
this component do:
show label and length of sections lib and pin to top if its true
open lib 
  show component context menu-->
  <div>
    <!-- open the lib by dbclick on it -->
    <div class="folder" @click.stop="goToLib(lib)">
      <context-menu-lib class="context" :lib="lib" />
      <div>
        <!-- length of sections folder -->
        <div class="folder-secs">{{lib.secs.length}}</div>
        <!-- lib's label -->
        <div class="folder-label">{{lib.label}}</div>
        <!-- lib's date -->
        <div class="folder-date">{{currentDate(lib.updated_date)}}</div>
        <!-- pin to top -->

        <div v-show="lib.pinToTop===true" class="pin-to-top-icon">
          <img src="@/./assets/icons/pin-to-top.png" alt />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";

import ContextMenuLib from "./ContextMenuLib.vue";
import { mapMutations, mapState, mapGetters } from "vuex";
import store from "@/store/store";
export default {
  //components
  components: {
    "context-menu-lib": ContextMenuLib
  },
  //props
  props: {
    //passing lib object and get it from its parent MeforshimLibsList
    lib: Object
  },
  computed: {
    ...mapState("sectionsFolder", ["countSelectedSections"])
  },
  data() {
    return {
      /* myDate: this.lib.updated_date,
      month: ("0" + (myDate.getMonth() + 1)).slice(-2),
      date: ("0" + myDate.getDate()).slice(-2),
      year: myDate.getFullYear(),
      formattedDate: date + "/" + month + "/" + year */
    };
  },
  methods: {
    //when user clicks on lib it will open in another page in another router
    currentDate(myDate) {
      var month = ("0" + (myDate.getMonth() + 1)).slice(-2);
      var date = ("0" + myDate.getDate()).slice(-2);
      var year = myDate.getFullYear();
      var formattedDate = date + "/" + month + "/" + year;
      //document.getElementById("dateDemo").innerHTML = formattedDate;
      return formattedDate;
    },
    goToLib(lib) {
      store.state.sectionsFolder.countSelectedSections = 0;
      this.$store.dispatch(
        "meforshimUserLibs/setAllSelectedInLib",
        lib.folderId
      );
      this.$router.push({
        name: "view-meforsh-user-lib",
        params: { folderId: lib.folderId }
      });
    }
  }
};
</script>
<!-- styling the user lib card-->
<style scoped>
.folder {
  background-color: #f5f5f5;
  float: right;
  width: 124px;
  height: 122px;
  border: 1px solid #dedede;
  margin: 10px;
  cursor: pointer;
  z-index: 2;

  border-radius: 50%;
}
.context {
  z-index: 1;
}
.folder-label {
  color: #000000;
  font-size: 15px;
  text-align: center;
  position: relative;
  left: 24px;
  top: 28px;
  width: 74px;
  height: 71px;

  word-wrap: break-word;
}
.folder-date {
  color: #b7b7b7;
  font-size: 11px;
  position: relative;
  left: 37px;
  bottom: 3px;
}
.pin-to-top-icon {
  font-size: 11px;
  position: relative;
  left: 91px;

  bottom: 92px;
}
.folder-secs {
  color: #00a6a6;
  font-size: 11px;
  position: relative;
  left: 60px;
  top: 3px;
}
</style>