export default {
  sortByTop(lst) {
    lst.sort((f, s) => {
      let ret = f.Marks.pageNum - s.Marks.pageNum;
      if (ret <= 0) return 0;
      // priorety by page
      else {
        //compare by order
        return s.Marks.order - f.Marks.order;
      }
    });
  },
  getBookId(data) {
    let id = data.book;
    let done = false;
    if (data.MareMakom != null) return data.MareMakom.book;
    if (data.MefoToMarks != undefined) {
      data.MefoToMarks.forEach(i => {
        if (done) return;
        // console.log(i.Marks);
        if (i.Marks != null && i.Marks.subbook != "0") {
          //&& i.Marks.book != id
          id = "9" + id + i.Marks.subbook;
          done = true;
          return;
        }
      });
    }
    return id;
  }
};
