<template>
  <div class="viewer unselectable" @scroll="top = '100px'" @click="sendUp">
    <!-- <Header :bookname="bookname" :bookauthor="bookauthor" /> -->
    <!-- <NavigationButtons
      @next="page += 1"
      @back="page -= 1"
      style="text-align:center"
      mask="0"
      @click.prevent
    />-->

    <Content
      style="padding: 15px;padding-left: 5px;"
      class="book-content"
      :book="book"
      :refresh="refresh"
      :resizevent="resizevent"
      :page="page"
      :sections="sections"
      v-if="book"
      @donedraw="sendDoneUp"
    />
  </div>
</template>

<script>
import NavigationButtons from "@/components/MainPage/SectionBookViewer/NavigationButtons.vue";
import Header from "./HeaderContainer.vue";
import Content from "./Content.vue";

export default {
  //props:["picture","sections","bookname","bookauthor"],
  props: ["sections", "resizevent", "vrefresh", "page", "book", "mask"],
  data() {
    return {
      // page: null,
      // book: null,
      height: "",
      top: "0px",
      refresh: 0,
    };
  },
  components: {
    Header,
    Content,
    NavigationButtons,
  },
  // watch: {
  //   realParentHeight() {
  //     console.log("j");
  //   }
  // },
  methods: {
    sendUp(e) {
      this.$emit("click", e);
    },
    sendDoneUp(e) {
      this.$emit("donedraw", e);
    },
    // maskPic() {
    //   this.refresh++;
    //   this.mask = !this.mask;
    // }
  },
  watch: {
    vrefresh: function() {
      this.refresh++;
    },
  },
  updated() {
    this.height = this.$el.clientHeight + "px";
    this.top = this.$el.scrollTop + "px";
  },
  created() {
    this.book = this.sections.book;
    if (this.sections.type == "em" || this.sections.type == "mh") {
      this.mask = false;
      this.book = this.sections.MareMakom.book;
      this.page = this.sections.MareMakom.pageNum;
    } else this.page = this.sections.MefoToMarks[0].Marks.pageNum;
  },
};
</script>

<style>
.buttons-book {
  position: relative;
  flex-basis: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
/* .buttons-insider {
  position: absolute;
  left: 0px;
  box-sizing: content-box;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #626262;
  
  background: #f5f5f5 0% 0% no-repeat padding-box;
  border: 1px solid #dedede;
  width: 32px;
} */
.viewer {
  display: flex;
  flex-flow: row;
  height: 100%;
}
.book-content {
  flex: 1;
}
</style>
