<template>
  <div class="header-container">
    <!-- <BookCheckBox :meforesh="meforesh" /> -->

    <BookName :bookname="getBookName" :bookauth="getBookAuthor" @click="toggleCard" />

    <ButtonsContainer :meforesh="meforesh" :data="data" :hover="hover" />
  </div>
</template>

<script>
import BookName from "./BookName.vue";
import BookCheckBox from "./CheckBox.vue";
import ButtonsContainer from "../CardButtons/ButtonsContainer.vue";
import secutil from "@/utils/section-util";
import { mapGetters } from "vuex";

export default {
  name: "CardHeader",
  // inject: ["inx"],
  props: ["meforesh", "data", "inx", "hover"],
  methods: {
    toggleCard() {
      // alert(this.inx);
      this.$store.getters.getCurrentTab.eventFullModeId += 1;
      this.$store.getters.getCurrentTab.eventFullMode = this.inx;
    }
  },
  computed: {
    ...mapGetters("books", ["getBookById"]),
    getBookAuthor() {
      return this.getBookData.authors.filter(
        i => i.authorTypeId == 1 || i.authorTypeId == undefined
      );
    },
    getBookName() {
      return this.getBookData.name;
    },
    getBookData() {
      let book = secutil.getBookId(this.meforesh); //this.meforesh.book;

      // //create the id of subbook if needed
      // if (book !== this.meforesh.MefoToMarks[0].Marks.book) {
      //   // debugger;
      //   book = "9" + this.meforesh.MefoToMarks[0].Marks.book + book;
      //   // debugger;
      // }
      let _book = this.getBookById(Number(book));
      // console.log(_book);
      // if (_book == undefined) debugger;
      return _book;
    }
  },
  components: {
    BookName,
    BookCheckBox,
    ButtonsContainer
  }
};
</script>

<style>
.header-container {
  width: 100%;
  position: relative;
  align-items: center;
  display: flex;
}
</style>
