import newtab from "@/store/modules/_tab_shaddow.js";
let tabs = [];
for (let item in localStorage) {
  if (item.startsWith("pin_tab")) {
    let dataFromLocalStorage = localStorage.getItem(item);
    let _item = newtab(JSON.parse(dataFromLocalStorage));
    tabs.push(_item);
  }
}
export default tabs;
