<template>
  <div>
    <div
      v-bind:class="{ 'section-card-block-lines':viewMode===1&&sec.section.selected===false,
      'section-card-block-lines-open-container':viewMode===1&&sec.section.selected===false&&displayShowLineSection===true,
      'section-card-block-lines-selected':viewMode===1&&sec.section.selected===true,
      'section-card-block-lines-selected-open-container':viewMode===1&&sec.section.selected===true&&displayShowLineSection===true,
       'section-card-block-small-box':viewMode===2&&sec.section.selected===false,'section-card-block-small-box-selected':viewMode===2&&sec.section.selected===true, 'section-card-block-big-box':viewMode===3&&sec.section.selected===false,'section-card-block-big-box-selected':viewMode===3&&sec.section.selected===true}"
    >
      <img
        v-if="sec.section.selected"
        @click.stop="changeSelectedOfSection(countSelectedSections-1)"
        src="@/./assets/icons/selected-section-v.png"
        alt
        v-bind:class="{ 'selected-section-v-lines':viewMode===1, 'selected-section-v-small-box':viewMode===2, 'selected-section-v-big-box':viewMode===3}"
      />
      <div
        v-if="!sec.section.selected"
        @click.stop="changeSelectedOfSection(countSelectedSections+1)"
        v-bind:class="{ 'selecte-box-lines':viewMode===1, 'selecte-box-small-box':viewMode===2, 'selecte-box-big-box':viewMode===3}"
      ></div>
      <!-- <BookLogo
        v-bind:class="{ 'sec-book-source-lines':viewMode===1, 'sec-book-source-small-box':viewMode===2, 'sec-book-source-big-box':viewMode===3}"
        :book="sec.section.sections[0].book"
      />-->
      <div
        v-bind:class="{ 'book-name-lines':viewMode===1, 'book-name-small-box':viewMode===2, 'book-name-big-box':viewMode===3}"
      >{{sec.section.bookName}}</div>
      <div
        v-bind:class="{ 'book-author-lines':viewMode===1, 'book-author-small-box':viewMode===2, 'book-author-big-box':viewMode===3}"
      >{{sec.section.bookAuthor}}</div>
      <img
        @click.stop="openDeleteSection"
        v-bind:class="{ 'delete-icon-lines':viewMode===1,
        'delete-icon-lines-open-container':viewMode===1&&displayShowLineSection===true,
         'delete-icon-small-box':viewMode===2, 
         'delete-icon-big-box':viewMode===3}"
        src="@/./assets/icons/trash-delete.png"
        alt
      />
      <div v-if="viewMode!=1||displayShowLineSection===true">
        <SectionContent
          v-bind:class="{ 'section-container-lines':viewMode===1, 'section-container-small-box':viewMode===2, 'section-container-big-box':viewMode===3}"
          :meforesh="sec.section"
        />
      </div>

      <delete-section-in-user-lib
        v-if="displayDelete"
        @click.stop="displayDelete=false"
        :sec="sec"
        :folderId="lib.folderId"
      />

      <img
        v-if="displayShowLineSection === true&&viewMode===1"
        @click.stop="hideLineSection"
        class="minus-hide-cont-sec-lines"
        src="@/./assets/icons/minus-hide-cont-sec.png"
        alt
      />
      <img
        v-if="displayShowLineSection === false&&viewMode===1"
        @click.stop="showLineSection"
        class="plus-show-cont-sec-lines"
        src="@/./assets/icons/plus-show-cont-sec.png"
        alt
      />
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import store from "@/store/store";
import DeleteSectionInUserLib from "./DeleteSectionInUserLib.vue";
export default {
  components: {
    "delete-section-in-user-lib": DeleteSectionInUserLib
  },
  props: {
    lib: Object,
    sec: Object
  },
  data() {
    return {
      displayDelete: false,
      displaySelected: this.countSelectedSections,
      displayShowLineSection: false,
      vcoConfig: {
        handler: this.hideLineSection,
        middleware: this.middleware,
        events: ["dblclick", "click", "displayShowLineSection"],

        isActive: true
      }
    };
  },
  computed: {
    ...mapState("sectionsFolder", ["countSelectedSections"]),
    ...mapState("sectionsFolder", ["viewMode"])
  },
  methods: {
    showLineSection() {
      this.displayShowLineSection = true;
    },
    hideLineSection() {
      this.displayShowLineSection = false;
    },
    changeClass() {
      return {
        lines: (this.viewMode = 1),
        small_box: (value = 2),
        big_box: (value = 3)
      };
    },
    openDeleteSection() {
      this.displayDelete = true;
    },
    changeSelectedOfSection(countSelectedSections) {
      if (this.sec.section.selected) {
        store.state.sectionsFolder.countSelectedSections = countSelectedSections;
      } else {
        store.state.sectionsFolder.countSelectedSections = countSelectedSections;
      }
      let payload = {
        folderId: this.lib.folderId,
        secId: this.sec.section.secId
      };
      this.$store.dispatch(
        "meforshimUserLibs/changeSelectedInSection",
        payload
      );
    }
  }
};
</script>

