<template>
  <!-- context menu when user click on one of his lib it will open the context menu with the options: 
pin to top lib
update lib
delete lib
  this component we use in UserLibCard component-->
  <div
    class="context-menu"
    @contextmenu.prevent="showContextMenu($event)"
    v-click-outside="vcoConfig"
  >
    <!-- the delete option -->
    <delete-lib v-show="displayDelete" @click.stop="displayDelete=false" :lib="lib" />
    <!-- the update option -->
    <update-lib v-show="displayUpdate" @click.stop="displayUpdate=false" :lib="lib" />
    <!-- the list of the options-->
    <div class="menu" v-show="showMenu">
      <ul>
        <li>
          <!-- the pin to top option -->
          <pin-to-top @click.stop="showMenu=false" :lib="lib" />
        </li>
        <li @click.stop="openUpdateLib">update</li>
        <li @click.stop="openDeleteLib">delete</li>
      </ul>
    </div>
  </div>
</template>

<script>
import PinToTop from "./PinToTop.vue";
import UpdateLib from "./UpdateLib.vue";
import DeleteLib from "./DeleteLib.vue";
export default {
  data() {
    return {
      //sett the default
      showMenu: false,
      displayDelete: false,
      displayUpdate: false,
      //called when click outside is on
      vcoConfig: {
        handler: this.closeContext,
        middleware: this.middleware,
        events: ["dblclick", "click", "contextmenu"],

        isActive: true
      }
    };
  },
  props: {
    //gettin lib object from userLibCard
    lib: Object
  },
  //components
  components: {
    "pin-to-top": PinToTop,
    "update-lib": UpdateLib,
    "delete-lib": DeleteLib
  },
  methods: {
    openUpdateLib() {
      this.displayUpdate = true;
      this.showMenu = false;
    },
    openDeleteLib() {
      this.displayDelete = true;
      this.showMenu = false;
    },
    //closing context
    closeContext() {
      this.showMenu = false;
    },

    //open context when right click on lib
    openContext() {
      this.showMenu = true;
    },
    //for click outside
    middleware(event, el) {
      return this.showMenu;
    },
    //set the event of wher context menu was clicked and open it
    showContextMenu(e) {
      let menu = this.$el.querySelector(".menu");
      let x = e.offsetX;
      let y = e.offsetY;
      menu.setAttribute(
        "style",
        `left:${x}px;
        top:${y}px`
      );
      this.showMenu = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.context-menu {
  width: 124px;
  height: 122px;
  cursor: pointer;
  position: absolute;
  vertical-align: top;
  border-radius: 50%;
}
.menu ul {
  list-style: none;
  width: 100px;
  height: auto;
  border: 1px solid black;
  background-color: white;
  font-size: 11px;
  vertical-align: top;
  margin: 0;
  padding: 0;
  position: relative;
}
.menu ul li {
  height: 20px;
  margin: 1px solid black;
  padding: 1px 1px 1px 1px;
  cursor: pointer;
}
.menu ul li:hover {
  background-color: #eee;
  padding: 1px 1px 1px 1px;
  cursor: pointer;
}
</style>