<template>
  <div
    class="FilterCombo unselecable"
    @click="openCombo"
    style="cursor:pointer"
    :class="{ FilterComboFocus: showMenu }"
    v-click-outside="closeCombo"
  >
    <!-- {{ selected }} -->
    <div class="selected-text">{{selected_content}}</div>
    <div class="arrow-down-col">
      <span class="mef-font" style="display:inline-block;font-size:60px;line-height:20px">p</span>
      <!-- <font-awesome-icon icon="caret-down" /> -->
    </div>
    <div
      :class="{
        'open-menu': showMenu && !grid,
        'open-menu-grid': showMenu && grid,
        'option-items': !grid,
        'option-items-grid': grid
      }"
      @click.stop="closeCombo(close)"
    >
      <div
        v-for="(item, index) in items"
        :key="index"
        :class="{ 'combo-item': !grid, 'combo-item-grid': grid }"
      >
        <p
          class="unselectable"
          :class="{ 'label-option': grid }"
          @click="triggerInput(item)"
          v-html="item[LabelKey]"
        ></p>
        <!-- {{ item[LabelKey] }} -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ComboboxInput",
  props: {
    grid: {
      default: false
    },
    items: {},
    LabelKey: {},
    value: {
      default: ""
    },
    close: {
      default: true
    },
    label: {
      type: String,
      default: " "
    },
    content: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      showMenu: false
    };
  },
  computed: {
    selected() {
      if (typeof this.value[this.LabelKey] === "undefined") return this.label;
      return this.value[this.LabelKey];
    },
    selected_content() {
      if (this.content != null) return this.content;
      return this.selected;
    }
  },
  methods: {
    openCombo() {
      this.showMenu = !this.showMenu;
    },
    closeCombo(close = true) {
      if (!close) return;
      this.showMenu = false;
    },
    triggerInput(data) {
      this.$emit("input", data);
    }
  }
};
</script>

<style scoped>
.FilterCombo {
  display: flex;
  height: inherit;
  box-sizing: border-box;
  border-style: solid;
  border-color: #c6c6c6;
  background-color: white;
  cursor: pointer;
  border-radius: 50px;
  width: 100%;
  text-align: right;
  border-width: 1px;
  position: relative;
  flex-direction: row;

  align-items: center;
}
.FilterComboFocus {
  border-color: gray;
}
.arrow-down-col {
  width: 18px;
  height: 20px;
  text-align: left;
}

.selected-text {
  height: 18px;
  padding-right: 6px;
  padding-left: 3px;
  font-size: 12px;
  text-align: right;
  width: calc(100% - 22px);
  text-overflow: ellipsis;
  white-space: nowrap;
  display: block;
  overflow: hidden;
}
.option-items {
  display: none;
  position: relative;
  z-index: 2;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  margin-top: 1px;
  max-height: 300px;
  overflow-y: scroll;
}
.option-items-grid {
  display: none;
  position: absolute;
  flex-direction: row;
  z-index: 2;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  left: 0px;
  top: 100%;
  width: 256px;
  flex-wrap: wrap;
}
.open-menu {
  overflow: auto;
  display: block;
  cursor: pointer;
  width: 100%;
  top: 100%;
  position: absolute;
}
.open-menu-grid {
  display: flex;
  width: 256px;
  left: -50%;
  top: 100%;
  position: absolute;
}
.combo-item {
  text-align: right;
  padding-right: 7px;
  font-size: 13px;
  line-height: 23px;
}
.combo-item:hover {
  background-color: #f5f5f5;
  /* text-shadow: 1px 1px blue; */
}

.combo-item-grid {
  height: 30px;
  width: 30px;
  align-self: flex-start;
  border-style: solid;
  border-width: 1px;
  border-color: #f5f5f5;
  font-size: 14px;
  text-align: center;
  display: table;
}

.label-option {
  display: table-cell;
  vertical-align: middle;
}
.label-option:hover {
  background-color: #dedede;
}
</style>
