<template>
  <div>
    <ArrowRightThick @click="goTo(-1)" class="hover"></ArrowRightThick>
    <component :is="maskActive" @click="goTo(1)" class="hover"></component>
    <ArrowLeftThick @click="goTo(1)" class="hover"></ArrowLeftThick>
  </div>
</template>

<script>
import ArrowLeftThick from "vue-material-design-icons/ArrowLeftThick.vue";
import ArrowRightThick from "vue-material-design-icons/ArrowRightThick.vue";
import EyeOffOutline from "vue-material-design-icons/EyeOffOutline.vue";
import EyeOutline from "vue-material-design-icons/EyeOutline.vue";
export default {
  components: { ArrowRightThick, ArrowLeftThick, EyeOutline, EyeOffOutline },
  props: ["mask"],
  methods: {
    goTo(page) {
      if (page == 1) this.$emit("next");
      else this.$emit("back");
    }
  },
  computed: {
    maskActive() {
      if (this.mask) return EyeOffOutline;
      return EyeOutline;
    }
  }
};
</script>

<style lang="scss" scoped></style>
