<template>
  <div class="popupHeader">
    <Title :bookname="bookData.name" bookauth />
    <TabCreate
      :id="id"
      v-if="popup.MareMakom != null && isShasBook"
      style="margin-left: 5px"
      :popup="popup"
    />
    <Switcher :id="id" v-if="popup.MareMakom == null" />

    <!-- <Close @click="closePopUp()" /> -->
    <span
      style="height: 28px; border-right: 1px solid #dedede; margin: 0px 10px"
    ></span>
    <div class="unselectable">
      <!--class="buttons-book" :style="{ height: height, top: top }" -->
      <!-- :style="{'height':realParentHeight}" -->
      <div class="buttons-insider">
        <span
          v-if="popup.MareMakom == null"
          @click="$emit('mask')"
          class="mef-font mef-font-default"
          style="height: 30px; line-height: 30px; width: 31px; margin-top: 0px"
        >
          <HoverGray h="25" w="25" t="3" l="3">
            <template v-if="mask">l</template>
            <template v-else>x</template>
          </HoverGray>
        </span>
        <span
          @click="$emit('backpage')"
          class="mef-font mef-font-default"
          style="
            height: 30px;
            line-height: 26px;
            width: 31px;
            margin-top: 2px;
            padding-bottom: 12px;
            box-sizing: border-box;
          "
        >
          <HoverGray h="25" w="25" t="1" l="3">J</HoverGray>
        </span>
        <span
          @click="$emit('nextpage')"
          class="mef-font mef-font-default"
          style="height: 31px; line-height: 31px; width: 24px; margin-top: 0px"
        >
          <HoverGray h="25" w="25" t="3" l="-1">j</HoverGray>
        </span>

        <!-- <span
          class="mef-font mef-font-default"
          style="height:30px;line-height:30px;width:31px;margin-top:0px"
        >
          <HoverGray h="25" w="25" t="3" l="4">v</HoverGray>
        </span>-->

        <!-- <span
          class="mef-font mef-font-default"
          style="height:30px;line-height:30px;width:31px;margin-top:0px;margin-right: 3px;"
        >
          <HoverGray h="25" w="25" t="3" l="4">u</HoverGray>
        </span>-->
        <div style="margin-right: 3px">
          <BookLogo
            :book="popup.MareMakom == null ? popup.book : popup.MareMakom.book"
          />
        </div>

        <!-- <span
          class="mef-font mef-font-default"
          style="height:30px;line-height:30px;width:31px;margin-top:0px"
        >
          <HoverGray h="25" w="25" t="3" l="-2">s</HoverGray>
        </span>-->
      </div>
    </div>

    <span
      style="height: 28px; border-right: 1px solid #dedede; margin-right: 10px"
    ></span>
    <span
      class="mef-font mef-font-default change-mode-popup"
      @click="toggleSize"
    >
      <HoverGray h="25" w="25" t="23" l="0">
        <template v-if="!big">S</template>
        <template v-else>T</template>
      </HoverGray>
    </span>
    <!-- <span class="mef-font mef-font-default close-popup" @click="addToMinibar()" style="margin:4px">
      <HoverGray h="25" w="25" t="-7" l="-4">p</HoverGray>
    </span>-->
    <span class="mef-font mef-font-default close-popup" @click="closePopUp()">
      <HoverGray h="25" w="25" t="-7" l="-4">q</HoverGray>
    </span>
  </div>
</template>

<script>
import Close from "./PopupCloseButton.vue";
import Title from "./PopupTitle.vue";
import Switcher from "./PopupSwitcher.vue";
import TabCreate from "./PopupNewTab.vue";
import store from "@/store/store";
import secutil from "@/utils/section-util";
import { ShasBooks } from "@/services/shasService";
import { mapActions } from "vuex";
export default {
  props: ["id", "popup", "mask"],
  components: {
    Close,
    Title,
    Switcher,
    TabCreate
  },
  computed: {
    getBookId() {
      let bookid = secutil.getBookId(this.popup);
      return bookid;
    },
    isShasBook() {
      // console.log(this.popup.MareMakom);
      // console.log(ShasBooks[this.popup.book]);
      return (
        ShasBooks[this.popup.MareMakom.book] != undefined &&
        ShasBooks[this.popup.MareMakom.book].done
      );
    },
    bookData() {
      let bookid = this.getBookId;
      let book = store.getters["books/getBookById"](bookid);
      return book;
    }
  },
  data() {
    return {
      big: true,
      size: null,
      maskPic: true
    };
  },
  methods: {
    ...mapActions("minibar", ["addMiniBarItem"]),
    closePopUp() {
      store.state.tabs.popups = store.state.tabs.popups.filter(
        (item) => item._id != this.id
      );
    },
    showMe(show = true) {
      this.$emit("show", show);
    },
    addToMinibar() {
      // console.log(this);
      this.showMe(false);
      let item = {
        label: this.bookData.name,
        close: this.closePopUp,
        click: this.showMe
      };
      this.addMiniBarItem(item);
    },
    toggleSize() {
      // let toSize = 0;
      // if (this.size == null) this.size = this.$parent.$el.clientWidth;
      // if (this.big) toSize -= this.size * 0.1;
      // else toSize += this.size * 0.1;
      // let style = this.$parent.$el.style.cssText + `;width:${toSize}px;`;
      // this.$parent.$el.setAttribute("style", style);
      // this.big = !this.big;
      this.$emit("togglewidth", this.big);
      this.big = !this.big;
    }
  }
};
</script>

<style>
.buttons-insider {
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  /* align-items: center; */
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: row;
  color: #626262;
}
.popupHeader {
  height: 56px;
  border-bottom-color: #dedede;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  background-color: #e9e9e9;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom-width: 1px;
  border-bottom-style: solid;
}
.close-popup {
  line-height: 10px;
  margin: 0 20px;
  color: #626262;
  margin-right: 3px;
}
.close-popup:hover {
  color: black;
  cursor: pointer;
}
.change-mode-popup {
  margin-left: 5px;
  color: #626262;
  margin-right: 12px;
}
.change-mode-popup:hover {
  color: black;
  cursor: pointer;
}
</style>
