<template>
  <div
    class="content"
    v-waypoint="{
      active: true,
      callback: onWaypoint,
      options: intersectionOptions,
    }"
  >
    <canvas class="bookImage"></canvas>
  </div>
</template>

<script>
import getcode from "@/utils/enc";
import PictureService from "@/services/pictureService.js";
import { mapGetters } from "vuex";
import config from "@/config/config";
import axios from "@/services/_axiosInstance";
import djvu from "@/utils/djvu";
import pdfImage from "@/utils/pdfreader";
import rotateCanvas from "@/utils/canvasRotate";
let mainCanvas = null;
function cloneCanvas(oldCanvas) {
  //create a new canvas
  var newCanvas = document.createElement("canvas");
  var context = newCanvas.getContext("2d");

  //set dimensions
  newCanvas.width = oldCanvas.width;
  newCanvas.height = oldCanvas.height;

  //apply the old canvas to the new one
  context.drawImage(oldCanvas, 0, 0);

  //return the new canvas
  return newCanvas;
}
export default {
  data() {
    return {
      intersectionOptions: {
        root: null,
        rootMargin: "0px 0px 0px 0px",
        threshold: [0, 0], // [0.25, 0.75] if you want a 25% offset!,
        // picture: null,
      },
      first: true,
      picture: null,
    };
  },
  computed: {
    ...mapGetters("books", ["getAngle"]),
  },
  props: ["sections", "book", "page", "resizevent", "refresh"],
  watch: {
    resizevent() {
      this.drawData();
    },
    page() {
      this.picture = null;
      this.loadImage();
    },
    refresh() {
      this.drawData();
    },
  },
  methods: {
    onWaypoint({ going, direction }) {
      // going: in, out
      // direction: top, right, bottom, left
      // console.log(this.rect);
      if (going === this.$waypointMap.GOING_IN) {
        this.loadImage();
      }
    },
    loadPicture() {
      return new Promise(async (resolve, reject) => {
        this.picture = document.createElement("img");
        let vm = this;
        this.picture.onload = function() {
          vm.access = true;
          resolve();
        };
        this.picture.onerror = function(err) {
          vm.ready = true;
          vm.access = false;
          vm.first = false;

          reject();
        };
        if (config.offline) {
          vm.access = true;
          let pic = await PictureService.getImageData(this.book, this.page);

          let imagedata = null;
          if (pic.headers["content-type"] == "image/djvu") {
            let djvuPage = new djvu.Document(pic.data);
            imagedata = await djvuPage.pages[0].createPngObjectUrl();
          } else if (pic.headers["content-type"].startsWith("image/png")) {
            let page = pic.headers["page"];
            let pdfcn = await pdfImage(pic.data, Number(page));
            this.picture = pdfcn;
            resolve();
            return;
          } else {
            imagedata = pic.data;
          }

          let _canvas = new OffscreenCanvas(imagedata.width, imagedata.height);
          let ctx = _canvas.getContext("2d");
          ctx.putImageData(imagedata, 0, 0);
          this.picture = _canvas;
          resolve();
        } else
          this.picture.src = PictureService.getImageUrl(this.book, this.page);
      });
    },
    async loadImage() {
      await this.loadPicture();
      this.drawData();
    },
    async drawData() {
      let canvas = this.$el.querySelector("canvas");
      let ctx = canvas.getContext("2d");
      let img = this.picture;

      let angle = this.getAngle(this.book, this.page);

      if (angle !== undefined && angle != 0) {
        if (img.nodeName == "IMG") {
          // console.log(angle);
          img = img.rotate(angle);
        } else {
          img = rotateCanvas(img, angle);
        }
      }
      let aspect_height =
        (canvas.offsetWidth * this.picture.height) / this.picture.width;
      // let aspect_height =
      //         (canvas.offsetWidth * this.picture.height) / this.picture.width;
      this.canvasHeight = this.picture.height;
      canvas.width = canvas.offsetWidth;
      canvas.height = aspect_height;

      // draw the image on the canvas
      // if (mainCanvas == null) {
      let pic = await this.$root.pica.resize(img, canvas);
      mainCanvas = cloneCanvas(pic);
      if (!this.$parent.mask) return;
      // if (this.sections.type == "s") return;
      let vm = this;
      ctx.save();
      ctx.globalAlpha = 0.5;
      //ctx.fillStyle = color;
      ctx.globalCompositeOperation = "destination-out";
      ctx.fillRect(0, 0, canvas.offsetWidth, aspect_height);
      ctx.restore();
      ctx.save();
      ctx.beginPath();
      if (this.sections.MareMakom == null) {
        this.sections.MefoToMarks.forEach((element) => {
          if (element.Marks.book != vm.book || element.Marks.pageNum != vm.page)
            return;

          // ctx.save();
          // ctx.fillStyle = "red";
          // ctx.globalCompositeOperation = "lighter";
          ctx.clearRect(
            element.Marks.x * canvas.offsetWidth,
            element.Marks.y * aspect_height,
            element.Marks.w * canvas.offsetWidth,
            element.Marks.h * aspect_height
          );
          // ctx.lineWidth = 1;
          // ctx.strokeStyle = color;

          ctx.rect(
            element.Marks.x * canvas.offsetWidth,
            element.Marks.y * aspect_height,
            element.Marks.w * canvas.offsetWidth,
            element.Marks.h * aspect_height
          );
          // ctx.fillRect(
          //   element.Marks.x * canvas.offsetWidth,
          //   element.Marks.y * aspect_height,
          //   element.Marks.w * canvas.offsetWidth,
          //   element.Marks.h * aspect_height
          // );
        });
      }
      ctx.clip();
      // this.$root.pica.resize(img, canvas);
      ctx.drawImage(mainCanvas, 0, 0, canvas.offsetWidth, aspect_height);
      // ctx.restore();
      if (this.first) {
        this.first = false;
        this.$emit("donedraw", {
          w: canvas.offsetWidth,
          h: aspect_height,
        });
      }
      //   }
    },
  },
  created() {},
};
</script>

<style>
.content {
  background-color: white;
  height: -webkit-fill-available;
  /* overflow: hidden; */
}
.bookImage {
  width: 100%;
  /* height: 100%; */
}
</style>
