<template>
  <div class="closeBtn" @click="sendCloseEvent()">
    <font-awesome-icon icon="times" class="close-txt" />
  </div>
</template>
<script>
export default {
  methods: {
    sendCloseEvent() {
      this.$emit("click");
    }
  }
};
</script>

<style>
.closeBtn {
  background-color: #f5f5f5;
  width: 50px;
  flex-grow: 0;
  flex-basis: 50px;
  justify-content: center;
  cursor: pointer;
}
</style>
