<template>
  <div class="popupsContainer">
    <PopupContiner v-for="popup in popups" :key="popup._id" :popup="popup" />
  </div>
</template>

<script>
import PopupContiner from "./PopupContainer.vue";
export default {
  data() {
    return {
      zindex: 1
    };
  },
  components: {
    PopupContiner
  },
  computed: {
    popups() {
      return this.$store.state.tabs.popups;
    }
  }
};
</script>

<style>
.popupsContainer {
  position: relative;
  width: 100%;
  text-align: center;
  z-index: 10;
  border-width: 0px;
}
</style>
