import store from "@/store/store";
export default {
  store,
  install(Vue, options) {
    Image.prototype.rotate = function(
      angle,
      imgWidth = this.width,
      imgHeight = this.height
    ) {
      if (angle > 180) angle = angle - 360;
      function diffAngleSize(angle, width, height) {
        angle = Math.abs(angle);
        if (angle === 180) return { w: width, h: height };
        angle = (angle * Math.PI) / 180;
        var leftAngle = (90 * Math.PI) / 180 - angle;
        var long = Math.max(width, height),
          short = Math.min(width, height);
        var rightW = Math.cos(angle) * short,
          leftW = Math.cos(leftAngle) * long;
        var rightH = Math.sin(angle) * short,
          leftH = Math.sin(leftAngle) * long;
        return { w: rightW + leftW, h: rightH + leftH };
      }

      let width = diffAngleSize(angle, imgWidth, imgHeight).w;
      let height = diffAngleSize(angle, imgWidth, imgHeight).h;

      const canvas = new OffscreenCanvas(width, height);
      const ctx = canvas.getContext("2d");

      ctx.save();
      ctx.translate(canvas.width / 2, canvas.height / 2);
      ctx.rotate((angle * Math.PI) / 180);
      ctx.drawImage(this, -imgWidth / 2, -imgHeight / 2);
      ctx.restore();
      return canvas;
    };
  }
};
