<template>
  <div v-if="show" class="print-form full-center" @click="show = false">
    <button class="print-btn" @click.stop="getCanvasContextsToOne" v-show="!full"> </button>

    <div class="inner" ref="content" id="printarea">
      <div v-if="mode == 's'" style="width: 100%;height: 100%;">
        <div v-for="section in sections" :key="section.id">
          <h1 class="center"> </h1>
          <hr />

          <SectionContent :meforesh="section" />
        </div>
      </div>
      <div v-else class="book-c">
        <div v-for="section in sections" :key="section.id">
          <!-- <h1 class="center"> </h1> -->
          <hr />
          <BookContent
            :book="section.book"
            :page="section.MefoToMarks[0].Marks.pageNum"
            :sections="section"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import BookContent from "@/components/MainPage/SectionBookViewer/Viewer.vue";
function createElementFromHTML(htmlString) {
  var div = document.createElement("div");
  div.innerHTML = htmlString.trim();

  // Change this to div.childNodes to support multiple top-level nodes
  return div.firstChild;
}
let GlobalcanvasToPrint = [];
export default {
  components: { BookContent },
  data() {
    return {
      show: false,
      full: false,
      counter: 0
    };
  },
  //   components:{BookName},
  methods: {
    getCanvasContextsToOne() {
      let canvasToPrint = [];
      let cnvs = this.$el.querySelectorAll("canvas");

      for (let cnv of cnvs) {
        cnv.style = "max-width:80%";
        canvasToPrint.push(cnv);
      }
      GlobalcanvasToPrint.push(canvasToPrint);
      this.print();
    },
    print() {
      var mywindow = window.open("", "PRINT", "height=400,width=600");
      // var mywindow = window.open("", "", "height=400,width=600");
      mywindow.document.write(
        "<html dir='rtl'><head><title>" + document.title + "</title>"
      );
      mywindow.document.write("</head><body><center>");
      let title = "<h1 style='text-align:center'>" + document.title + "</h1>";

      let book = '<h1 style="text-align:center"> </h1><hr>';

      mywindow.document.body.appendChild(createElementFromHTML(title));

      let _div = document.createElement("div");
      _div.style = `display: flex;
    width: 100%;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;`;
      GlobalcanvasToPrint.forEach(elem => {
        _div.appendChild(
          createElementFromHTML("<h1 style='width:100%'> <hr /></h1>")
        );
        elem.forEach(i => {
          _div.appendChild(i);
        });
      });

      // mywindow.document.body.appendChild(div);
      mywindow.document.body.appendChild(_div);
      //mywindow.document.write("</div>");
      mywindow.document.write("</body></html>");

      mywindow.document.close(); // necessary for IE >= 10
      mywindow.focus(); // necessary for IE >= 10*/

      mywindow.print();
      // mywindow.close();
      this.show = false;
      //   this.$nextTick(() => {
      //     // Scroll Down
      //window.print();
      //     this.$nextTick(() => {
      //       // Scroll Down
      //       this.full = false;
      //     });
      //   });

      //   //   this.full = false;
    }
  },
  computed: {
    ...mapState("Prints", ["printId", "sections", "mode"])
  },
  watch: {
    printId() {
      this.show = true;
      if (this.$refs.content == undefined) return;
      this.$refs.content.scrollTo({
        top: 1000,
        behavior: "smooth"
      });
    }
  },
  created() {
    window.onafterprint = function() {
      this.full = false;
    };
  }
};
</script>

<style lang="scss" scoped>
.center {
  text-align: center;
  //   width: 100%;
}
.book-c {
  width: 595px;
  height: 842px;
}
.inner {
  //   height: 80%;
  display: flex;
  //   width: 100%;
  position: relative;
  overflow: scroll;
  background-color: white;
  //   justify-content: center;
  //   align-items: center;
}
.print-btn {
  position: sticky;
  z-index: 19999;
  top: 0px;
  left: 0px;
}
.print-form {
  position: fixed;
  z-index: 9918;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  transition: opacity 0.3s ease;
}

@media screen {
  #screenarea {
    display: flex;
  }
  #printarea {
    display: flex;
  }
}
@media print {
  #screenarea {
    display: none;
  }
  #printarea {
    page-break-after: always;
    display: flex;
    justify-content: center;
    width: 100%;
  }
}
</style>
