const CardModule = {
  state() {
    return {
      viewMode: true,
      selected: []
    };
  },
  mutations: {
    TOGGLE_VIEWMODE(state) {
      state.viewMode = !state.viewMode;
    },
    ADD_ALL_SECTIONS_TO_SELECTED(state, { lib }) {
      for (var i = 0; i < lib.secs.length; i++) {
        state.selected.push({
          secId: lib.secs[i].section.secId,
          isSelected: false
        });
      }
    },
    ADD_SELECTED(state, secId) {
      state.selected.push({
        secId
      });
    },
    REMOVE_SELECTED(state, { secId }) {
      var index = state.selected.findIndex(sec => sec.secId === secId);
      // Vue.delete(state.selected, index);
      state.selected.splice(index, 1);
    },
    SET_VIEWMODE(state, getters, view) {
      state.viewMode = view;
    }
  },
  actions: {
    toggleViewMode({ commit }) {
      commit("TOGGLE_VIEWMODE");
    },
    setViewMode({ commit }, view) {
      commit("SET_VIEWMODE", view);
    },
    addSelected({ commit, state }, secId) {
      for (var i = 0; i < state.selected.length; i++) {
        if (state.selected[i].secId === secId) return;
      }
      commit("ADD_SELECTED", secId);
    },
    removeSelected({ commit }, secId) {
      commit("REMOVE_SELECTED", secId);
    }
  },
  getters: {
    getCurrentViewMode(state) {
      return state.viewMode;
    }
  },
  namespaced: true
};

export default CardModule;
