<template>
  <div class="btn" @click="sendUp()">
    <p :class="['content', cls]">
      <slot></slot>
    </p>
  </div>
</template>

<script>
export default {
  props: ["cls"],
  methods: {
    sendUp() {
      this.$emit("click");
    },
  },
};
</script>

