.btn {
  background: #6d5940 0% 0% no-repeat padding-box;
  border-radius: 12px;
  padding: 3px 9px;
}
.btn:hover {
  cursor: pointer;
  background: #614f39 0% 0% no-repeat padding-box;
}
.inhr {
  color: inherit;
}
.content {
  background: inherit;
  text-align: center;
  font-family: "heebo";
  font-size: 12px;
  line-height: 22px;
  letter-spacing: 0px;
  color: #ffffff;
}
</style>
