<template>
  <div
    class="card_button"
    style="height:31px"
    v-tooltip="' '"
    @click="printForm"
  >
    <!-- <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 15 14">
            <use xlink:href="~@/assets/svgs/icons-pack.svg#print_button"></use>
    </svg>-->
    <span class="mef-font printicon"
      ><HoverGray h="25" w="25" t="2" l="0">v</HoverGray></span
    >
    <!-- <font-awesome-icon icon="print" @click="print" /> -->
  </div>
</template>

<script>
export default {
  props: ["data", "meforesh"],
  methods: {
    printForm() {
      this.$store.dispatch("Prints/addToPrint", this.meforesh);
    },
    print() {
      let div = this.data; //.getElementsByTagName("canvas");

      var mywindow = window.open("", "PRINT", "height=400,width=600");
      // var mywindow = window.open("", "", "height=400,width=600");
      mywindow.document.write(
        "<html><head><title>" + document.title + "</title>"
      );
      mywindow.document.write("</head><body >");
      mywindow.document.write(
        "<h1 style='text-align:center'>" + document.title + "</h1>"
      );
      // div.firstElementChild.remove();
      // div.lastElementChild.remove();

      // div = div.lastElementChild.remove();
      mywindow.document.body.appendChild(div);
      mywindow.document.write("</body></html>");

      mywindow.document.close(); // necessary for IE >= 10
      mywindow.focus(); // necessary for IE >= 10*/

      mywindow.print();
      mywindow.close();

      return true;
    }
  }
};
</script>

<style scoped>
.printicon {
  font-size: 60px;
  line-height: 31px;
  width: 26px;
  height: 31px;
  display: inline-block;
}
</style>
