        <div class="modal-wrapper">
          <div class="modal-container" style v-click-outside="close">
            <div class="modal-header" style="position: relative;">
              <slot name="header">
                <!-- <span @click="hideme=true" class="mef-font close-icon">
                  <HoverGray h="25" w="25" t="3" l="0">q</HoverGray>
                </span>-->
              </slot>
            </div>

            <div class="modal-body" style>
              <slot name="body">default body</slot>
            </div>

            <!-- <div class="modal-footer">
              <slot name="footer">
                <button class="modal-default-button" @click="$emit('close')">
                  
                </button>
              </slot>
            </div>-->
          </div>
        </div>
      </div>
