<template>
  <div class="btn-page-tools">
    <v-popover offset="3" placement="right">
      <!-- <font-awesome-icon icon="search-plus" v-tooltip="' '" /> -->
      <span class="mef-font mef-font-default zoomin" v-tooltip="' '">e</span>

      <template slot="popover">
        <vue-slider @change="change" />
      </template>
    </v-popover>
  </div>
</template>

<script>
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/default.css";
import TabState from "@/mixing/importTabState";

export default {
  components: { VueSlider },
  mixins: [TabState],
  methods: {
    change(val) {
      this.tab.zoom = val;
    }
  }
};
</script>

<style scoped>
.popover {
  border-style: solid;
  border-width: 1px;
  border-color: #dfcfcf;
  width: 150px;
}
.zoomin {
  width: 28px;
  height: 31px;
  line-height: 31px;
  margin-top: 14px;
}
</style>
