import store from "@/store/store";
const ManagePageRouter = {
  beforeRouteUpdate(to, from, next) {
    let page = to.params.page;
    let book = to.params.bookid;
    store.getters.getCurrentTab.page = page;
    store.getters.getCurrentTab.book = book;

    if (to.params.sec != "undefined") {
      let secId = to.params.sec;
      store.getters.getCurrentTab.selectedSection = secId;
    }
    next();
  },

  beforeRouteEnter(to, from, next) {
    store.state.welcomeScreen = false;
    let page = to.params.page;
    let book = to.params.bookid;

    store.getters.getCurrentTab.page = page;
    store.getters.getCurrentTab.book = book;

    if (to.params.sec != "undefined") {
      let secId = to.params.sec;
      store.getters.getCurrentTab.selectedSection = secId;
    }
    next();
  }
};

export default ManagePageRouter;
