<template>
  <div class="item">
    <span class="mef-font icon" @click="open">u</span>
    <span class="txt" @click="open">{{item.item.label}}</span>
    <span class="mef-font icon close" @click="close">q</span>
  </div>
</template>

<script>
export default {
  props: ["item"],
  methods: {
    close() {
      this.item.item.close();
      this.$store.dispatch("minibar/removeMiniBarItem", this.item.id);
    },
    open() {
      this.item.item.click();
      this.$store.dispatch("minibar/removeMiniBarItem", this.item.id);
    }
  }
};
</script>

<style lang="scss" scoped>
.item {
  box-sizing: border-box;
  margin: 2px 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  height: 30px;
  cursor: pointer;
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
}
.item:hover {
  border-color: white;
}
.icon {
  font-size: 60px;
  line-height: 10px;
}
.txt {
  font-size: 13px;
  margin-left: 4px;
  margin-right: 2px;
}
.close:hover {
  font-weight: bold;
}
</style>