<template>
  <div class="card_button" style="margin:0px;height:31px">
    <span
      class="mef-font star-icon"
      :class="{ active: active }"
      @click="removeFromFav"
      v-tooltip="'  '"
    >
      <HoverGray :color="false" h="25" w="25" t="2" l="1">G</HoverGray>
    </span>
  </div>
</template>

<script>
export default {
  props: ["meforesh"],
  data() {
    return {
      fav: 0
    };
  },
  computed: {
    active() {
      return this.fav == 1;
    }
  },
  created() {
    this.fav = this.meforesh.fav;
  },
  methods: {
    removeFromFav() {
      if (this.fav) {
        this.fav = 0;
        this.$store.dispatch(
          "tab_" + this.$store.getters.getCurrentTab.tabId + "/removeFav",
          this.meforesh.book
        );

        // this.$store.getters.getCurrentTab.sortAgain += 1;
      } else {
        this.fav = 1;
        this.$store.dispatch(
          "tab_" + this.$store.getters.getCurrentTab.tabId + "/addFav",
          this.meforesh.book
        );
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.star-icon {
  font-size: 60px;
  line-height: 31px;
  width: 26px;
  height: 31px;
  display: inline-block;
}
.active {
  color: orange;
}
</style>
