var pdfjsLib = require("pdfjs-dist");
// const pdfjsWorker = require("pdfjs-dist/build/pdf.worker.entry");

// pdfjsLib.GlobalWorkerOptions.workerSrc = pdfjsWorker;

function NodeCanvasFactory() {}
NodeCanvasFactory.prototype = {
  create: function NodeCanvasFactory_create(width, height) {
    // assert(width > 0 && height > 0, "Invalid canvas size");
    var canvas = new OffscreenCanvas(width, height);
    var context = canvas.getContext("2d");
    return {
      canvas: canvas,
      context: context,
    };
  },

  reset: function NodeCanvasFactory_reset(canvasAndContext, width, height) {
    // assert(canvasAndContext.canvas, "Canvas is not specified");
    // assert(width > 0 && height > 0, "Invalid canvas size");
    canvasAndContext.canvas.width = width;
    canvasAndContext.canvas.height = height;
  },

  destroy: function NodeCanvasFactory_destroy(canvasAndContext) {
    // assert(canvasAndContext.canvas, "Canvas is not specified");

    // Zeroing the width and height cause Firefox to release graphics
    // resources immediately, which can greatly reduce memory consumption.
    canvasAndContext.canvas.width = 0;
    canvasAndContext.canvas.height = 0;
    canvasAndContext.canvas = null;
    canvasAndContext.context = null;
  },
};

function getPdfPageAsImage(pdfRaw, pageNum) {
  return new Promise((resolve, reject) => {
    // Relative path of the PDF file.

    // Read the PDF file into a typed array so PDF.js can load it.
    var rawData = new Uint8Array(pdfRaw);

    // Load the PDF file.
    var loadingTask = pdfjsLib.getDocument(rawData);
    loadingTask.promise
      .then(function(pdfDocument) {
        // console.log("# PDF document loaded.");
        // console.log(pageNum);
        // Get the first page.
        pdfDocument.getPage(pageNum).then(function(page) {
          // Render the page on a Node canvas with 100% scale.

          var viewport = page.getViewport({ scale: 5.0 });
          var canvasFactory = new NodeCanvasFactory();

          var canvasAndContext = new OffscreenCanvas(
            viewport.width,
            viewport.height
          );

          var renderContext = {
            canvasContext: canvasAndContext.getContext("2d"),
            viewport: viewport,
            canvasFactory: canvasFactory,
          };

          //here we got the image buffer
          var renderTask = page.render(renderContext);
          renderTask.promise.then(function() {
            // Convert the canvas to an image buffer.

            // var image = canvasAndContext.canvas.toBuffer();
            resolve(canvasAndContext);
          });
        });
      })
      .catch(function(reason) {
        reject(reason);
      });
  });
}

export default getPdfPageAsImage;
