<template>
  <!-- <intersect @enter="goIn" @leave="goOut"> -->
  <canvas
    class="section_mef_part default-height"
    v-if="type != 's'"
    v-waypoint="{
      active: true,
      callback: onWaypoint,
      options: intersectionOptions,
    }"
  />

  <div class="section_mef_part" v-else>
    <h2>
       :
      {{ title }}
    </h2>
  </div>
  <!-- </intersect> -->
  <!-- <div class="loading-section"> </div> -->
</template>

<script>
import PictureService from "@/services/pictureService.js";
import { mapGetters } from "vuex";
import config from "@/config/config";
import axios from "@/services/_axiosInstance";

import djvu from "@/utils/djvu";
import rotateCanvas from "@/utils/canvasRotate";

import pdfImage from "@/utils/pdfreader";

export default {
  components: {},
  props: ["rect", "type", "title", "meforesh", "refresh"],
  data() {
    return {
      intersectionOptions: {
        root: null,
        // rootMargin: "440px 0px 50px 0px",
        rootMargin: "0px 0px 0px 0px",
        threshold: [0, 0], // [0.25, 0.75] if you want a 25% offset!,
        // picture: null,
      },
      viewonscreen: false,
      picture: null,
      done: false,
    };
  },
  created() {
    // console.log(this.$parent.inx);
    // if (this.$parent.inx < 8) this.loadImg();
  },
  computed: {
    ...mapGetters("books", ["getAngle"]),
    loadings() {
      return this.$parent.loadings;
    },
    loads() {
      return this.$parent.loads;
    },
    parentView() {
      return this.$parent.viewonscreen;
    },
    forceload() {
      return this.$parent.forceLoad;
    },
  },
  watch: {
    forceload: function() {
      this.loadImg();
    },
    loads: function() {
      if (this.loads.includes(this.rect.pageNum)) {
        // console.log(this.rect.book);
        // this.picture = this.$parent.picture[this.rect.pageNum];
        // this.drawData();
      }
    },
    refresh: function() {
      // console.log(this.picture);
      // this.picture = this.$parent.picture[this.rect.pageNum];
      // this.drawData();
    },
    parentView() {
      // this.loadImg();
    },
  },
  methods: {
    loadImg() {
      if (this.done) return;
      if (config.offline) {
        PictureService.getImageData(this.meforesh.book, this.rect.pageNum).then(
          async (pic) => {
            let imagedata = null;
            let _canvas = null;
            if (pic.headers["content-type"] == "image/djvu") {
              let djvuPage = new djvu.Document(pic.data);

              imagedata = await djvuPage.pages[0].createPngObjectUrl();
              _canvas = new OffscreenCanvas(imagedata.width, imagedata.height);
              let ctx = _canvas.getContext("2d");

              ctx.putImageData(imagedata, 0, 0);
              this.drawData(_canvas);

              this.$parent.loads.push(this.rect.pageNum);
            } else if (pic.headers["content-type"].startsWith("image/png")) {
              console.log("pdf here");
              // let blob = new Blob([pic.data], { type: "image/png" });
              // let url = URL.createObjectURL(blob);
              // _canvas = new Image();
              // _canvas.onload = () => {
              //   this.drawData(_canvas);
              //   this.$parent.loads.push(this.rect.pageNum);
              // };
              // _canvas.src = url;
              // _canvas.onload(() => {
              //   window.URL.revokeObjectURL(url);
              // });
            } else if (pic.headers["content-type"] == "image/null") {
              this.$parent.versionerror = true;
            }
          }
        );
      } else {
        PictureService.getImage(
          this.meforesh.book,
          this.rect.pageNum,
          this.rect.angle
        ).then((data) => {
          this.drawData(data);
        });
      }
    },
    onWaypoint({ going, direction }) {
      if (this.done) return;
      // if (!this.$parent.viewonscreen) return;
      if (going === this.$waypointMap.GOING_IN) {
        // let top = getOffsetTop(this.$parent.$el);

        // if (this.$parent.loadings.includes(this.rect.pageNum)) {
        //   this.viewonscreen = true;
        // } else {
        this.viewonscreen = true;
        // this.$parent.loadings.push(this.rect.pageNum);
        //check if this is offline
        if (config.offline) {
          PictureService.getImageData(
            this.meforesh.book,
            this.rect.pageNum
          ).then(async (pic) => {
            // console.log("GOT IT");
            let imagedata = null;
            let _canvas = null;
            if (pic.headers["content-type"] == "image/djvu") {
              let djvuPage = new djvu.Document(pic.data);

              imagedata = await djvuPage.pages[0].createPngObjectUrl();
              _canvas = new OffscreenCanvas(imagedata.width, imagedata.height);
              let ctx = _canvas.getContext("2d");

              // this.$parent.picture[this.rect.pageNum] = _canvas;
              // this.picture = _canvas;

              ctx.putImageData(imagedata, 0, 0);
              this.drawData(_canvas);

              this.$parent.loads.push(this.rect.pageNum);
            } else if (pic.headers["content-type"].startsWith("image/png")) {
              let page = pic.headers["page"];
              let pdfcn = await pdfImage(pic.data, Number(page));
              this.drawData(pdfcn);
              // let blob = new Blob([pic.data], { type: "image/png" });
              // let url = URL.createObjectURL(blob);
              // _canvas = new Image();
              // _canvas.onload = () => {
              //   // this.$parent.picture[this.rect.pageNum] = _canvas;
              //   // this.picture = _canvas;
              //   this.drawData(_canvas);
              //   this.$parent.loads.push(this.rect.pageNum);
              // };
              // _canvas.src = url;
              // _canvas.onload(() => {
              //   window.URL.revokeObjectURL(url);
              // });
            } else if (pic.headers["content-type"] == "image/null") {
              this.$parent.versionerror = true;
            }
          });
        } else {
          PictureService.getImage(
            this.meforesh.book,
            this.rect.pageNum,
            this.rect.angle
          ).then((data) => {
            //let page = this.rect.page;
            //this.$parent.picture[this.rect.pageNum] = data;
            // this.picture = data;
            //this.$parent.loads.push(this.rect.pageNum);
            this.drawData(data);
          });
        }
        // }
        //this is for caching prefomance but actually is buggy sometime
        // else if (!this.$parent.picture[this.rect.page]) {
        //   PictureService.getImage(
        //     this.rect.book,
        //     this.rect.pageNum,
        //     this.rect.angle
        //   ).then(data => {
        //     let page = this.rect.page;
        //     this.$parent.picture[page] = data;
        //     this.drawData();
        //   });
        // } else {
        //   this.drawData();
        // }
      } else if (going === this.$waypointMap.GOING_OUT) {
        // this.$parent.picture[this.rect.pageNum] = undefined;
        this.viewonscreen = false;
      }
      // if (direction === this.$waypointMap.DIRECTION_TOP) {
      // }
    },
    drawData(tmpPic) {
      let imgsection = this.$el;

      imgsection.classList.remove("default-height");

      let mark = this.rect;
      // // let mark = this.mymeforesh;

      let image = tmpPic; //this.picture;
      if (image == undefined) return;
      let angle = this.getAngle(mark.book, mark.pageNum);

      let img = {
        // w: image.naturalWidth,
        // h: image.naturalHeight
        w: image.width,
        h: image.height,
      };

      let sec = this.meforesh;

      let m = {
        w: img.w * mark.w,
        h: img.h * mark.h,
        y: img.h * mark.y,
        x: img.w * mark.x,
      };

      let markColumn = Object.values(sec.pages[mark.pageNum].columns).filter(
        (c) => c.marks.filter((m) => m.id === mark.id).length > 0
      )[0];
      let col = {
        x: img.w * markColumn.x,
        w: img.w * markColumn.w,
      };
      let widestColWidth = img.w * sec.widestColWidth;
      let leftSpaceFromCol = Math.abs(col.x - m.x);
      let canvas = this.$el;
      canvas.height = m.h;
      canvas.width = mark.isKoteret ? m.w : widestColWidth;
      let location = mark.isKoteret
        ? 0
        : leftSpaceFromCol + (widestColWidth - col.w);
      if (angle > 180) angle = angle - 360;
      if (angle !== undefined && angle != 0) {
        if (image.nodeName == "IMG") {
          image = image.rotate(angle);
        } else {
          image = rotateCanvas(image, angle);
        }
      }
      ////////   --  
      // canvas.style.margin = "6px";

      let context = canvas.getContext("2d");
      context.drawImage(image, m.x, m.y, m.w, m.h, location, 0, m.w, m.h);

      //canvas to image
      // this.canvasToImage(canvas, imgsection);
      let borders = {};
      tmpPic = null;

      this.done = true;
      this.$emit("donedraw", borders);
    },
    canvasToImage(canvas, img) {
      canvas.toBlob((blob) => this.loadBlobToImg(blob, img));
    },
    loadBlobToImg(blob, pic) {
      let url = URL.createObjectURL(blob);
      pic.src = url;
      setTimeout(() => {
        URL.revokeObjectURL(url);
      }, 0);
    },
  },
};
</script>

<style>
.section_mef_part {
  width: 100%;
  text-align: center;
  display: flex;

  flex-direction: column;
}
.default-height {
  height: 347px;
  flex-basis: 347px;
}
.loading-section {
  position: absolute;
  top: 50%;

  font-size: 25px;
  width: 100%;
  text-align: center;
}
</style>
