<!-- this component updates user's lib-->
  <template>
  <div class="update-lib">
    <form @submit.prevent.self="updateLib">
      <p class="text-update">   </p>
      <div>
        <!-- the user input for a new lib label -->
        <input
          type="text"
          @click.stop
          v-model="label"
          class="input-box"
          :placeholder="[[lib.label]]"
        />
        <!-- click prevent update lib -->
        <input v-on="$listeners" class="submit-btn" type="submit" value="" />
      </div>
    </form>
    <!-- this button will not update lib and go back to the user libs list-->
    <button v-on="$listeners" class="exit-to-list">
      <img src="@/./assets/icons/exit-x-icon.png" class="exit-icon" alt />
    </button>
  </div>
</template>


<script>
import { mapMutations, mapState, mapGetters } from "vuex";

export default {
  props: {
    //getting lib from contextMenuLib component to find witch lib to update
    lib: Object
  },
  data() {
    return {
      //sets the label to get from the user a new label and insert it in here
      label: ""
    };
  },
  methods: {
    //this methods called when user click the submit button to update
    updateLib() {
      let payload = {
        folderId: this.lib.folderId,
        label: this.label
      };
      //the actual update from the mutation UPDATE_LIB that in the module meforshimUserLibs
      this.$store.dispatch("meforshimUserLibs/updateLib", payload);
    }
  }
};
</script>
<!-- styling the update box-->
