  border: 1px solid #c6c6c6;
  background-color: white;
  border-radius: 14px;

  position: fixed;
  left: 577px;
  top: 60px;
}
.input-box {
  position: fixed;
  left: 660px;
  top: 60px;

  width: 248px;
  height: 24.5px;
  font-size: 12px;
  outline: none;
  text-align: right;

  margin: 0px;
  padding: 0px;
  border-style: solid #f5f5f5;
  border-width: 0px 0px 1px 0px;
}
.text-update {
  font-size: 14px;
