<template>
  <!-- <input class="BookCheckBox" type="checkbox" v-model="selected"> -->
  <div class="BookCheckBox">
    <BaseCheckBox :checked="selected" @change="changeVal" />
  </div>
</template>

<script>
export default {
  name: "BookCheckBox",
  props: ["meforesh"],

  computed: {
    selected() {
      let obj = this.$store.state.Prints.sections.find(
        i => i.id == this.meforesh.id
      );
      return obj != undefined;
    },
    all() {
      return this.$store.state.Prints.all;
    }
  },
  watch: {
    all(old, nval) {
      this.changeVal(nval);
    }
  },
  methods: {
    changeVal(mode) {
      if (mode) this.$store.dispatch("Prints/pushToPrint", this.meforesh);
      else this.$store.dispatch("Prints/delPrint", this.meforesh);
      // this.selected = mode;
    }
  },
  created() {}
};
</script>

<style>
.BookCheckBox {
  height: 31px;
  display: flex;
  margin-left: 10px;
  justify-content: center;
  align-items: center;
}
</style>
