import store from "@/store/store";
import * as newtab from "./_tab";

export const namespaced = true;

export const state = {
  tabs: [],
  popups: [],
  currentTabId: null,
  refreshMarkers: false,
  refreshPage: false,
  pageWidth: 0,
  pageHeight: 0,
  reloadAll: false,
  refreshSections: 0
};

export const mutations = {
  SET_CURRENT_TABID_TO(state, tabid) {
    console.log(tabid);
    state.currentTabId = tabid;
  },
  ADD_TAB(state, tab) {
    state.tabs.push(tab);
  },

  DELETE_TAB(state, payload) {
    // state.currentTabId = state.tabs[0];
    // console.log(state.currentTabId);
    state.tabs.splice(payload.tabIndex, 1);
    // console.log(state.tabs);
    store.unregisterModule(`tab_${payload.tabId}`);
  },
  SET_TOGGLE_MODE(state, tab) {
    tab.viewFullMode = tab.viewFullMode;
  },
  SET_TOGGLE_PER_SECTION(state, tab) {
    tab.viewFullMode = tab.viewFullMode;
  },
  SET_DIMS(state, data) {
    if (state.pageWidth != data.width) state.pageWidth = data.width;
    if (state.pageHeight != data.height) state.pageHeight = data.height;
    state.refreshPage = !state.refreshPage;
  }
};
export const actions = {
  setDims({ commit }, data) {
    commit("SET_DIMS", data);
  },
  addTabId({ commit, state }, data) {
    state.tabs.push(data);
  },
  addTab({ commit, state }, pdata, goto = true) {
    //const _tab=require('./_tab')
    let tmpId = Math.random() * 1000000;
    let data = JSON.stringify(pdata);
    data = JSON.parse(data);
    store.registerModule(`tab_${tmpId}`, newtab);

    store.state[`tab_${tmpId}`].book = data.book;
    store.state[`tab_${tmpId}`].page = data.page;
    if (typeof data.page.selectedSection !== "undefined")
      store.state[`tab_${tmpId}`].selectedSection = data.selectedSection;
    store.state[`tab_${tmpId}`].tabId = tmpId;

    commit("ADD_TAB", tmpId);

    if (goto) state.currentTabId = tmpId;
    return tmpId;
  },
  delTab({ state, commit }, { tabId, newTab }) {
    let tabIndex = null;
    state.tabs.forEach((e, i) => {
      if (e == tabId) {
        tabIndex = i;
        return;
      }
    });
    // console.log(newTab);
    // if (newTab != 0) commit("SET_CURRENT_TABID_TO", newTab);
    commit("DELETE_TAB", { tabIndex, tabId });
  },
  setSelectedTab({ state }, tabId) {
    state.currentTabId = tabId;

    //router.push({name:'main-page',params:{bookid:tab.book,page:tab.page}})
  },
  setTabShasSecId({ state }, data) {
    state.tabs.forEach((e, i) => {
      if (e.tabId === data.tabId && e.selectedSection != data.selectedSection)
        state.tabs[i].selectedSection = data.selectedSection;
    });
  },
  refreshMarkers({ state }) {
    state.refreshMarkers = !state.refreshMarkers;
  },
  reloadPage({ state }) {
    state.refreshPage = !state.refreshPage;
  },
  setBookAndPage({ getters }, data) {
    bookid = data.bookid;
    page = data.page;
    getters.getCurrentTab.bookid = bookid;
    getters.getCurrentTab.page = page;
  },

  toggleViewMode({ commit }) {
    commit("SET_TOGGLE_MODE", getters.getCurrentTab);
  },
  toggleViewModePerSection({ commit, getters }, secId) {
    getters.getCurrentTab.sections.forEach((e, i) => {
      if (e.secId == secId) {
        e.viewMode = false;
      }
    });
  },

  setCatgFilter({ getters }, CatgId) {
    if (getters.getCurrentTab.type_selected.includes(CatgId)) {
      if (CatgId == 0) {
        getters.getCurrentTab.type_selected = [];
      } else {
        getters.getCurrentTab.type_selected = getters.getCurrentTab.type_selected.filter(
          i => i != CatgId && i != 0
        );
      }
    } else {
      if (CatgId == 0) {
        for (let y = 0; y <= 5; y++) {
          if (!getters.getCurrentTab.type_selected.includes(y)) {
            getters.getCurrentTab.type_selected.push(y);
          }
        }
      } else {
        getters.getCurrentTab.type_selected.push(CatgId);

        if (getters.getCurrentTab.type_selected.length == 5) {
          if (!getters.getCurrentTab.type_selected.includes(0))
            getters.getCurrentTab.type_selected.push(0);
        }
      }
    }
    //console.log(getters.getCurrentTab.type_selected);
  }
};
export const getters = {
  getTabs: state => state.tabs,
  getTab: state => tabId => {
    return state.tabs.find(e => e.tabId == tabId);
  },
  getCurrentTab(state) {
    return store.state[`tab_${state.currentTabId}`];
  }
};
