<template>
  <div>
    <div class="flex full-center">
      <div @click.stop="toggleAll">
        <BaseCheckBox
          :checked="$store.state.Prints.sections.length > 0"
          :text="!$store.state.Prints.all?'0':'m'"
        />
      </div>

      <span
        class="mef-font mef-font-default"
        style="line-height: 20px;height: 20px;"
        @click.stop="open=!open"
      >p</span>
    </div>
    <div class="menu" v-if="open">
      <p @click.stop="$store.state.Prints.printId+=1"></p>
      <p> </p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      open: false
    };
  },
  methods: {
    toggleAll() {
      this.$store.state.Prints.all = !this.$store.state.Prints.all;
    }
  }
};
</script>

<style lang="scss" scoped>
.menu {
  position: absolute;
  top: 25px;
  text-align: right;
  padding-right: 7px;
  font-size: 13px;
  line-height: 25px;
  width: 100px;
  z-index: 2;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  margin-top: 1px;
  max-height: 300px;
  overflow-y: hidden;
}
</style>