<template>
  <div
    class="pin-to-top"
    @click="isActive = !isActive"
    :class="{ disable: !isActive }"
  >
    <span v-if="stype == 'p'" class="pirush"></span>
    <span v-else-if="stype == 'd'" class="drush"></span>
    <span v-else-if="stype == 's'" class="shiur"></span>
    <span v-else-if="stype == 'h'" class="hagaa"></span>
    <span v-else-if="stype == 'z'" class="zion"></span>
    <span v-else-if="stype == 'note'" class="note"></span>
  </div>
</template>

<script>
export default {
  props: ["stype"],
  data() {
    return {
      isActive: true
    };
  }
};
</script>

<style lang="scss" scoped>
.disable {
  opacity: 0.2;
}
.pirush {
  background-color: black;
  color: white;
}
.drush {
  background-color: peru;
  color: red;
}
.shiur {
  background-color: purple;
  color: orange;
}
.zion {
  background-color: black;
  color: chartreuse;
}
.hagaa {
  background-color: black;
  color: rgb(255, 255, 255);
}
.note {
  background-color: purple;
  color: peru;
}

.pin-to-top {
  position: absolute;
  left: 0px;
  top: 0px;
}
</style>
