
<!-- this component deletes user's lib-->
<template>
  <div class="delete-lib" v-on="$listeners">
    <p class="text-update">   {{lib.label}}</p>
    <!-- click prevent delete lib-->
    <div class="delete-btn" @click.stop="deleteLib(lib,lib.folderId)"></div>
    <!-- this button will not delete lib and go back to the user libs list-->
    <button class="exit-to-list"></button>
  </div>
</template>


<script>
import { mapMutations, mapState, mapGetters } from "vuex";

export default {
  props: {
    //getting lib from contextMenuLib component to find witch lib to delete
    lib: Object
  },
  methods: {
    //this methods called when user click the delete
    deleteLib(lib, folderId) {
      //the actual deletion from the action deleteLib that in the module meforshimUserLibs
      this.$store.dispatch("meforshimUserLibs/deleteLib", {
        lib,
        folderId
      });
    }
  },
  computed: {}
};
</script>
