<template>
  <div class="user-holder">
    <ModalUserData v-if="showUserData" @close="showUserData = false" />
    <ModalLogin v-if="showUserLogin" @close="showUserLogin = false" />
    <p class="btn-cart" alt v-tooltip="'  '" @click="goStore">
      <HoverGray
        :color="false"
        bgcolor="rgb(0,0,0,0.1)"
        fgcolor="white"
        h="30"
        w="30"
        t="3"
        l="3"
      >
        b</HoverGray
      >
    </p>
    <p
      class="btn-user mef-font"
      alt
      @click="getUserData"
      v-tooltip="'  '"
    >
      <HoverGray
        :color="false"
        bgcolor="rgb(0,0,0,0.1)"
        fgcolor="white"
        h="30"
        w="30"
        t="3"
        l="3"
      >
        a</HoverGray
      >
    </p>

    <span
      @click="getUserData"
      v-if="user.name != null"
      style="padding-left:5px"
      >{{ user.name }}</span
    >
  </div>
</template>

<script>
import ModalUserData from "./ModalUserData";
import ModalLogin from "./ModalLogin";
import { mapState } from "vuex";
export default {
  components: { ModalUserData, ModalLogin },
  data() {
    return {
      showUserData: false,
      showUserLogin: false,
    };
  },
  methods: {
    getUserData() {
      if (this.user.name == null) this.showUserLogin = true;
      else this.showUserData = true;
    },
    goStore() {
      window.open("https://www.otzar.org/wotzar/pcionlinestore.aspx");
    },
  },
  computed: {
    ...mapState({ user: (state) => state.user }),
  },
};
</script>

<style lang="scss" scoped>
.btn-cart {
  font-size: 36px;
  font-family: "user";
}
.btn-user {
  font-size: 36px;
  font-family: "user";
}
.user-holder {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding: 0px 4px; */
  color: white;
}
</style>
