<template>
  <div>
    <!-- -->
    <div
      v-if="!done && meforesh.type != 's' && !versionerror"
      style="position: absolute;width:100%;height:100%;display:flex;justify-content:center;align-items: center;
    "
    >
      <img
        @click="forceLoad = true"
        v-if="!waitTooLong"
        src="~@/assets/images/mefbook.gif"
        v-waypoint="{
          active: true,
          callback: onWaypointLoader
        }"
      />
      <button
        v-else
        @click="
          forceLoad = true;
          startTimer();
        "
        class="switcher"
      >   -      </button>
    </div>

    <div v-if="versionerror">
      <h3 style="text-align:center;color:#98774f">       </h3>
    </div>
    <div v-if="meforesh.type == 's'">
      <Section
        v-show="!versionerror"
        :title="meforesh.SectionToShiur.title"
        :refresh="refresh"
        :type="meforesh.type"
        :meforesh="meforesh"
        @donedraw="donedraw({})"
      />
    </div>
    <div
      v-else
      v-waypoint="{
        active: true,
        callback: onWaypoint,
        options: intersectionOptions
      }"
    >
      <Section
        v-show="!versionerror"
        :refresh="refresh"
        @donedraw="donedraw"
        v-for="section in filterdMefoToMarks"
        :key="section.id"
        :rect="section.Marks"
        :type="meforesh.type"
        :meforesh="mymeforesh"
      />
    </div>
  </div>
</template>

<script>
import Section from "./_SectionContentPart";

const getOffsetTop = element => {
  // console.dir(element.scrollHeight);
  let offsetTop = 0;
  while (element) {
    offsetTop += element.offsetTop;
    element = element.offsetParent;
  }
  return offsetTop;
};

const getParentScroll = e => {
  while (e) {
    // console.log(e.style["overflow-y"]);
    if (e.style["overflow-y"] == "auto") return e;
    e = e.parentElement;
  }
  return null;
};

export default {
  name: "SectionContent",
  props: ["meforesh", "refresh", "inx"],
  components: {
    Section
  },
  computed: {
    filterdMefoToMarks() {
      let data = this.meforesh.MefoToMarks.filter(
        i => i != null && i.Marks != null
      );

      return data;
    }
  },

  data() {
    return {
      waitTooLong: false,
      versionerror: false,
      picture: [],
      mymeforesh: null,
      loadings: [],
      loads: [],
      timer: null,
      forceLoad: false,
      borders: { left: 9999, right: 9999 },
      counter: 0,
      done: true,
      removeSides: false,
      viewonscreen: false,
      intersectionOptions: {
        root: null,
        rootMargin: "10px 0px 0px 0px",
        threshold: [0, 0] // [0.25, 0.75] if you want a 25% offset!,
        // picture: null,
      }
    };
  },
  beforeDestroy() {
    clearTimeout(this.timer);
    this.picture = [];
  },
  beforeMount() {
    this.done = false;
  },
  created() {
    var sec = this.meforesh;

    // this.id = Math.random() * 10000000;
    sec.MefoToMarks.sort((a, b) => {
      if (!a.Marks || !b.Marks) return 0;
      return a.Marks.order - b.Marks.order;
    });
    sec.MefoToMarks.sort((a, b) => {
      if (!a.Marks || !b.Marks) return 0;
      return a.Marks.pageNum - b.Marks.pageNum;
    });

    sec.fillObj = (obj, curMark, clIndx) => {
      var p = obj.pages || {};
      p[curMark.pageNum] = p[curMark.pageNum] || {};
      p[curMark.pageNum].columns = p[curMark.pageNum].columns || {};
      p[curMark.pageNum].columns[clIndx] =
        p[curMark.pageNum].columns[clIndx] || {};
      p[curMark.pageNum].columns[clIndx]["marks"] =
        p[curMark.pageNum].columns[clIndx]["marks"] || [];
      p[curMark.pageNum].columns[clIndx]["marks"].push(curMark);
      return p;
    };
    function isNewColumn(prevMark, curMark) {
      var theSamePage = curMark.pageNum == prevMark.pageNum;
      var isCurLower = curMark.y - prevMark.y > 0;
      let isCurLefter = curMark.x + curMark.w <= prevMark.x;
      if (theSamePage && (!isCurLower || isCurLefter)) return true;
    }
    let columnIndx = 0;
    let marks = sec.MefoToMarks;
    sec.pages = sec.fillObj(sec, marks[0].Marks, columnIndx);
    for (let i = 1; i < marks.length; i++) {
      var prevMark = marks[i - 1].Marks,
        curMark = marks[i].Marks;
      if (curMark.pageNum !== prevMark.pageNum) columnIndx = 0;
      if (isNewColumn(prevMark, curMark)) columnIndx++;
      sec.pages = sec.fillObj(sec, curMark, columnIndx);
    }
    Object.values(sec.pages).forEach(p => {
      let pageMarks = Object.values(p.columns)
        .map(c => c.marks)
        .flat();
      let sum = pageMarks.reduce((sum, i) => sum + i.w, 0) / pageMarks.length;
      let toLargDiff =
        Math.min(...pageMarks.map(m => m.w)) <
        Math.max(...pageMarks.map(m => m.w)) / 2.5;
      if (toLargDiff) {
        let pm = pageMarks.filter(
          m => m.w > Math.min(...pageMarks.map(m => m.w))
        );
        sum = pm.reduce((sum, i) => sum + i.w, 0) / pm.length;
      }
      p.maxWidthAbleOfPage = sum >= 0.8 ? 1.1 : 0.8;
      Object.values(p.columns).forEach(col => {
        let noKoteretMarks = col.marks.filter(m => m.w <= p.maxWidthAbleOfPage);
        noKoteretMarks.forEach(m => (m.isKoteret = false));
        let koteretMarks = col.marks.filter(m => m.w > p.maxWidthAbleOfPage);
        koteretMarks.forEach(m => (m.isKoteret = true));
        col.x = Math.min(...noKoteretMarks.map(m => m.x));
        //         
        let mostRightOfCol = Math.max(...noKoteretMarks.map(m => m.w + m.x));
        col.w = mostRightOfCol - col.x;
        //     
        if (pageMarks.length === 1) {
          col.x = pageMarks[0].x;
          col.w = pageMarks[0].w;
        }
      });
      //  
      sec.widestColWidth = Math.max(
        ...Object.values(sec.pages)
          .map(p => Object.values(p.columns))
          .flat()
          .map(col => col.w)
      );
    });

    // this.sec = sec;
    this.mymeforesh = sec;
  },
  methods: {
    donedraw(borders) {
      // this.counter++;
      // if (this.counter == this.filterdMefoToMarks.length) {
      //   // console.log("done all");
      //   if (this.left != 9999 && this.right != 9999)
      //     this.removeSides = !this.removeSides;
      // }

      // if (this.borders.left > borders.left) this.borders.left = borders.left;
      // if (this.borders.right > borders.right)
      //   this.borders.right = borders.right;
      this.done = true;
      this.$emit("donedraw");
    },
    startTimer() {
      this.waitTooLong = false;
      this.timer = setTimeout(() => {
        this.waitTooLong = true;
      }, 1500);
    },
    onWaypointLoader({ going, direction }) {
      if (going === this.$waypointMap.GOING_IN) {
        this.startTimer();
      }
    },
    onWaypoint({ going, direction }) {
      // going: in, out
      // direction: top, right, bottom, left
      // console.log(this.rect);
      // let top = getOffsetTop(this.$el);
      // top += this.inx * 40;
      // let parentScroll = getParentScroll(this.$parent.$el);
      // let parentViewer = parentScroll.clientHeight;
      // let parentScrollTop = parentScroll.scrollTop - 50; //remove header and stuff
      // if (parentScrollTop + parentViewer > top) {
      //   console.log("showen " + top);
      //   this.viewonscreen = true;
      // }
      // console.log(parentViewer);
      // console.log(top + "-" + this.inx);
      // console.dir(parentScroll.scrollHeight + "-" + parentScroll.scrollTop);
      // console.log(this.$parent.$el);
      // console.log(top);
      // if (this.$topInViewPort(top)) {
      //   // console.log("yes");
      //   this.viewonscreen = true;
      // } else if (this.$topInViewPort(top - 1500)) this.viewonscreen = false;
      // } else if (this.$topInViewPort(top - 2000)) this.viewonscreen = false;
      // if (going === this.$waypointMap.GOING_IN) {
      //   // console.log(top);
      //   if (this.$topInViewPort(top + 100)) this.viewonscreen = true;
      //   // console.log(this.$parent);
      //   // console.log("hi");
      // } else if (going === this.$waypointMap.GOING_OUT) {
      //   //this.$parent.picture[this.rect.pageNum] = undefined;
      //   this.viewonscreen = false;
      // }
    }
  }
};
</script>

<style scoped>
.switcher {
  -webkit-box-flex: 0;
  -ms-flex-positive: 0;
  flex-grow: 0;
  -ms-flex-preferred-size: 120px;
  /* flex-basis: 120px; */
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  cursor: pointer;
  border-color: #98774f;
  background-color: white;
  color: #98774f;
  border-radius: 50px;
  padding: 0px 12px;
  height: 32px;
  border-width: 2px;
  border-style: solid;
  font-size: 14px;
  line-height: 32px;
}
</style>
