import userLibExceptions from "@/store/errorTypes/userLibExceptions.js";
import Vue from "vue";
import { uuid, currentDate } from "@/store/plugins/saveState";
export const namespaced = true;
export const meforshUserList = JSON.parse(
  localStorage.getItem("meforshUserList")
);
// load the libs from localstorage
let data = localStorage.getItem("meforshUserList");
let libs = [];
if (data) libs = JSON.parse(data).libs;
//loop through the items and change every update_date field to date object
libs.every(item => {
  item.updated_date = new Date(libs[0].updated_date);
  return item;
});

export const state = {
  meforshUserList: {
    libs
  },
  sortLibsBy: "label"
};
//mutations
export const mutations = {
  UPDATE_SORT_LIBS(state, { sortType }) {
    state.sortLibsBy = sortType;
  },

  CREATE_LIB(state, label) {
    state.meforshUserList.libs.push({
      label,
      secs: [],
      updated_date: new Date(),
      pinToTop: false,
      folderId: uuid()
    });
  },
  ADD_SECTION_TO_USER_LIB(state, { folderId, section }) {
    let index = state.meforshUserList.libs.findIndex(
      lib => lib.folderId === folderId
    );
    state.meforshUserList.libs[index].secs.push({
      section
    });
  },
  CHANGE_SELECTED_IN_SECTION(state, { folderId, secId }) {
    let indexLib = state.meforshUserList.libs.findIndex(
      lib => lib.folderId === folderId
    );
    var indexSec = state.meforshUserList.libs[indexLib].secs.findIndex(
      sec => sec.section.secId === secId
    );
    Vue.set(
      state.meforshUserList.libs[indexLib].secs[indexSec].section,
      "selected",
      !state.meforshUserList.libs[indexLib].secs[indexSec].section.selected
    );
  },
  SET_ALL_SELECTED_IN_LIB(state, { indexLib, i }) {
    Vue.set(
      state.meforshUserList.libs[indexLib].secs[i].section,
      "selected",
      false
    );
  },
  DELETE_LIB(state, { folderId }) {
    var index = state.meforshUserList.libs.findIndex(
      lib => lib.folderId === folderId
    );
    Vue.delete(state.meforshUserList.libs, index);
  },
  DELETE_USER_SECTION(state, { folderId, secId }) {
    var indexLib = state.meforshUserList.libs.findIndex(
      lib => lib.folderId === folderId
    );
    var indexSec = state.meforshUserList.libs[indexLib].secs.findIndex(
      sec => sec.section.secId === secId
    );
    Vue.delete(state.meforshUserList.libs[indexLib].secs, indexSec);
  },
  DELETE_USER_SECTIONS(state, { lib, ...sectionsSelected }) {
    for (var i = 0; i < sectionsSelected.length; i++) {
      var secIndex = lib.secs.section.findIndex(
        section => section.secId === sectionsSelected[i]
      );
      Vue.delete(lib.secs.section, secIndex);
    }
  },
  UPDATE_LIB(state, { folderId, label }) {
    let index = state.meforshUserList.libs.findIndex(
      e => e.folderId === folderId
    );
    state.meforshUserList.libs[index].label = label;
    state.meforshUserList.libs[index].updated_date = new Date();
  },
  PIN_TO_TOP_LIB(state, { folderId, pinToTop }) {
    let index = state.meforshUserList.libs.findIndex(
      e => e.folderId === folderId
    );
    state.meforshUserList.libs[index].pinToTop = !pinToTop;
  }
};
export const actions = {
  changeSelectedInSection({ commit }, { folderId, secId }) {
    commit("CHANGE_SELECTED_IN_SECTION", { folderId, secId });
  },
  setAllSelectedInLib({ commit }, folderId) {
    var indexLib = state.meforshUserList.libs.findIndex(
      lib => lib.folderId === folderId
    );
    for (var i = 0; i < state.meforshUserList.libs[indexLib].secs.length; i++) {
      commit("SET_ALL_SELECTED_IN_LIB", { indexLib, i });
    }
  },
  updateSortLibs({ commit }, sortType) {
    commit("UPDATE_SORT_LIBS", sortType);
  },
  createLib({ commit }, label) {
    for (var i = 0; i < state.meforshUserList.libs.length; i++) {
      if (state.meforshUserList.libs[i].label === label)
        return alert(userLibExceptions.USER_INPUT_EXISTS_LIB_LABEL);
    }
    commit("CREATE_LIB", label);
  },
  addSectionToUserLib({ commit }, { folderId, section }) {
    let index = state.meforshUserList.libs.findIndex(
      lib => lib.folderId === folderId
    );
    if (state.meforshUserList.libs[index].secs.length === 0) {
      Vue.set(section, "selected", false);
      return commit("ADD_SECTION_TO_USER_LIB", { folderId, section });
    } else {
      for (var i = 0; i < state.meforshUserList.libs[index].secs.length; i++) {
        if (
          state.meforshUserList.libs[index].secs[i].section.secId ===
          section.secId
        )
          return alert(userLibExceptions.USER_INPUT_EXISTS_SECTION);
      }
    }
    Vue.set(section, "selected", false);
    commit("ADD_SECTION_TO_USER_LIB", { folderId, section });
  },
  deleteUserSection({ commit }, { folderId, secId }) {
    commit("DELETE_USER_SECTION", { folderId, secId });
  },
  deleteLib({ commit }, folderId) {
    commit("DELETE_LIB", folderId);
  },
  updateLib({ commit }, { folderId, label }) {
    commit("UPDATE_LIB", { folderId, label });
  },

  pinToTopLib({ commit }, { folderId, pinToTop }) {
    commit("PIN_TO_TOP_LIB", { folderId, pinToTop });
  }
};
//getters
export const getters = {
  getLib(state) {
    return folderId => {
      for (const lib of state.meforshUserList.libs) {
        if (lib.folderId === folderId) {
          return lib;
        }
      }
    };
  },
  getLibs(state) {
    return state.meforshUserList.libs.sort((item, selected) => {
      if (!item.pinToTop && selected.pinToTop) return 1;
      if (item.pinToTop && !selected.pinToTop) return -1;
      switch (state.sortLibsBy) {
        case "label":
          return item.label.localeCompare(selected.label);
        case "updated_date":
          return item.updated_date > selected.updated_date ? -1 : 1; //item.updated_date.localeCompare(selected.updated_date);
      }
      return 0;
    });
  }
};
