<template>
  <div>
    <Modal @close="$emit('close')">
      <template v-slot:header>
        <h3>  </h3>
      </template>
      <template v-slot:body>
        <label> </label>
        <input class="txt-folder-name" v-model="txtFolder" type="text" />
        <button @click="createUserMefLib()">+</button>
        <br />
        <div>
          <p v-for="item in getLibs" :key="item.id" @click="addMe(item)">{{item.label}}</p>
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from "vuex";
export default {
  name: "SectionToUserMefLib",
  props: ["section"],
  computed: {
    ...mapGetters("meforshimUserLibs", ["getLibs"])
  },
  data() {
    return {
      txtFolder: ""
    };
  },
  methods: {
    ...mapActions("meforshimUserLibs", ["addSectionToUserLib", "createLib"]),
    addMe(item) {
      console.dir(item);
      console.dir(this.section);
      this.addSectionToUserLib({
        folderId: item.folderId,
        section: this.section
      });
    },
    createUserMefLib() {
      this.createLib(this.txtFolder);
    }
  }
};
</script>

