<template>
  <div class="page-tools">
    <FitToHeight @click="FitToWidth(false)" />
    <FitToWidth @click="FitToWidth" />
    <FullScreen />
    <ZoomIn :tabid="$store.state.tabs.currentTabId" />
    <!-- <Fav :tabid="$store.state.tabs.currentTabId" /> -->
    <!-- <Pin :tabid="$store.state.tabs.currentTabId" /> -->
  </div>
</template>

<script>
import FitToHeight from "./BtnFitToHeight";
import FitToWidth from "./BtnFitToWidth";
import FullScreen from "./BtnFullScreen";
import ZoomIn from "./BtnZoomIn";
import Fav from "./BtnFav";
import Pin from "./BtnPin";
import TabState from "@/mixing/importTabState";

export default {
  mixins: [TabState],
  components: {
    FitToHeight,
    FitToWidth,
    FullScreen,
    ZoomIn,
    Fav,
    Pin
  },
  methods: {
    FitToWidth(fit = true) {
      this.$store.dispatch(
        `tab_${this.$store.state.tabs.currentTabId}/fitToWidth`,
        fit
      );
      this.$store.state.tabs.refreshPage = !this.$store.state.tabs.refreshPage;
    }
  }
};
</script>

<style>
.page-tools {
  flex-basis: 36px;
  align-items: center;
  display: flex;
  position: sticky;
  left: 0px;
  flex-direction: column;
  border-right-color: #dedede;
  border-right-style: solid;
  border-right-width: 1px;
  background-color: #f5f5f5;
  padding-top: 8px;
  flex-shrink: 0;
  color: #737373;
}
.btn-page-tools {
  font-size: 22px;
  text-align: center;
  cursor: pointer;
}
</style>
