<template>
  <div class="PopupContainer" :style="{ 'z-index': zindex }" @click="addZindex" v-show="show">
    <vue-draggable-resizable
      ref="test"
      class="shadow-class"
      :w="sevntyPerc"
      :h="contentHeight"
      :min-width="150"
      :min-height="150"
      :disable-user-select="false"
      :x="InitLeft"
      class-name="border-solid"
      class-name-handle="my-handle-class"
      @resizestop="resizeEventOccured"
    >
      <PopupHeader
        class="popup-header"
        :id="id"
        :popup="popup"
        @show="showMe"
        @nextpage="goPage"
        @backpage="goPage(false)"
        @keydown.left.stop="goPage"
        tabindex="1"
        @keydown.right.stop="goPage(false)"
        :mask="mask"
        @mask="
          mask = !mask;
          resizeoccured += 1;
        "
        @togglewidth="toggleWidth"
      />
      <!--<BookViewer :sections="popup.sections" :bookname="popup.bookName" :bookauthor="popup.bookAuthor"/>-->
      <div
        tabindex="0"
        class="book-viewer"
        ref="book_viewer"
        v-show="viewBookMode"
        v-focus
        @keydown.left="goPage"
        @keydown.right="goPage(false)"
        @keydown.esc="closePopUp"
      >
        <!-- <BookViewer :sections="popup.sections" :bookname="popup.bookName" :bookauthor="popup.bookAuthor"/> -->
        <BookViewer
          :sections="popup"
          :resizevent="resizeoccured"
          :mask="mask"
          ref="bookContent"
          @donedraw="setCorrectHeightAndScroll"
          :book="book"
          :page="page"
        />
      </div>
      <div class="section-viewer" v-show="!viewBookMode">
        <SectionContent v-if="popup.MareMakom == null" :meforesh="popup" />
      </div>
    </vue-draggable-resizable>
  </div>
</template>

<script>
import BookViewer from "@/components/MainPage/SectionBookViewer/Viewer.vue";
import PopupHeader from "./PopupHeader.vue";
import store from "@/store/store";
import { mapState } from "vuex";
import SectionPopup from "@/store/modules/section-popup";
export default {
  props: ["popup"],
  data() {
    return {
      id: null,
      zindex: 1,
      resizeoccured: 0,
      first: true,
      book: 0,
      page: 0,
      mask: true,
      show: true
    };
  },
  components: {
    BookViewer,
    PopupHeader
  },
  methods: {
    closePopUp() {
      this.$store.state.tabs.popups = store.state.tabs.popups.filter(
        item => item._id != this.id
      );
    },
    showMe(show) {
      this.show = show;
    },
    goPage(next = true) {
      if (next) this.page += 1;
      else this.page -= 1;

      this.$el.getElementsByClassName("book-viewer")[0].scrollTop = 0;
    },
    goToCorrectPosInScroll(event) {
      let scrollTag = this.$refs.book_viewer;
      let imageHeight = event.h;
      let imageWidth = event.w;
      let firstMark = this.popup.MefoToMarks[0].Marks;
      let scrollToH = firstMark.y * imageHeight;
      console.log(firstMark.y * imageHeight);
      scrollTag.scrollTo(0, scrollToH);
    },
    setCorrectHeightAndScroll(event) {
      //goto correct position in scroll
      this.goToCorrectPosInScroll(event);
      let bookHeight = 0;
      // if (bookViewer) {

      bookHeight = this.$refs.bookContent.$el.firstElementChild
        .firstElementChild.scrollHeight;
      // } else {
      //   // console.log("ddd");
      //   // console.log(this.$el.children[0].lastElementChild);
      //   bookHeight = this.$el.children[0].lastElementChild.firstElementChild
      //     .scrollHeight;
      // }

      let currentHeight =
        this.$children[0].rawTop + this.$children[0].rawBottom;
      currentHeight = Math.abs(currentHeight);
      // console.log(bookHeight);
      const vh =
        Math.max(
          document.documentElement.clientHeight,
          window.innerHeight || 0
        ) - 75;
      // if(currentHeight < bookHeight) return;

      currentHeight -= 58; //remove the header height
      currentHeight -= 30; // remove the padding of content

      if (currentHeight > bookHeight) {
        let remainder = currentHeight - bookHeight; //get the remainder // if (remainder + currentHeight > vh) { //   //calc the correct height //   remainder = remainder + currentHeight - vh; // }

        // console.log(remainder);
        /*  */ let splRemainder = remainder / 2;
        // console.log(splRemainder);
        // console.log(this.$children[0].rawTop);
        // console.log(this.$children[0].rawBottom);
        this.$children[0].resizing = true;
        //  if (bookViewer && this.first) {
        this.first = false;
        this.$children[0].rawTop += splRemainder;
        this.$children[0].rawBottom += splRemainder;
        // } else if (bookViewer) {
        //   //  this.$children[0].rawTop += splRemainder;
        //   this.$children[0].rawBottom += splRemainder * 2;
        // } else {
        //   this.$children[0].rawBottom += splRemainder * 2;
        // }
        this.$children[0].resizing = false;
        // console.log(this.$children[0].rawTop);
        // console.log(this.$children[0].rawBottom);
        // console.log(splRemainder);
      }

      // this.$children[0].resizing = true;
      //        this.$children[0].rawTop += -50;
      //        this.$children[0].rawBottom += -50;
      //        this.$children[0].resizing = false;
    },
    toggleWidth(add = true) {
      this.$children[0].resizing = true;

      this.first = false;
      if (add) {
        this.$children[0].rawLeft += 100;
        this.$children[0].rawRight += 100;
      } else {
        this.$children[0].rawLeft -= 100;
        this.$children[0].rawRight -= 100;
      }

      this.$children[0].resizing = false;
    },
    setDragWidth(width, bigger = true) {
      this.$children[0].resizing = true;
      width = width / 2;
      if (bigger) width = width * -1;
      this.$children[0].rawRight += width;
      this.$children[0].rawLeft += width;

      this.$children[0].resizing = false;
    },
    addZindex() {
      // alert(5);
      // console.log(this.$parent.zindex);
      this.$parent.zindex += 1;
      this.zindex = this.$parent.zindex + 1;
    },
    resizeEventOccured() {
      this.resizeoccured++;
    }
  },

  computed: {
    sevntyPerc() {
      return window.innerWidth * 0.7;
    },
    zIndex() {
      return Date.now();
    },
    InitLeft() {
      return (
        globalThis.screen.availWidth * 0.15 + Math.floor(Math.random() * 10)
      );
    },
    viewBookMode() {
      return store.state[`section-popup-` + this.id].viewBookMode;
    },
    contentHeight() {
      const vh = Math.max(
        document.documentElement.clientHeight,
        window.innerHeight || 0
      );

      return vh - 76;
    }
  },
  created() {
    this.id = this.popup._id;
    this.zindex = this.$parent.zindex;
    store.registerModule(`section-popup-${this.id}`, SectionPopup);

    this.book = this.popup.book;
    if (this.popup.type == "em" || this.popup.type == "mh") {
      this.mask = false;
      this.book = this.popup.MareMakom.book;
      this.page = this.popup.MareMakom.pageNum;
    } else this.page = this.popup.MefoToMarks[0].Marks.pageNum;
  },

  beforeDestroy() {
    store.unregisterModule(`section-popup-${this.id}`);
  }
};
</script>

<style>
.PopupContainer {
  direction: rtl;
  display: flex;
  position: relative;
  /* height: 450px; */
  flex-direction: column;
  border-style: solid;
  border-width: 0px;
  border-color: #707070;
  -webkit-box-shadow: 0px 0px 8px -3px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 8px -3px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 8px -3px rgba(0, 0, 0, 0.75);
}
.popup-header {
  flex-grow: 1;
}
.popup-container {
  flex-grow: 1;
}
.section-viewer {
  background-color: white;
  overflow-y: scroll;
  height: calc(100% - 50px);
}
.book-viewer {
  background-color: white;
  overflow-y: auto;
  overflow-x: hidden;
  height: calc(100% - 50px);
  direction: ltr;
  padding: 0 10px;
}
.border-solid {
  border-width: 0px;
  /* height: fit-content !important; */
}
.my-handle-class {
  position: absolute;
  background-color: pink;
  border: 1px solid black;
  border-radius: 50%;
  height: 14px;
  width: 14px;

  -webkit-transition: all 300ms linear;
  -ms-transition: all 300ms linear;
  transition: all 300ms linear;
}

.my-handle-class-tl {
  top: -14px;
  left: -14px;
  cursor: nw-resize;
  opacity: 0;
}

.my-handle-class-tm {
  top: -14px;
  left: 50%;
  margin-left: -7px;
  cursor: n-resize;
  visibility: hidden;
}

.my-handle-class-tr {
  top: -14px;
  right: -14px;
  cursor: ne-resize;
  opacity: 0;
}

.my-handle-class-ml {
  top: 50%;
  margin-top: -7px;
  left: -14px;
  cursor: w-resize;
  visibility: hidden;
}

.my-handle-class-mr {
  top: 50%;
  margin-top: -7px;
  right: -14px;
  cursor: e-resize;
  display: none;
  visibility: hidden;
}

.my-handle-class-bl {
  bottom: -14px;
  left: -14px;
  cursor: sw-resize;
  opacity: 0;
}

.my-handle-class-bm {
  bottom: -14px;
  left: 50%;
  margin-left: -7px;
  cursor: s-resize;
  visibility: hidden;
}

.my-handle-class-br {
  bottom: -14px;
  right: -14px;
  cursor: se-resize;
  opacity: 0;
}
.shadow-class {
  /* -webkit-box-shadow: 10px 10px 64px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 64px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 64px 0px rgba(0, 0, 0, 0.75); */
  -webkit-box-shadow: 10px 10px 77px 20px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 77px 20px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 77px 20px rgba(0, 0, 0, 0.75);
}
.my-handle-class-tl:hover,
.my-handle-class-tr:hover,
.my-handle-class-bl:hover,
.my-handle-class-br:hover {
  background-color: gray;
  transform: scale(1.4);
  opacity: 1;
}
</style>
