import { mapGetters } from "vuex";
const ret = {
  computed: {
    ...mapGetters("tabs", ["getCurrentTab"]),
    getTheMainPageAddr() {
      let name = "main-page";
      let _selectedSection = null;

      if (typeof this.getCurrentTab == "undefined") return "/";
      let tab = this.getCurrentTab;
      if (tab.selectedSection != null) {
        _selectedSection = { sec: `${tab.selectedSection}` };
        name = "home-page-plus-section";
      }

      let addr = {
        name,
        params: {
          bookid: tab.book,
          page: tab.page,
          ..._selectedSection
        }
      };
      return { ...addr };
    }
  }
};

export default ret;
