<template>
  <div
    class="BookCoverButton card_button"
    style="height:31px"
    @click="showBookCover(1)"
    v-tooltip="'  '"
  >
    <span class="mef-font book-logo">
      <HoverGray h="25" w="25" t="3" l="-2">u</HoverGray>
    </span>
    <div class="BookCover" v-show="show" v-click-outside="hideBookCover">
      <span
        class="mef-font close-icon"
        color="black"
        @click.stop="hideBookCover"
      >
        <HoverGray h="25" w="25" t="3" l="-2">q</HoverGray>
      </span>
      <canvas
        width="464"
        height="600"
        v-waypoint="{ active: true, callback: onWaypoint }"
        class="BookCoverImage"
      />
    </div>
  </div>
</template>

<script>
import PictureService from "@/services/pictureService.js";
import getcode from "@/utils/enc";
import axios from "@/services/_axiosInstance";
import djvu from "@/utils/djvu";
import config from "@/config/config";
export default {
  props: ["book"],
  data() {
    return {
      show: false,
      onScreen: false,
      picture: null,
    };
  },
  methods: {
    showBookCover(val) {
      this.$store.state.showBook = this.book;
      // this.$$store.showBook = true;
      console.log(this.$root.showBook);
      this.show = val;
    },
    hideBookCover() {
      if (this.onScreen) this.show = false;
    },
    loadPicture() {
      return new Promise(async (resolve, reject) => {
        this.picture = document.createElement("img");
        let vm = this;
        this.picture.onload = function() {
          vm.access = true;
          resolve();
        };
        this.picture.onerror = function(err) {
          vm.ready = true;
          vm.access = false;
          vm.first = false;

          reject();
        };
        if (config.offline) {
          vm.access = true;
          let pic = await PictureService.getImageData(this.book, 0);

          let imagedata = null;

          if (pic.headers["content-type"] == "image/djvu") {
            let djvuPage = new djvu.Document(pic.data);
            imagedata = await djvuPage.pages[0].createPngObjectUrl();
          } else {
            let pic = await PictureService.getImage(this.book, 0, 0);
            // imagedata = pic.data;

            this.picture = pic;
            resolve();
            return;
          }

          let _canvas = new OffscreenCanvas(imagedata.width, imagedata.height);
          let ctx = _canvas.getContext("2d");
          ctx.putImageData(imagedata, 0, 0);

          this.picture = _canvas;
          resolve();
        } else this.picture.src = PictureService.getImageUrl(this.book, 0);
      });
    },
    async drawImage() {
      let canvas = this.$el.querySelector("canvas");
      // let ctx = canvas.getContext("2d");
      // ctx.drawImage(this.picture, 0, 0);
      let wd = (canvas.width * this.picture.height) / this.picture.width;
      canvas.height = wd;

      canvas.setAttribute(
        "style",
        `width:${canvas.width}px;height:${canvas.height}px`
      );

      await this.$root.pica.resize(this.picture, canvas);
    },
    async onWaypoint({ going, direction }) {
      // going: in, out
      // direction: top, right, bottom, left
      if (going === this.$waypointMap.GOING_IN) {
        this.onScreen = true;
        await this.loadPicture();
        await this.drawImage();
        // this.$refs.img.src = ImageService.getImageUrl(this.book, 0);
      }
      if (direction === this.$waypointMap.DIRECTION_TOP) {
        //gone
        this.onScreen = false;
      }
    },
  },
};
</script>

<style scoped>
.book-logo {
  font-size: 60px;
  line-height: 31px;
  width: 21px;
  height: 31px;
  display: inline-block;
}
.close-icon {
  font-size: 60px;
  line-height: 31px;
  width: 21px;
  height: 31px;
  display: inline-block;
  position: absolute;
  left: 5px;
}
.BookCover {
  display: flex;
  box-shadow: 10px 10px 77px 20px rgba(0, 0, 0, 0.75);
  justify-content: center;
  position: fixed;
  opacity: 1;
  top: 15%;
  left: 50%;
  margin-top: -50px;
  margin-left: -223px;
  width: 447px;
  height: 682px;
  /* border-style: solid;
  border-color: #707070;
  border-width: 1px; */
  z-index: 10;
  padding: 5px;
  text-align: center;
  background-color: white;
}

.BookCoverImage {
  max-height: 672px;
  max-width: 437px;
  vertical-align: middle;

  flex: 1;
}
</style>
