<template>
  <div class="filterHeader">
    <div class="filter-label-col flex-center">
      <span class="filter-label-col">  :</span>
    </div>
    <div class="filter-search-input-col" style="margin-right:5px">
      <FilterByText />
    </div>
    <div class="filter-catg-input-col">
      <ComboboxInput
        :content="selectedContent"
        :close="false"
        :label="label"
        :items="catgList"
        LabelKey="label"
        @input="filterByCatg($event)"
      />
    </div>
    <div
      class="filter-disable-col main-text-color"
      @click="turnOfFilters"
      v-tooltip="'   '"
      style="line-height:31px;width:22px;margin-right:5px;"
    >
      <span class="mef-font mef-font-default unselectable" style="z-index:2;position:relative">
        <template v-if="filterMode">k</template>
        <template v-else>U</template>
      </span>
      <div class="hover-active"></div>
    </div>
    <!-- <div class="filter-disable-col" @click="turnOfFilters" v-tooltip="'   '">
      <div class="filter-off-container" :class="{ disabled: !filterMode }">
        <svg viewBox="0 0 21.216 22.108" class="filter-off-btn">
          <use xlink:href="~@/assets/svgs/icons-pack.svg#filter-off" />
        </svg>
      </div>
    </div>-->
    <!-- <div class="filter-close-col close-btn" @click="tab.showFilters = false">
      <font-awesome-icon icon="times" class="close-size" />
    </div>-->
  </div>
</template>

<script>
// import { mdiFilter } from '@mdi/js';

import FilterByText from "./FilterByText";
import store from "@/store/store";
import TabState from "@/mixing/importTabState";

export default {
  name: "FilterHeader",
  mixins: [TabState],
  data() {
    return {
      label: "  ",
      catgdata: [
        { label: "", key: 0 },
        {
          label: "",
          key: 1
        },
        { label: "", key: 2 },
        { label: "", key: 3 },
        { label: "", key: 4 },
        { label: "", key: 5 }
      ],
      oCatgList: [
        { label: "", key: 0 },
        {
          label: "",
          key: 1
        },
        { label: "", key: 2 },
        { label: "", key: 3 },
        { label: "", key: 4 },
        { label: "", key: 5 }
      ]
    };
  },
  components: {
    FilterByText
  },
  methods: {
    filterByCatg($event) {
      // this.label = $event.label;

      this.$store.dispatch("tabs/setCatgFilter", $event.key);
    },
    turnOfFilters() {
      this.$store.getters.getCurrentTab.filter = !this.$store.getters
        .getCurrentTab.filter;
    }
  },
  computed: {
    selectedContent() {
      let content = null;
      if (this.tab.type_selected.length > 0) {
        content = "";
        if (this.tab.type_selected.includes(0)) return this.label;
        this.tab.type_selected.forEach(i => {
          switch (i) {
            case 1:
              content += ",";
              break;
            case 2:
              content += ",";
              break;
            case 3:
              content += ",";
              break;
            case 4:
              content += ",";
              break;
            case 4:
              content += ",";
              break;
          }
        });
      }
      if (content == null) return this.label;
      if (content.length > 0)
        content = content.substring(0, content.length - 1);
      return content;
    },
    catgList() {
      this.catgdata.forEach((i, inx) => {
        if (this.tab.type_selected.includes(inx)) {
          this.catgdata[inx].label =
            "<b><span class='mef-font checked'>M</span>" +
            this.oCatgList[inx].label +
            "</b>";
        } else {
          this.catgdata[0].label = this.oCatgList[0].label;
          this.catgdata[inx].label = this.oCatgList[inx].label;
        }
      });

      return this.catgdata;
    },
    cataaaagList() {
      return [
        { label: "<b><span class='mef-font checked'>M</span></b>", key: 0 },
        {
          label: "<b><span class='mef-font checked'>M</span></b>",
          key: 1
        },
        { label: "<b><span class='mef-font checked'>M</span></b>", key: 2 },
        { label: "", key: 3 },
        { label: "", key: 4 },
        { label: "", key: 5 }
      ];
    },
    filterMode() {
      return this.tab.filter;
    }
  }
};
</script>

<style>
.filterHeader {
  margin-top: 6.5px;
  margin-left: 11px;
  margin-right: 11px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-basis: 100%;
  height: 32px;
  position: relative;
  align-items: center;
  /* max-width: 532px; */
}
.filter-label-col {
  font-family: "Heebo";
  font-size: 12px;
  font-weight: 400;
  width: 63px;
  text-align: right;
  line-height: 12.7px;
  display: inline-block;
  color: #818181;
  position: relative;
}
.hover-active {
  position: absolute;
  height: 24px;
  width: 24px;
  top: 2px;
  background-color: #dedede;
  border-radius: 50%;
  z-index: 1;
}
.filter-search-input-col {
  flex: 1;
  /* min-width: 192px; */
}
.filter-catg-input-col {
  width: 117px;
  height: 20px;
  margin-right: 5px;
}
.filter-disable-col {
  width: 22px;
  height: 32px;
  cursor: pointer;
}

.disabled {
  stroke: gray;
}
.close-btn {
  top: -10px;
  vertical-align: text-top;
  position: absolute;
  left: 0px;
  width: 8px;
  height: 8px;
  cursor: pointer;
}
.filter-off-container {
  width: 20px;
}
.close-size {
  font-size: 12px;
}
.checked {
  line-height: 13px;
  font-size: 45px;
  vertical-align: sub;
}
</style>
