import ShasService from "@/services/shasService";
import ErrorChecker from "@/utils/rect-error-util";
import secutil from "@/utils/section-util";
export const namespaced = true;
let InnerSections = [];

export const state = function() {
  return {
    row: false,
    tabId: null,
    page: 9,
    alphaSort: false,
    showAllBooksPopup: false,
    book: 173936,
    selectedSection: null,
    selected: true,
    fltToBookIds: [],
    topBooks: [],
    pin: false,
    fav: false,
    showerror: false,
    only: 0,
    except: 0,
    catg_selected: 0,
    type_selected: [0, 1, 2, 3, 4, 5],
    filter: true,
    fitToWidth: true,
    bookViewerMode: false,
    title: " ",
    meforeshDims: {},
    zoom: 0,
    secId: null,
    showFilters: true,
    viewFullMode: true,
    eventFullMode: null, // this is use as event for change
    eventFullModeId: 0,
    cards: [],
    // sections: [],
    filteredSections: [],
    filterByText: "",
    popups: [],
    InnerSectionsGUID: 0, // for check if innersections changed,
    favBooks: [],
    sortAgain: 0,
  };
};

// export let sections = [];

export const mutations = {
  SET_SECTIONS(state, payload) {
    InnerSections[state.tabId] = payload;

    state.InnerSectionsGUID++;
  },
  FIT_TO_WIDTH(state, data) {
    state.fitToWidth = data;
  },
  SET_SELECTED_SECTION(state, data) {
    state.selectedSection = data;
  },
  SET_TYPE_SELECTED(state, data) {
    state.catg_selected = data;
  },
  SET_SELECTED_CHAPTER(state, data) {
    state.secId = data;
  },
  TOGGLE_PIN(state) {
    state.pin = !state.pin;
    if (state.pin)
      localStorage.setItem("pin_tab_" + state.tabId, JSON.stringify(state));
    else localStorage.removeItem("pin_tab_" + state.tabId);
  },
  ADD_TO_FLT_BOOKS(state, data) {
    if (data.only) state.fltToBookIds = [];
    state.fltToBookIds.push(data.book);
  },
};
export const actions = {
  removeFav({ state }, id) {
    state.favBooks = state.favBooks.filter((i) => i != id);
  },
  addFav({ state }, id) {
    state.favBooks.push(id);
  },
  delFltBook({ state }, data) {
    state.fltToBookIds = state.fltToBookIds.filter((i) => i != data);
  },
  addToFltBook({ commit }, data) {
    commit("ADD_TO_FLT_BOOKS", data);
  },
  setSelectedChapter({ commit }, data) {
    commit("SET_SELECTED_CHAPTER", data);
  },
  fitToWidth({ commit }, data) {
    commit("FIT_TO_WIDTH", data);
  },
  setSections({ commit }, sections) {
    return new Promise((resolve, reject) => {
      commit("SET_SECTIONS", sections);
      resolve();
    });
  },
  setSelectedSection({ commit }, secId) {
    commit("SET_SELECTED_SECTION", secId);
  },
  togglePin({ commit }) {
    commit("TOGGLE_PIN");
  },
  setTypeSelected({ commit }, data) {
    commit("SET_TYPE_SELECTED", data);
  },
};
export const getters = {
  getFltData(state) {
    return { ...state.fltToBookIds };
  },

  getFilteredSections(state, getters, rootState, rootGetters) {
    let lst = [];
    let filteredList = getters.getTextFilteredSections.lst || [];
    let selectedSectionGuid = state.InnerSectionsGUID;

    if (!selectedSectionGuid) return lst;

    filteredList.forEach((e) => {
      // console.log(e.id);
      if (state.secId != null) {
        if (e.id != state.secId) return;
      }
      //if (e.id == state.selectedSection) lst.push(e);
      if (state.filter) {
        //   //txt filter
        //   if (
        //     !e.bookName.includes(state.filterByText) &&
        //     !e.bookAuthor.includes(state.filterByText)
        //   )
        //     return;
        //console.log(e);
        //   //catg combo filter (sihor)
        if (!state.type_selected.includes(1) && e.type == "s") return;

        //   //catg combo filter (perush)
        if (!state.type_selected.includes(3) && e.type == "p") return;

        //   //catg combo filter (drush)
        if (!state.type_selected.includes(2) && e.type == "d") return;

        //   //catg combo filter (drush)
        if (!state.type_selected.includes(4) && e.type == "z") return;

        //   //catg combo filter (drush)
        if (!state.type_selected.includes(5) && e.type == "h") return;
      }

      if (state.catg_selected != 0) {
        let correctBook = secutil.getBookId(e);

        // if (correctBook !== e.MefoToMarks[0].Marks.book) {
        //   correctBook = "9" + correctBook + e.MefoToMarks[0].Marks.book;
        // }
        let book = rootGetters["books/getBookById"](correctBook);

        if (book == undefined) {
          return;
        }
        //book.sederdorot
        if (book.moredata == undefined && state.catg_selected == 9) {
          lst.push(e);
          return;
        }
        if (
          book.moredata == undefined ||
          book.moredata.dor != state.catg_selected
        ) {
          return;
        }
      }

      lst.push(e);
    });

    //remove dups
    lst = Array.from(new Set(lst));

    return lst;
  },
  getTextFilteredSections(state, getters, rootState, rootGetters) {
    let lst = [];
    let err = false;
    let err_id = "";
    let allowLink = true;
    let error_msg = false;
    let counters = {
      all: 0,
      mikra: 0,
      rishonim: 0,
      hacronim_str: 0,
      hacronim_trs: 0,
      hacrnim_zmanneno: 0,
      others: 0,
    };
    let selectedSectionGuid = state.InnerSectionsGUID;

    let _rectMefo = getters.getMefs;
    // console.log(_rectMefo);
    if (_rectMefo.length == 0 || !_rectMefo) return [];

    _rectMefo.forEach((elem, index) => {
      // elem.RectMefo = filterByCrossId(elem.RectMefo);
      elem.RectMefo.forEach((e, i) => {
        err = false;
        allowLink = true;
        err_id = "";
        //txt filter
        let correctBook = e.book;

        //actually here i ignore from errors from impotrted data !!!!!!!!!
        err = ErrorChecker.getError(e);

        if (err != false) {
          if (rootGetters.isItWorkEdition) {
            error_msg = err;
            err = true;
          } else return;
        }
        if (!err) {
          correctBook = secutil.getBookId(e); //"9" + e.MefoToMarks[0].Marks.book + correctBook;
        }
        let book = rootGetters["books/getBookById"](correctBook);
        if (book == undefined && !err) {
          // console.log(correctBook);
          err = true;
          err_id = "NO_BOOK";
          error_msg = "   ";
          //allowLink = false;
        }
        if (!err && state.filter) {
          if (
            !book.name.includes(state.filterByText) &&
            !book.authors[0].name.includes(state.filterByText)
          )
            return;

          //catg combo filter (sihor)

          if (!state.type_selected.includes(1) && e.type == "s") return;

          //   //catg combo filter (perush)
          if (!state.type_selected.includes(3) && e.type == "p") return;

          //   //catg combo filter (drush)
          if (!state.type_selected.includes(2) && e.type == "d") return;

          //   //catg combo filter (drush)
          if (!state.type_selected.includes(4) && e.type == "z") return;

          //   //catg combo filter (drush)
          if (!state.type_selected.includes(5) && e.type == "h") return;
        }
        //counter checks
        //inc the counter of all
        // if (book != undefined) {

        // }
        // debugger;
        if (book != undefined && book.moredata) {
          counters.all++;
          // counters.all++;
          switch (Number(book.moredata.dor)) {
            //inc mikra
            case 1:
              counters.mikra++;
              break;
            case 2:
              counters.rishonim++;
              break;
            case 3:
              counters.hacronim_str++;
              break;
            case 4:
              counters.hacronim_trs++;
              break;
            case 5:
              counters.hacrnim_zmanneno++;
              break;
            // default:
            //   // case 9:
            //   //inc others counter
            //   counters.others++;
            //   break;
            //   break;
          }
        } else if (book != undefined) {
          // book.sederdorot = { id: 9 };
          counters.others++;
          counters.all++;
        }
        // console.log(counters.others);
        if (err) {
          e.err = true;
          e.error_msg = error_msg;
          e.allowlink = allowLink;
          e.error_id = err_id;
          if (!rootGetters.isItWorkEdition) return;
        }
        if (rootGetters.isItWorkEdition && state.showerror) {
          if (e.err != true) return;
        }
        //REMOVE ERROR OF NOT EXIST BOOK
        if (err_id != "") return;
        lst.push(e);
      });
    });

    return { counters, lst };
  },

  getFullModeOfMeforesh: (state) => (secId) => {
    return state.sections.filter((e) => e.secId == secId).viewMode;
  },
  getTabTitle(state) {
    return ShasService.getFullName(state.book, state.page);
  },
  getAllBooks(state) {
    return () => {
      return InnerSections[state.tabId];
    };
  },
  getMefs(state, getters, rootState, rootGetters) {
    let selectedSectionGuid = state.InnerSectionsGUID;
    let data = [];
    if (state.showerror) {
      return InnerSections[state.tabId];
    }
    // console.log(InnerSections[state.tabId]);
    if (state.fltToBookIds.length > 0) {
      let clone = JSON.parse(JSON.stringify(InnerSections[state.tabId]));
      // console.log(clone);
      clone.forEach((i) => {
        i.RectMefo = i.RectMefo.filter((item) => {
          let bid = secutil.getBookId(item);
          let found = state.fltToBookIds.some((p) => p == bid);
          return found;
        });
      });

      return clone;
    }

    //   InnerSections.forEach(e=>{
    //     let flt=e.RectMefo.filter(i=>{
    //       let bid=secutil.getBookId(i)
    //       if(state.fltToBookIds.some(p=>p==bid)){

    //       }
    //     })
    //   });
    // }
    if (InnerSections[state.tabId] == undefined) return [];
    let item = InnerSections[state.tabId].filter(
      (e) => e.id == state.selectedSection
    )[0];
    if (item != undefined) data.push(item);
    else return [];
    // let rmefo = JSON.parse(JSON.stringify(data[0].RectMefo));
    if (data && !state.alphaSort) {
      data[0].RectMefo = data[0].RectMefo.sort((f, s) => {
        let priority = f.priority - s.priority;
        if (f.priority == s.priority) {
          let fbid = secutil.getBookId(f);
          let sbid = secutil.getBookId(s);
          let fbook = rootGetters["books/getBookById"](fbid);
          let sbook = rootGetters["books/getBookById"](sbid);
          if (fbook == undefined) return -1;
          if (sbook == undefined) return 1;
          priority = fbook.moredata.dor - sbook.moredata.dor;
        }

        return priority;
      });
    } else if (data) {
      data[0].RectMefo = data[0].RectMefo.sort((f, s) => {
        let fbid = secutil.getBookId(f);
        let sbid = secutil.getBookId(s);
        let fbook = rootGetters["books/getBookById"](fbid);
        let sbook = rootGetters["books/getBookById"](sbid);
        //if (fbook == undefined || sbook == undefined) return 0;
        if (fbook == undefined) return -1;
        if (sbook == undefined) return 1;
        if (fbook.name < sbook.name) {
          return -1;
        }
        if (fbook.name > sbook.name) {
          return 1;
        }
        return 0;
      });
    }

    // let _reactive = state.sortAgain;

    data[0].RectMefo.forEach((I) => {
      if (state.favBooks.includes(I.book)) I.fav = 1;
      else I.fav = 0;
    });
    data[0].RectMefo.sort((f, s) => {
      return s.fav - f.fav;
    });

    //   let f_fav = state.favBooks.includes(f.book);
    //   let s_fav = state.favBooks.includes(s.book);

    //   if (f_fav && !s_fav) {
    //     f_fav.fav = true;
    //     return -1;
    //   } else if (s_fav && !f_fav) {
    //     s_fav.fav = true;
    //     return 1;
    //   }
    //   return 0;
    // });
    return data; //InnerSections.filter(e => e.id == state.selectedSection)[0];
  },
};

function filterByCrossId(lst) {
  lst = lst.filter((e) => {
    //check for null or undef or not crossid
    if (
      e.MefoToMarks == undefined ||
      e.MefoToMarks == null ||
      e.MefoToMarks[0] == undefined ||
      e.MefoToMarks[0].crossId == null
    )
      return true;
    else if (e.remove != undefined) {
      return false;
    } else {
      //crossid not null
      console.log(e);
      // console.log(e);
      lst.forEach((item) => {
        if (
          item.MefoToMarks == undefined ||
          item.MefoToMarks == null ||
          item.MefoToMarks[0] == undefined ||
          item.MefoToMarks[0].crossId == null
        )
          return;
        else if (
          item.MefoToMarks[0].id != e.MefoToMarks[0].id &&
          item.MefoToMarks[0].crossId == e.MefoToMarks[0].crossId
        ) {
          // console.log("removed");
          item.remove = 1;
        }
      });
    }
    return true;
  });
  return lst;
}
