<template>
  <div class="card_button" style="margin:0px;height:31px">
    <span
      class="mef-font star-icon"
      @click="info"
      v-tooltip="'   '"
    >
      <HoverGray :color="false" h="25" w="25" t="2" l="1">7</HoverGray>
    </span>
    <ModalClean v-if="show" @close="show = false">
      <template v-slot:body>
        <div class="sec-info">
          <div class="flex sec-content">
            <div class="flex line">
              <span class="m2"> "</span>
              <span>{{ meforesh.pageSectionId }}</span>
            </div>
            <div class="flex line">
              <span class="m2"> </span>
              <span>{{ meforesh.id }}</span>
            </div>
            <div class="flex line">
              <span class="m2">  </span>
              <span>{{ meforesh.book }}</span>
            </div>
            <div class="flex line">
              <span class="m2">  </span>
              <span>{{ realBook.id }}</span>
            </div>
            <div class="flex line">
              <span class="m2"></span>
              <span>{{ fromPage }} - {{ toPage }}</span>
            </div>
            <div class="flex line">
              <span class="m2"> </span>
              <span>
                <template v-if="realBook.id != meforesh.book"></template>
                <template v-else></template>
              </span>
            </div>
            <div class="flex line">
              <span class="m2"> </span>
              <span>{{ realBook.name }}</span>
            </div>
            <div class="flex line">
              <span class="m2"></span>
              <span>{{ dor }}</span>
            </div>
            <div class="flex line">
              <span class="m2"></span>
              <span>{{ meftype }}</span>
            </div>
            <div class="flex line">
              <span class="m2"></span>
              <span>{{ meforesh.priority }}</span>
            </div>
            <div class="flex line">
              <span class="m2"></span>

              <span>
                <p
                  v-for="auth in realBook.authors"
                  :key="auth.id"
                  :class="{
                    bold:
                      auth.authorTypeId != undefined && auth.authorTypeId == 1
                  }"
                >
                  {{ auth.name }}
                </p>
              </span>
            </div>
            <div class="flex line">
              <span class="m2"> </span>
              <span>{{ meforesh.MefoToMarks.length }}</span>
            </div>
            <div class="flex line" v-if="!offline">
              <span class="m2"> "</span>
              <span>{{ getWorkerInfo.name }}</span>
            </div>
            <div class="flex line" v-if="!offline">
              <span class="m2"> </span>
              <span>{{ getWorkerInfo.id }}</span>
            </div>
            <div class="flex line">
              <span class="m2"></span>
              <span>
                <button @click="copyData(meforesh)" style="margin-left:5px">
                    
                </button>
                <button @click="copyData(realBook)">  </button>
              </span>
            </div>
          </div>
        </div>
      </template>
    </ModalClean>
  </div>
</template>

<script>
import secutil from "@/utils/section-util";
import _getWorkerInfo from "@/services/workersInfo";
import config from "@/config/config";
export default {
  props: ["meforesh"],
  data() {
    return {
      jsonData: false,
      show: false
    };
  },
  computed: {
    getWorkerInfo() {
      return _getWorkerInfo(3);
    },
    dor() {
      switch (this.realBook.moredata.dor) {
        case 1:
          return ' "';
        case 2:
          return " ";
        case 3:
          return ` '-"`;
        case 4:
          return ' "-"';
        case 5:
          return " ";
        default:
          return " ";
      }
    },
    offline() {
      return config.offline;
    },
    fromPage() {
      return this.meforesh.MefoToMarks[0].Marks.pageNum;
    },
    toPage() {
      return this.meforesh.MefoToMarks[this.meforesh.MefoToMarks.length - 1]
        .Marks.pageNum;
    },
    meftype() {
      switch (this.meforesh.type) {
        case "p":
          return "";
        case "d":
          return "";
        case "z":
          return "";
        case "h":
          return "";
        case "s":
          return "";
      }
    },
    realBook() {
      let correctBook = secutil.getBookId(this.meforesh);
      let book = this.$store.getters["books/getBookById"](correctBook);
      return book;
    }
  },
  created() {
    // this.fav = this.meforesh.fav;
  },
  methods: {
    copyData(data) {
      data = JSON.stringify(data);
      let elm = document.createElement("INPUT");
      elm.setAttribute("type", "text");
      elm.setAttribute("value", data);
      elm = document.body.appendChild(elm);
      elm.select();
      document.execCommand("copy");
      elm.remove();
      alert(" ");
    },
    info() {
      console.log(this.meforesh);
      this.show = !this.show;
      // alert(JSON.stringify(this.meforesh));
    }
  }
};
</script>

<style lang="scss" scoped>
.star-icon {
  font-size: 60px;
  line-height: 31px;
  width: 26px;
  height: 31px;
  display: inline-block;
}
.active {
  color: orange;
}
.sec-info {
  padding: 30px;
  -webkit-box-shadow: 10px 10px 79px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 79px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 79px 0px rgba(0, 0, 0, 0.75);
  border: 1px solid black;
}
.sec-content {
  flex-direction: column;
  justify-content: center;
  align-content: center;
  font-size: 17px;
  width: 450px;
}
.m2 {
  margin-left: 45px;
}
.bold {
  font-weight: bold;
}
.line {
  padding: 3px 3px;
  border-bottom: 1px solid #0d0d0e;
  justify-content: space-between;
}
</style>
