<template>
  <div style="position: relative;
    overflow: hidden;" :style="{right: right + 'px'}">
    <slot></slot>
  </div>
</template>

<script>
export default {
  data() {
    return {
      right: 0,
    };
  },
  created() {
    this.right = 500;
    setInterval(() => {
      if (this.right < -1500) this.right = 500;
      this.right -= 1;
    }, 30);
  },
};
</script>

