<!-- this component sort user's libs by label or date implemented in MeforUserLibsList component -->
<template>
  <div class="order-libs-options-block">
    <p class="just-text">: </p>
    <!-- the select option-->
    <select
      class="option-menu-box"
      name="sortBy"
      id="sortBy"
      @change="updateOrderLibsByUser(sortType)"
      v-model="sortType"
    >
      <!-- user can choose to sort by click on the one of the options-->
      <option class="option-menu" v-for="item in sortOptions" :value="item.value">{{item.text}}</option>
    </select>
  </div>
</template>

<script>
import { mapMutations, mapState, mapGetters } from "vuex";

export default {
  data() {
    return {
      //the defoult sort type
      sortType: "label",
      //a list of the options
      sortOptions: [
        { text: " ", value: "label" },
        { text: "  ", value: "updated_date" }
      ]
    };
  },
  methods: {
    //by click on one of the options user change the sort libs list
    updateOrderLibsByUser(sortType) {
      //the actual update of the sort from the mutation updateSortLibs that in the module meforshimUserLibs
      this.$store.dispatch("meforshimUserLibs/updateSortLibs", {
        sortType
      });
    }
  }
};
</script>
<!-- styling the sort box-->
<style lang="scss" scoped>
.order-libs-options-block {
  width: 204px;
  height: 24px;
  position: fixed;
  left: 14%;
  top: 90px;
}
.just-text {
  width: 44px;
  height: 21px;
  font-size: 12px;
  color: #b7b7b7;

  position: fixed;
  left: 24.6%;
  top: 83px;
}
.option-menu-box {
  width: 156px;
  height: 24px;
  border: 1px solid #dedede;
  border-radius: 20px;
  background-color: #f5f5f5;
  outline: none;
}
.option-menu-box option {
  width: 146px;
  height: 24px;
  border: 1px solid #dedede;
  background-color: #f5f5f5;
}
</style>