<template>
  <div style="height:20px">
    <SearchInput v-model="text" @input="triggerFilter" placeholder="   " />
  </div>
</template>

<script>
import store from "@/store/store";
export default {
  data() {
    return {
      text: ""
    };
  },
  methods: {
    triggerFilter() {
      store.getters.getCurrentTab.filterByText = this.text;
    }
  }
};
</script>

