<template>
  <div class="delete-lib">
    <p class="text-update"> {{countSelectedSections}}  </p>
    <!-- click prevent delete lib-->
    <div v-on="$listeners" class="delete-btn" @click.stop="deleteSelectedUserSections()"></div>
    <!-- this button will not delete lib and go back to the user libs list-->
    <button v-on="$listeners" class="exit-to-list"></button>
  </div>
</template>


<script>
import { mapMutations, mapState, mapGetters } from "vuex";
import store from "@/store/store";

export default {
  props: {
    //getting lib from contextMenuLib component to find witch lib to delete
    secs: Array,
    folderId: ""
  },
  methods: {
    //this methods called when user click the delete
    deleteSelectedUserSections() {
      store.state.sectionsFolder.countSelectedSections = 0;
      for (var i = 0; i < this.secs.length; i) {
        if (this.secs[i].section.selected === true) {
          let payload = {
            folderId: this.folderId,
            secId: this.secs[i].section.secId
          };
          //the actual deletion from the action deleteLib that in the module meforshimUserLibs
          this.$store.dispatch("meforshimUserLibs/deleteUserSection", {
            folderId: this.folderId,
            secId: this.secs[i].section.secId
          });
        } else {
          i++;
        }
      }
    }
  },
  computed: {
    ...mapState("sectionsFolder", ["countSelectedSections"])
  }
};
</script>
