<template>
  <div class="flex" v-if="user != null" style="flex-direction: column">
    <div>
      <span> :</span>
      <span>{{ user.name }}</span>
    </div>
    <div>
      <span> :</span>
      <span>{{ user.email }}</span>
    </div>
    <div>
      <span>  :</span>
      <span v-if="!isItExpire">{{ userFrom }} - {{ userTo }}</span>
      <span v-else style="color: red">   </span>
    </div>
    <div class="bottom-buttons">
      <button @click="signout"></button>
    </div>
  </div>
  <div v-else>  ...</div>
</template>

<script>
import apiClient from "@/services/_axiosInstance";
import { clearJwt } from "@/services/_axiosInstance";
import moment from "moment";
import { mapActions } from "vuex";
export default {
  data() {
    return {
      user: null
    };
  },
  computed: {
    isItExpire() {
      if (this.user.to == null) return true;
      return Date(this.user.to) < Date.now();
    },
    userFrom() {
      return moment(this.user.from).format("L");
    },
    userTo() {
      return moment(this.user.to).format("L");
    }
  },
  methods: {
    ...mapActions("user", ["setJwt", "setName"]),
    signout() {
      this.setJwt(null);
      this.setName(null);
      localStorage.removeItem("name");
      localStorage.removeItem("token");
      clearJwt();
      this.$store.state.tabs.reloadAll = !this.$store.state.tabs.reloadAll;
      this.$emit("close");
    }
  },
  created() {
    apiClient
      .get(`${MAIN_SERVER}:${MAIN_PORT}/api/user/info`)
      .then(({ data }) => {
        this.user = data;
      })
      .catch((e) => {
        this.signout();
      });
  }
};
</script>

<style lang="scss" scoped>
.bottom-buttons {
  text-align: center;
  margin-top: 15px;
}
</style>
