<template>
  <div class="pagePicker" @click="toggleMenu" v-click-outside="closeMenu">
    <div class="selected-text">{{ innergetSelectedPage }}</div>
    <!-- <font-awesome-icon icon="caret-down" class="dropdown-icon" /> -->
    <span class="mef-font-default mef-font dropdown-icon">p</span>

    <div class="option-items" :class="{ 'open-menu': showMenu }">
      <div
        class="option-item"
        v-for="(page, index) in getBookListPages"
        :key="index"
      >
        <span class="label-option" @click.stop="setPageNumber(page)">
          {{ page.title }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import Menu from "@/mixing/menuFunctions";
import { mapGetters, mapActions } from "vuex";
import { getBookPages } from "@/services/shasService";
import { pagesDic } from "@/services/shasService";
export default {
  mixins: [Menu],
  props: ["lazy", "selected", "pagedata", "trick"],
  methods: {
    pagesDic(x, y) {
      return pagesDic(x, y);
    },
    setPageNumber(page) {
      this.closeMenu();
      if (this.lazy == "1") {
        this.$emit("pageselect", page);
        return;
      }
      this.$router.push({
        name: "n-main-page",
        params: {
          bookid: this.getSelectedBookNumber,
          page: page.number,
          tab: this.$store.state.tabs.currentTabId
        }
      });
    }
  },
  computed: {
    ...mapGetters("Shas", [
      "getBookPages",
      "getSelectedPage",
      "getSelectedBookNumber"
    ]),
    innergetSelectedPage() {
      if (this.lazy == "1") {
        return this.pagedata.number == -1 ? "" : this.pagedata.title;
      }
      return this.getSelectedPage;
    },
    getBookListPages() {
      if (this.lazy == "1") {
        let add = 0;
        let selected = this.selected;
        if (
          this.$parent.book != null &&
          this.$parent.book.firstLabelPage != undefined
        ) {
          let pages = [
            { title: "", number: 53 },
            { title: "", number: 55 },
            { title: "", number: 57 },
            { title: "", number: 59 },
            { title: "", number: 61 },
            { title: "", number: 63 },
            { title: "", number: 65 },
            { title: "", number: 67 },
            { title: "", number: 69 }
          ];
          return pages;
        }

        let pages = this.pagedata ? getBookPages(add, selected) : [];
        // let pages = this.pagedata ? pagesDic(0, selected) : [];

        return pages;
      }
      return this.getBookPages;
    }
  }
};
</script>

<style scoped>
.pagePicker {
  font-size: 12px;
  border-left-color: #dcdcdc;
  border-width: 0px;
  display: flex;
  border-left-width: 1px;
  border-style: solid;
  text-align: right;
  width: 49px;
  box-sizing: border-box;
  position: relative;
  /* justify-content: center; */
  align-items: center;
}
.selected-text {
  width: 30px;
  padding-right: 6px;
}
.dropdown-icon {
  width: 13px;
  line-height: 14px;
  margin-left: 5px;
}
.option-items {
  display: none;
  position: absolute;
  flex-direction: row;
  z-index: 2;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  left: 0px;
  top: 100%;
  width: 256px;
  flex-wrap: wrap;
}
.open-menu {
  display: flex;
}
.option-item {
  height: 30px;
  width: 30px;
  align-self: flex-start;
  border-style: solid;
  border-width: 1px;
  border-color: #f5f5f5;
  font-size: 14px;
  text-align: center;
  display: table;
}
.label-option {
  display: table-cell;
  vertical-align: middle;
}
.label-option:hover {
  background-color: #dedede;
}
</style>
