<template>
  <div class="btn-page-tools" v-tooltip="' '">
    <!-- <span class="mef-font">o</span> -->
    <font-awesome-icon
      icon="thumbtack"
      :class="{ active: isPin }"
      @click="togglePin"
    />
  </div>
</template>

<script>
import TabState from "@/mixing/importTabState";

export default {
  mixins: [TabState],
  methods: {
    togglePin() {
      this.$store.dispatch(`tab_${this.tabid}/togglePin`);
      // this.tab.pin = !this.tab.pin;
    }
  },
  computed: {
    isPin() {
      return this.tab.pin;
    }
  }
};
</script>

<style scoped>
.active {
  color: orange;
}
</style>
