import Vue from "vue";
import App from "./App.vue";
import router from "./routes/router";
import store from "./store/store";
import VueSplit from "vue-split-panel";
import VueWaypoint from "vue-waypoint";
import vClickOutside from "v-click-outside";
import ToggleButton from "vue-js-toggle-button";
import VueDraggableResizable from "vue-draggable-resizable";
import VueI18n from "vue-i18n";
import messages from "./localization/local";
import upperFirst from "lodash/upperFirst";
import camelCase from "lodash/camelCase";
import VueVirtualScroller from "vue-virtual-scroller";
import VTooltip from "v-tooltip";
import Pica from "pica";
import Notifications from "vue-notification";
import FontAwesomeInstall from "./utils/registerFa";
FontAwesomeInstall(Vue);
Vue.use(VTooltip);

Vue.use(Notifications);

Vue.directive("focus", {
  // When the bound element is inserted into the DOM...
  inserted: function(el) {
    // Focus the element
    el.focus();
  },
});

Vue.use(VueVirtualScroller);
import "vue-virtual-scroller/dist/vue-virtual-scroller.css";

//init bootstrap
require("./bootstrap/init");

// import "vue-material-design-icons/styles.css";
const requireComponent = require.context(
  // The relative path of the components folder
  "@/components/BaseComponents",
  // Whether or not to look in subfolders
  false,
  // The regular expression used to match base component filenames
  /[A-Z]\w+\.(vue|js)$/
);

requireComponent.keys().forEach((fileName) => {
  // Get component config
  const componentConfig = requireComponent(fileName);

  // Get PascalCase name of component
  const componentName = upperFirst(
    camelCase(
      // Gets the file name regardless of folder depth
      fileName
        .split("/")
        .pop()
        .replace(/\.\w+$/, "")
    )
  );

  // Register component globally
  Vue.component(
    componentName,
    // Look for the component options on `.default`, which will
    // exist if the component was exported with `export default`,
    // otherwise fall back to module's root.
    componentConfig.default || componentConfig
  );
});
// optionally import default styles

require("vue-draggable-resizable/dist/VueDraggableResizable.css");
//vue-draggable-resizable plugin
Vue.component("vue-draggable-resizable", VueDraggableResizable);

import "nprogress/nprogress.css";
import("./style.scss");

import viewport from "./plugins/viewport";
Vue.use(viewport);

import storePlugin from "./plugins/mobileDetect";
Vue.use(storePlugin);

import imageRotatePlugin from "./plugins/imageRotate";
Vue.use(imageRotatePlugin);

import errorPlugin from "./plugins/errorManager";
Vue.use(errorPlugin);

// Waypoint plugin
Vue.use(VueWaypoint);
// split.js plugin
Vue.use(VueSplit);
Vue.config.productionTip = false;

//v-click-outside plugin
Vue.use(vClickOutside);

//toggle button plugin
Vue.use(ToggleButton);

//VueI18n plugin
Vue.use(VueI18n);

// Create VueI18n instance with options
const i18n = new VueI18n({
  locale: "he", // set locale
  messages, // set locale messages
});

// import io from "socket.io-client";
new Vue({
  router,
  i18n,
  store,
  render: (h) => h(App),
  data: () => {
    return { pica: null };
  },
  // data: () => {
  //   return { server: null };
  // },
  created() {
    this.pica = Pica({ features: ["js", "wasm", "ww", "cib"] });
  },
}).$mount("#app");
