export const namespaced = true;

export const state = {
  page: {
    page: 8,
    book: 173936,
    selectedSection: 0,
    only: 0,
    except: 0,
    id: 1
  }
};

export const mutations = {
  SET_PAGE(state, tabData) {
    state.page.page = tabData.page;
    state.page.book = tabData.book;
    state.page.selectedSection = tabData.selectedSection;
    state.page.only = tabData.only;
    state.page.except = tabData.except;
    state.page.id = tabData.tabId;
  }
};
export const actions = {
  setPage({ commit, state }, tabData) {
    commit("SET_PAGE", tabData);
  }
};
export const getters = {
  getTabs: state => state.tabs
};
