<template>
  <div class="card" :class="[full ? 'card-open' : 'card-close']">
    <CardHeader :meforesh="section" :inx="inx" />
    <div v-show="full" class="piccontent">
      <BookContent
        :vrefresh="refresh"
        :book="section.book"
        :page="section.MefoToMarks[0].Marks.pageNum"
        :sections="section"
      />
    </div>
  </div>
</template>

<script>
import tabState from "@/mixing/importTabState";
import BookContent from "@/components/MainPage/SectionBookViewer/Viewer.vue";
import CardHeader from "./CardHeader/CardHeader";
import { mapGetters } from "vuex";

export default {
  mixins: [tabState],
  data() {
    return {
      full: true,
      refresh: 0
    };
  },
  components: { BookContent, CardHeader },
  computed: {
    ...mapGetters("tabs", ["getCurrentTab", "getFullModeOfMeforesh"]),
    GlobalViewMode() {
      return this.getCurrentTab.viewFullMode;
    },
    ViewModeEvent() {
      return this.getCurrentTab.eventFullMode;
    },
    refreshSection() {
      return this.$store.state.tabs.refreshMarkers;
    }
  },
  watch: {
    GlobalViewMode: function(val) {
      this.full = val;
    },
    ViewModeEventId: function(val) {
      if (this.ViewModeEvent == this.inx) this.full = !this.full;
    },
    refreshSection: function() {
      // console.log("xx");
      if (this.$inViewPort(this.$el)) {
        // console.log("zzz");
        // this.showBook = !this.showBook;
        // this.showBook = !this.showBook;
        this.refresh += 1;
      }
    }
  },
  props: ["section", "inx", "tabId"],
  created() {
    // console.log(this.getCurrentTab);
    this.full = this.getCurrentTab.viewFullMode;
  }
};
</script>

<style lang="scss" scoped>
.piccontent {
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #d2d2d2;
}
.card {
  padding-top: 2.5px;
  margin: 0px 12px;
}
.card-open {
  margin-bottom: 12px;
}
.card-close {
  margin-bottom: 2.5px;
}
</style>
