<template>
  <div>
    <div v-for="(item, index) in lst" :key="item.id">
      <slot :item="item" :inx="index" :inc="inc"></slot>
    </div>
    <!-- <slot v-for="(item, index) in data.slice(10)" :item="item" :inx="index" :inc="inc"></slot> -->

    <!-- <div :style="bottom_h" id="placeholder_bottom" /> -->
  </div>
</template>

<script>
export default {
  name: "FakeSectionScroll",
  props: ["data", "max"],
  data() {
    return {
      bottom: 0,
      current: 0,
      inc: 8,
      done: false
    };
  },
  watch: {
    data() {
      //this.recursive_load_data();
    }
  },
  computed: {
    bottom_h() {
      if (this.done) return 0;
      let shiur_len = this.data.filter(i => {
        return i.type == "s";
      }).length;
      let others_len = this.data.filter(i => {
        return i.type != "s";
      }).length;
      this.bottom = others_len * 347;
      this.bottom += shiur_len * 40;
      return `height:${this.bottom}px;width:2px;position:absolutes`;
    },
    lst() {
      //console.log(this.data);
      return this.data; //.slice(0, 10);
    }
  },
  mounted() {
    //this.inc = 10;
    // setTimeout(() => {
    //   this.recursive_load_data();
    // }, 100);
  },
  methods: {
    recursive_load_data() {
      //console.log(this.inc);
      if (this.inc < this.data.length) {
        this.inc += 20;
        setTimeout(() => {
          this.recursive_load_data();
        }, 15000);
      } else {
        this.done = true;
      }
    }
  }
};
</script>

<style lang="scss" scoped></style>
