<template>
  <div class="pagePicker" @click="toggleMenu" v-click-outside="closeMenu">
    <div class="selected-text">{{ innergetSelectedSubPage }}</div>
    <!-- <font-awesome-icon icon="caret-down" class="dropdown-icon" /> -->
    <span class="mef-font-default mef-font dropdown-icon">p</span>
    <div class="option-items" :class="{ 'open-menu': showMenu }">
      <p @click.stop="setSubPageNumber(getSelectedPageNumber, 0)"></p>
      <p @click.stop="setSubPageNumber(getSelectedPageNumber, 1)"></p>
    </div>
  </div>
</template>

<script>
import Menu from "@/mixing/menuFunctions";
import { mapGetters } from "vuex";
export default {
  mixins: [Menu],
  props: ["lazy", "num"],
  computed: {
    ...mapGetters("Shas", [
      "getSelectedSubPage",
      "getSelectedPageNumber",
      "getSelectedBookNumber"
    ]),
    innergetSelectedSubPage() {
      if (this.lazy == "1") {
        if (this.num == 0) return '"';
        else if (this.num == 1) return '"';
        // return "";
      }
      return this.getSelectedSubPage;
    }
  },
  methods: {
    setSubPageNumber(page, sp) {
      this.closeMenu();
      if (this.lazy == "1") {
        console.log(sp);
        this.$emit("select", sp);
        return;
      }
      if (page % 2 == 0) {
        page = Number(page) + 1;
      } else page = Number(page) - 1;
      this.$router.push({
        name: "n-main-page",
        params: {
          bookid: this.getSelectedBookNumber,
          page,
          tab: this.$store.state.tabs.currentTabId
        }
      });
    }
  }
};
</script>

<style scoped>
.pagePicker {
  font-size: 12px;
  border-left-color: #c6c6c6;
  border-width: 0px;
  display: flex;
  width: 47px;
  position: relative;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}
.selected-text {
  width: 20px;
  padding-right: 5px;
}
.dropdown-icon {
  width: 13px;
  margin-top: 1.5px;
  line-height: 14px;
  margin-left: 6px;
}
.option-items {
  display: none;
  position: absolute;
  z-index: 2;
  width: 40px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);

  top: 100%;
  width: 100%;
}
.open-menu {
  display: block;
}
</style>
