<template>
  <div class="book_viewer card_button" @click="openBookViewer" v-tooltip="' '">
    <span class="mef-font book-viewer-icon">
      <HoverGray h="25" w="25" t="2" l="1">t</HoverGray>
    </span>
    <!-- <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 15">
            <use xlink:href="~@/assets/svgs/icons-pack.svg#open_book_viewer"></use>
    </svg>-->
    <!-- <font-awesome-icon icon="book-open" /> -->
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import store from "@/store/store";
export default {
  props: ["meforesh"],
  computed: {
    ...mapState("tabs", ["currentTabId"]),
    ...mapGetters("tabs", ["getTab"])
  },
  methods: {
    openBookViewer() {
      this.$parent.$parent.$parent.showBook = !this.$parent.$parent.$parent
        .showBook;

      let Data = this.meforesh;

      let popup = {
        ...Data,
        ready: false
      };

      store.getters.getCurrentTab.popups.push(popup);
    }
  }
};
</script>

<style>
.book-viewer-icon {
  font-size: 60px;
  line-height: 31px;
  width: 26px;
  height: 31px;
  display: inline-block;
}
.book_viewer {
  height: 31px;
  width: 26px;
}
</style>
