const pako = require("pako");
const axios = require("axios");
import BTree from "sorted-btree";
import moment from "moment";
import config from "@/config/config";
export const namespaced = true;

export let books = [];
export let angles = [];
export const state = {};
export let treeBooks = new BTree();
export const mutations = {
  SET_BOOKS(state, Data) {
    books = Data;
  },
  SET_ANGLES(state, data) {
    angles = data;
  },
};
export const actions = {
  async loadAngles({ commit }) {
    let res = await axios.get(`${MAIN_SERVER}:${MAIN_PORT}/api/books/angles`);
    let data = pako.inflate(res.data, {
      to: "string",
    });
    data = JSON.parse(data);

    commit("SET_ANGLES", data);
    data = null;
    return Promise.resolve();
  },

  async loadBooks({ commit, dispatch }) {
    //check if the books already in the localstorage

    let data = localStorage.getItem("books");
    let lastUpdateFromLocal = localStorage.getItem("lastbookupdate");
    let lastUpdate = null;
    if (!config.offline) {
      lastUpdate = await axios.get(
        `${MAIN_SERVER}:${MAIN_PORT}/api/general/update/check`
      );
      lastUpdate = lastUpdate.data;
    }
    if (
      !lastUpdateFromLocal ||
      moment(lastUpdate).isAfter(lastUpdateFromLocal)
    ) {
      //reset data so we load the books
      data = false;
      localStorage.removeItem("lastbookupdate");
    }
    if (config.offline) data = 0;
    if (!data) {
      //load books from the server
      let res = await axios.get(`${MAIN_SERVER}:${MAIN_PORT}/api/books`);
      try {
        localStorage.setItem("books", res.data);
        localStorage.setItem("lastbookupdate", lastUpdate);
      } catch (e) {
        console.warn("the browser is unable to save data to the localstorage");
      }

      data = JSON.parse(
        pako.inflate(res.data, {
          to: "string",
        })
      );
    } else {
      //decompress the books

      data = pako.inflate(data, {
        to: "string",
      });
      data = JSON.parse(data);
    }
    data.forEach((e) => {
      treeBooks.set(e.id, e);
      //treeBooks.insert(e.id, e);
    });
    commit("SET_BOOKS", data);
    data = null;
    return dispatch("loadAngles");
  },
  setPage({ commit, state }, tabData) {
    commit("SET_PAGE", tabData);
  },
};
export const getters = {
  getBookById: (state) => (bookid) => {
    let ret = treeBooks.get(bookid);
    // debugger;
    return ret;
    // books.find(e => {
    //   return e.id == bookid;
    // })
  },
  getAngle: (state) => (bookid, page) => {
    let angle = angles.find((e) => {
      return e.page == page && e.book == bookid;
    });
    // console.log(angle);
    if (angle == undefined) return 0;
    else {
      return angle.angle;
    }
  },

  //getBookNameById: bookid => state => books.find(e => (e.bookid = bookid)[0].)
};
