  methods: {
    goHone() {
      this.$store.state.welcomeScreen = true;
      router.push("/");
    },
    openGuide() {
      let userLang = navigator.language || navigator.userLanguage;

      if (config.offline) {
        //open pdfs

        let userLang = navigator.language || navigator.userLanguage;
        if (userLang == "he")
          window.open("http://127.0.0.1:3010/pdf/guide_heb.pdf", "_blank");
        else window.open("http://127.0.0.1:3010/pdf/guide_eng.pdf", "_blank");
      } else {
        //go to info
        // window.location.href = ;
        window.open("https://mefo.otzar.org/info", "_blank");
      }
    },
