const ret = {
  props: ["tabid"],
  computed: {
    getTextFilteredSections() {
      let addr = `tab_${this.tabid}/getTextFilteredSections`;
      return this.$store.getters[addr];
    },
    getFilteredSections() {
      let addr = `tab_${this.tabid}/getFilteredSections`;
      return this.$store.getters[addr];
    },
    getTabTitle() {
      let addr = `tab_${this.tabid}/getTabTitle`;
      return this.$store.getters[addr];
    },
    getAllBooks() {
      let addr = `tab_${this.tabid}/getAllBooks`;
      return this.$store.getters[addr]();
    },
  },
};

export default ret;
