<template>
  <div class="btn-page-tools fullscreen" @click="toggleFullScreen" v-tooltip="' '">
    <!-- <font-awesome-icon :icon="fullScreen ? 'compress-arrows-alt' : 'compress'" /> -->
    <span class="mef-font mef-font-default">
      <template v-if="!fullScreen">
        <HoverGray h="37" w="37" t="-3" l="-3">c</HoverGray>
      </template>
      <template v-else>
        <HoverGray h="37" w="37" t="-3" l="-3">d</HoverGray>
      </template>
    </span>
  </div>
</template>

<script>
export default {
  data() {
    return { fullScreen: false };
  },
  methods: {
    toggleFullScreen() {
      if (this.fullScreen) this.closeFullscreen();
      else this.openFullscreen();
    },
    openFullscreen() {
      this.fullScreen = true;
      let elem = document.body;
      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.mozRequestFullScreen) {
        /* Firefox */
        elem.mozRequestFullScreen();
      } else if (elem.webkitRequestFullscreen) {
        /* Chrome, Safari and Opera */
        elem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) {
        /* IE/Edge */
        elem.msRequestFullscreen();
      }
    },
    closeFullscreen() {
      this.fullScreen = false;
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.mozCancelFullScreen) {
        /* Firefox */
        document.mozCancelFullScreen();
      } else if (document.webkitExitFullscreen) {
        /* Chrome, Safari and Opera */
        document.webkitExitFullscreen();
      } else if (document.msExitFullscreen) {
        /* IE/Edge */
        document.msExitFullscreen();
      }
    }
  }
};
</script>

<style scoped>
.fullscreen {
  width: 32px;
  height: 31px;
  line-height: 31px;
  margin-top: 12px;
}
</style>
