<template>
  <div class="card_button" style="margin:0px;height:31px">
    <span
      class="mef-font star-icon"
      :class="{ active: active }"
      @click="copyLink"
      v-tooltip="' '"
    >
      <HoverGray :color="false" h="25" w="25" t="2" l="1">5</HoverGray>
    </span>
  </div>
</template>

<script>
export default {
  props: ["meforesh"],
  data() {
    return {
      fav: 0
    };
  },
  computed: {
    active() {
      return this.fav == 1;
    }
  },
  created() {
    this.fav = this.meforesh.fav;
  },
  methods: {
    copyLink() {
      let elm = document.createElement("INPUT");
      let tab = this.$store.getters.getCurrentTab;
      let url = window.location.href;
      let arr = url.split("/");

      elm.setAttribute("type", "text");
      elm.setAttribute(
        "value",
        `${arr[0]}//${arr[2]}/#/t/${tab.tabId}/b/${tab.book}/p/${tab.page}/s/${this.meforesh.pageSectionId}/c/${this.meforesh.id}`
      );
      elm = document.body.appendChild(elm);
      elm.select();
      document.execCommand("copy");
      elm.remove();
      alert(" ");
    }
  }
};
</script>

<style lang="scss" scoped>
.star-icon {
  font-size: 60px;
  line-height: 31px;
  width: 26px;
  height: 31px;
  display: inline-block;
}
.active {
  color: orange;
}
</style>
