<template>
  <div class="flex" style="align-items: center;">
    <!-- <Tools /> -->
    <span class="prl2"> </span>
    <span class="prl2">    - {{ lastUpdateFormated }}</span>
    <BaseButton class="prl2" v-if="!updateInProgress" @click="startUpdate">
       
    </BaseButton>
    <span
      class="prl2"
      style="color:red;left:10px;position:fixed"
      v-if="updateInProgress || err"
      >{{ content }}</span
    >
  </div>
</template>

<script>
import io from "socket.io-client";
import moment from "moment";
import Tools from "vue-material-design-icons/Tools.vue";
let server = null;
export default {
  components: { Tools },

  data() {
    return {
      content: "     !!!",
      err: false,
      updateInProgress: false,
      lastUpdate: null,
    };
  },
  computed: {
    lastUpdateFormated() {
      return moment
        .utc(this.lastUpdate)
        .local(false)
        .startOf("hour")
        .fromNow(true);
    },
  },
  methods: {
    startUpdate() {
      let code = prompt(" ", "");

      server.emit("start-update", code);
    },
  },
  created() {
    server = io.connect(`${MAIN_SERVER}:${MAIN_PORT}/`);
    moment.locale("he");
    // server.on("connect", function() {});
    server.on("connection", (data) => {
      console.log(data);
    });
    server.on("update-status", (data) => {
      console.log(data);
      this.updateInProgress = data;
    });
    server.on("update-begin", () => {
      this.updateInProgress = true;
    });
    server.on("update-end", (data) => {
      //delete local-storage and reload the page
      localStorage.removeItem("books");

      this.updateInProgress = false;

      this.lastUpdate = data;
      this.$router.go(0);
    });
    server.on("general-data", (data) => {
      this.updateInProgress = data.updateInProgress;
      this.lastUpdate = data.lastMefOtzarUpdate;
    });
    server.on("update-err", (data) => {
      this.updateInProgress = false;
      this.err = true;
      this.content = "    ";
    });
  },
};
</script>

<style lang="scss" scoped>
.prl2 {
  padding-left: 2px;
  padding-right: 2px;
}
</style>
