import ShasService from "@/services/shasService";
import ErrorChecker from "@/utils/rect-error-util";
import secutil from "@/utils/section-util";

let InnerSections = [];
export default function(state) {
  return {
    namespaced: true,
    state: function() {
      return state;
    },
    mutations: {
      SET_SECTIONS(state, payload) {
        InnerSections = payload;

        state.InnerSectionsGUID++;
      },
      FIT_TO_WIDTH(state, data) {
        state.fitToWidth = data;
      },
      SET_SELECTED_SECTION(state, data) {
        state.selectedSection = data;
      },
      SET_TYPE_SELECTED(state, data) {
        state.catg_selected = data;
      },
      SET_SELECTED_CHAPTER(state, data) {
        state.secId = data;
      },
      TOGGLE_PIN(state) {
        state.pin = !state.pin;
        if (state.pin)
          localStorage.setItem("pin_tab_" + state.tabId, JSON.stringify(state));
        else localStorage.removeItem("pin_tab_" + state.tabId);
      }
    },
    actions: {
      setCardMode({ commit }) {},
      setSelectedChapter({ commit }, data) {
        commit("SET_SELECTED_CHAPTER", data);
      },
      fitToWidth({ commit }, data) {
        commit("FIT_TO_WIDTH", data);
      },
      setSections({ commit }, sections) {
        return new Promise((resolve, reject) => {
          commit("SET_SECTIONS", sections);
          resolve();
        });
      },
      setSelectedSection({ commit }, secId) {
        commit("SET_SELECTED_SECTION", secId);
      },
      togglePin({ commit }) {
        commit("TOGGLE_PIN");
      },
      setTypeSelected({ commit }, data) {
        commit("SET_TYPE_SELECTED", data);
      }
    },
    getters: {
      getFilteredSections(state, getters, rootState, rootGetters) {
        let lst = [];
        let filteredList = getters.getTextFilteredSections.lst || [];
        let selectedSectionGuid = state.InnerSectionsGUID;

        if (!selectedSectionGuid) return lst;

        filteredList.forEach(e => {
          // console.log(e.id);
          if (state.secId != null) {
            if (e.id != state.secId) return;
          }
          //if (e.id == state.selectedSection) lst.push(e);
          if (state.filter) {
            //   //txt filter
            //   if (
            //     !e.bookName.includes(state.filterByText) &&
            //     !e.bookAuthor.includes(state.filterByText)
            //   )
            //     return;
            //console.log(e);
            //   //catg combo filter (sihor)
            if (state.type_selected == 1 && e.type != "s") return;

            //   //catg combo filter (perush)
            if (state.type_selected == 3 && e.type != "p") return;

            //   //catg combo filter (drush)
            if (state.type_selected == 2 && e.type != "d") return;

            //   //catg combo filter (drush)
            if (state.type_selected == 4 && e.type != "z") return;

            //   //catg combo filter (drush)
            if (state.type_selected == 5 && e.type != "h") return;
          }

          if (state.catg_selected != 0) {
            let correctBook = secutil.getBookId(e);
            // if (correctBook !== e.MefoToMarks[0].Marks.book) {
            //   correctBook = "9" + correctBook + e.MefoToMarks[0].Marks.book;
            // }
            let book = rootGetters["books/getBookById"](correctBook);
            console.log(book);
            if (book == undefined) return;

            if (
              book.sederdorot == undefined ||
              book.sederdorot.id != state.catg_selected
            )
              return;
          }

          lst.push(e);
        });

        return lst;
      },
      getTextFilteredSections(state, getters, rootState, rootGetters) {
        let lst = [];
        let err = false;
        let err_id = "";
        let allowLink = true;
        let error_msg = false;
        let counters = {
          all: 0,
          mikra: 0,
          rishonim: 0,
          hacronim_str: 0,
          hacronim_trs: 0,
          hacrnim_zmanneno: 0,
          others: 0
        };
        let selectedSectionGuid = state.InnerSectionsGUID;

        let _rectMefo = getters.getMefs;

        if (_rectMefo.length == 0 || !_rectMefo) return [];

        _rectMefo.forEach((elem, index) => {
          elem.RectMefo.forEach((e, i) => {
            err = false;
            allowLink = true;
            err_id = "";
            //txt filter
            let correctBook = e.book;

            //actually here i ignore from errors from impotrted data !!!!!!!!!
            err = ErrorChecker.getError(e);

            if (err != false) {
              if (rootGetters.isItWorkEdition) {
                error_msg = err;
                err = true;
              } else return;
            }
            if (!err) {
              correctBook = secutil.getBookId(e); //"9" + e.MefoToMarks[0].Marks.book + correctBook;
            }
            let book = rootGetters["books/getBookById"](correctBook);
            if (book == undefined && !err) {
              // debugger;
              err = true;
              error_msg = "   ";
              //allowLink = false;
            }
            if (!err && state.filter) {
              if (
                !book.name.includes(state.filterByText) &&
                !book.authors[0].name.includes(state.filterByText)
              )
                return;

              //catg combo filter (sihor)

              if (state.type_selected == 1 && e.type != "s") return;

              //catg combo filter (perush)
              if (state.type_selected == 3 && e.type != "p") return;

              //catg combo filter (drush)
              if (state.type_selected == 2 && e.type != "d") return;

              //catg combo filter (zion)
              if (state.type_selected == 4 && e.type != "z") return;

              //catg combo filter (hgahha)
              if (state.type_selected == 5 && e.type != "h") return;
            }
            //counter checks
            //inc the counter of all

            if (book != undefined && book.sederdorot) {
              counters.all++;
              switch (Number(book.sederdorot.id)) {
                case 9:
                  //inc others counter
                  counters.others++;
                  break;
                //inc mikra
                case 1:
                  counters.mikra++;
                  break;
                case 2:
                  counters.rishonim++;
                  break;
                case 3:
                  counters.hacronim_str++;
                  break;
                case 4:
                  counters.hacronim_trs++;
                  break;
                case 5:
                  counters.hacrnim_zmanneno++;
                  break;
              }
            }
            if (err) {
              e.err = true;
              e.error_msg = error_msg;
              e.allowlink = allowLink;
            }
            if (rootGetters.isItWorkEdition && state.showerror) {
              if (e.err != true) return;
            }
            lst.push(e);
          });
        });

        return { counters, lst };
      },

      getFullModeOfMeforesh: state => secId => {
        return state.sections.filter(e => e.secId == secId).viewMode;
      },
      getTabTitle(state) {
        return ShasService.getFullName(state.book, state.page);
      },
      getAllBooks() {
        return () => {
          return InnerSections;
        };
      },
      getMefs(state) {
        let selectedSectionGuid = state.InnerSectionsGUID;
        let data = [];
        if (state.showerror) {
          return InnerSections;
        }
        // if (state.fltToBookIds.length == 0) {

        //   InnerSections.forEach(e=>{
        //     let flt=e.RectMefo.filter(i=>{
        //       let bid=secutil.getBookId(i)
        //       if(state.fltToBookIds.some(p=>p==bid)){

        //       }
        //     })
        //   });
        // }
        let item = InnerSections.filter(e => e.id == state.selectedSection)[0];
        if (item != undefined) data.push(item);
        else return [];

        if (data) {
          data[0].RectMefo = data[0].RectMefo.sort(
            (f, s) => f.priority - s.priority
          );
        }

        return data; //InnerSections.filter(e => e.id == state.selectedSection)[0];
      }
    }
  };
}

// export let sections = [];
