<template>
  <div>
    <div class="login-row">
      <pre>   </pre>
      <input ref="user" type="text" v-model="username" />
    </div>
    <div class="login-row">
      <pre>  </pre>
      <input v-model="password" type="password" @keyup.enter="Login" />
    </div>
    <div class="login-row" style="color: red" v-if="error">
           
    </div>
    <div class="login-row" style="margin-top: 10px; justify-content: center">
      <button @click="Login"></button>
    </div>
  </div>
</template>

<script>
import axios from "@/services/_axiosInstance";
import { setJwt } from "@/services/_axiosInstance";
import { mapActions } from "vuex";
import { loginCheck } from "@/bootstrap/auth.js";
export default {
  data() {
    return {
      username: "",
      password: "",
      error: false
    };
  },
  methods: {
    ...mapActions("user", ["setJwt", "setName"]),
    Login() {
      axios
        .post(`${MAIN_SERVER}:${MAIN_PORT}/api/user/login`, {
          username: this.username,
          password: this.password
        })
        .then(({ data }) => {
          this.setJwt(data.token);
          this.setName(data.name);
          localStorage.setItem("name", data.name);
          localStorage.setItem("token", data.token);
          setJwt(data.token);
          this.$store.state.tabs.reloadAll = !this.$store.state.tabs.reloadAll;
          this.$emit("close");
          loginCheck(data.token);
        })
        .catch((err) => {
          this.error = true;
        });
    }
  },
  mounted() {
    console.log(this.$refs);
    this.$refs.user.focus();
  }
};
</script>

<style lang="scss" scoped>
.login-row {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
}
</style>
