import store from "../store/store";
export default function() {
  let tabs = [];
  if (store.state.tabs.tabs.length == 0) return;
  store.state.tabs.tabs.forEach(tab => {
    let book = store.state["tab_" + tab].book;
    let page = store.state["tab_" + tab].page;
    let selectedSection = store.state["tab_" + tab].selectedSection;
    let filterByText = store.state["tab_" + tab].filterByText;
    tabs.push({ book, page, selectedSection, filterByText });
  });
  localStorage.setItem("last-tabs", JSON.stringify(tabs));
}
