<template>
  <div class="flex book-item">
    <div class="book-text">
      <BookOpenOutline />
      <span class="book-text-item">{{ book }}</span>
    </div>
    <div class="book-show-only">
      <button @click="addBook(true)" style="margin-left:5px"> </button>
      <button @click="addBook(false)" v-if="bookNotFiltered"></button>
      <button @click="delBook()" style="color:red" v-else></button>
    </div>
  </div>
</template>

<script>
import BookOpenOutline from "vue-material-design-icons/BookOpenOutline.vue";
import tabState from "@/mixing/importTabState";

export default {
  mixins: [tabState],
  props: ["book", "bookid"],
  components: {
    BookOpenOutline
  },
  computed: {
    bookNotFiltered() {
      if (this.tab.fltToBookIds.some(item => item == this.bookid)) return false;
      return true;
    }
  },
  methods: {
    addBook(only) {
      this.$store.dispatch(`tab_${this.tab.tabId}/addToFltBook`, {
        book: this.bookid,
        only
      });
    },
    delBook() {
      this.$store.dispatch(`tab_${this.tab.tabId}/delFltBook`, this.bookid);
    }
  }
};
</script>

<style lang="scss" scoped>
.book-item {
  height: 40px;
}
.book-text {
  display: flex;
}
.book-text-item {
  padding-left: 5px;
  padding-right: 5px;
}
.book-show-only {
  flex-grow: 1;
  text-align: left;
  padding-left: 10px;
}
</style>
