import getcode from "@/utils/enc";
import axios from "./_axiosInstance";
import config from "@/config/config";
export default {
  getImage(book, page, rotate) {
    return new Promise((resolve, reject) => {
      let picture = document.createElement("img");
      picture.onload = function() {
        resolve(picture);
      };
      picture.crossOrigin = "Anonymous";
      picture.src =
        `${MAIN_SERVER}:${MAIN_PORT}/api/books/${book}/${page}/image?all=true&c=` +
        getcode(book, page);
    });
  },
  async getImageData(book, page, isPageId = true, rotate) {
    let pic = await axios.get(
      `${MAIN_SERVER}:${MAIN_PORT}/api/books/${book}/${page}/image?c=${getcode(
        book,
        page
      )}`,
      {
        responseType: "arraybuffer",
      }
    );
    if (config.offline) {
      pic.data = decryptHeader(new Uint8Array(pic.data)).buffer;
    }
    return pic;
  },
  getImageUrl(book, page, rotate = 0) {
    let picture = document.createElement("img");
    picture.crossOrigin = "Anonymous";
    return (picture.src =
      `${MAIN_SERVER}:${MAIN_PORT}/api/books/${book}/${page}/image?c=` +
      getcode(book, page));
  },
};

const decryptHeader = (uint8buff) => {
  /**@type {Uint8Array} */
  const buff = uint8buff;
  const key = `ZsfbTQX=4xrAPFMA8^RHW6qFgM-gSrX-xXvF6@6GFHV2$agT#FzjmgFd5EHdQt%Vb$bkcCZUZHznFPj$L^LyXt#tuHtVaY+k#!?ZJjChQr9hSa4a$NJApVecm9ST`;
  const max = key.length > buff.length ? buff.length : key.length;
  for (let i = 0; i < max; i++) {
    buff[i] = buff[i] ^ key.charCodeAt(i);
  }
  return buff;
};
